import React, { useEffect, useMemo, useState } from 'react';
import { Plus, Edit, Trash2, Search, Filter, Users, Shield, Mail, Phone, X, Loader, Upload, UserPlus, ChevronLeft, ChevronRight } from 'lucide-react';
import type { BackendUser, CreateUserRequest, UpdateUserRequest, RoleAssignmentRequest, Mahasiswa, Dosen, BulkCreateUsersRequest, CreateMahasiswaRequest, CreateDosenRequest } from '../../types';
import { 
  useUsersList,
  useStudentsList,
  useLecturersList,
  useCreateUser,
  useUpdateUser,
  useDeleteUser,
  useBulkCreateUsers,
  useCreateStudent,
  useCreateLecturer,
} from '../../hooks/useUsers';

interface UserWithDetails extends BackendUser {
  studentInfo?: Mahasiswa;
  lecturerInfo?: Dosen;
  lastLogin?: string;
}

const UserManagement: React.FC = () => {
  const [students, setStudents] = useState<Mahasiswa[]>([]);
  const [lecturers, setLecturers] = useState<Dosen[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedRole, setSelectedRole] = useState<string>('all');
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [isBulkAddModalOpen, setIsBulkAddModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<UserWithDetails | null>(null);
  const [formError, setFormError] = useState<string | null>(null);
  const [actionLoading, setActionLoading] = useState(false);
  const [bulkUsers, setBulkUsers] = useState<CreateUserRequest[]>([]);
  
  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const [totalUsers, setTotalUsers] = useState(0);
  const [itemsPerPage] = useState(50); // Display 50 users per page

  const [formData, setFormData] = useState<{
    email: string;
    name: string;
    password: string;
    roles: RoleAssignmentRequest[];
  }>({
    email: '',
    name: '',
    password: '',
    roles: [],
  });

  const getSqlValue = (val: any): string => {
    if (val && typeof val === 'object' && 'String' in val) {
      return val.Valid ? val.String : '';
    }
    return val || '';
  };

  // Queries: students, lecturers (cached) and users (paginated)
  const { data: studentsData, isLoading: isStudentsLoading, error: studentsError } = useStudentsList();
  const { data: lecturersData, isLoading: isLecturersLoading, error: lecturersError } = useLecturersList();
  const usersParams = useMemo(() => ({ q: searchTerm || undefined, limit: itemsPerPage, offset: (currentPage - 1) * itemsPerPage }), [itemsPerPage, currentPage, searchTerm]);
  // Server-side search by email/name; role filter stays client-side
  const usersQuery = useUsersList(usersParams, { enabled: true });

  // Mutations
  const createUserMutation = useCreateUser();
  const updateUserMutation = useUpdateUser();
  const deleteUserMutation = useDeleteUser();
  const bulkCreateUsersMutation = useBulkCreateUsers();
  const createStudentMutation = useCreateStudent();
  const createLecturerMutation = useCreateLecturer();

  // Keep local copies for select inputs if needed
  useEffect(() => {
    if (studentsData) setStudents(studentsData);
  }, [studentsData]);
  useEffect(() => {
    if (lecturersData) setLecturers(lecturersData);
  }, [lecturersData]);

  useEffect(() => {
    if (usersQuery.data) {
      setTotalUsers(usersQuery.data.total);
    }
  }, [usersQuery.data]);

  const loading = usersQuery.isLoading || isStudentsLoading || isLecturersLoading;
  const loadError = usersQuery.error || studentsError || lecturersError;

  const getRoleColor = (role: string) => {
    switch (role) {
      case 'admin':
        return 'bg-red-100 text-red-800';
      case 'lecturer':
        return 'bg-blue-100 text-blue-800';
      case 'student':
        return 'bg-green-100 text-green-800';
      case 'staff':
        return 'bg-purple-100 text-purple-800';
      case 'student_applicant':
        return 'bg-yellow-100 text-yellow-800';
      case 'alumni':
        return 'bg-gray-100 text-gray-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getRoleLabel = (role: string) => {
    const labels: Record<string, string> = {
      admin: 'Admin',
      lecturer: 'Dosen',
      student: 'Mahasiswa',
      staff: 'Staff',
      student_applicant: 'Pendaftar',
      alumni: 'Alumni',
      guest: 'Guest',
    };
    return labels[role] || role;
  };

  // Reset to page 1 when search/filter changes
  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm, selectedRole]);

  const users: UserWithDetails[] = useMemo(() => {
    const base = usersQuery.data?.data ?? [];
    // Enrich with entity info from cached lists
    return base.map((user) => {
      const userRoles = user.roles || [];
      const studentRole = userRoles.find((r) => r.role === 'student');
      const lecturerRole = userRoles.find((r) => r.role === 'lecturer');
      const enriched: UserWithDetails = { ...user };
      if (studentRole?.entity_id && studentsData) {
        enriched.studentInfo = studentsData.find((s) => s.nim === studentRole.entity_id);
      }
      if (lecturerRole?.entity_id && lecturersData) {
        enriched.lecturerInfo = lecturersData.find((l) => l.id === lecturerRole.entity_id);
      }
      return enriched;
    });
  }, [usersQuery.data, studentsData, lecturersData]);

  const filteredUsers = useMemo(() => {
    return users.filter((user) => {
      const userName = getSqlValue(user.name) || user.email;
      const matchesSearch =
        userName.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.email.toLowerCase().includes(searchTerm.toLowerCase());

      const userRoles = user.roles?.map((r) => r.role) || [];
      const matchesRole = selectedRole === 'all' || userRoles.includes(selectedRole);

      return matchesSearch && matchesRole;
    });
  }, [users, searchTerm, selectedRole]);

  const userStats = useMemo(() => {
    const allRoles = users.flatMap((u) => u.roles?.map((r) => r.role) || []);
    return {
      total: totalUsers, // Use totalUsers from pagination
      students: allRoles.filter((r) => r === 'student').length,
      lecturers: allRoles.filter((r) => r === 'lecturer').length,
      admins: allRoles.filter((r) => r === 'admin').length,
      staff: allRoles.filter((r) => r === 'staff').length,
    };
  }, [users, totalUsers]);

  const openAddModal = () => {
    setFormError(null);
    setFormData({
      email: '',
      name: '',
      password: '',
      roles: [],
    });
    setIsAddModalOpen(true);
  };

  const closeAddModal = () => {
    setIsAddModalOpen(false);
  };

  const openBulkAddModal = () => {
    setFormError(null);
    setBulkUsers([]);
    setIsBulkAddModalOpen(true);
  };

  const closeBulkAddModal = () => {
    setIsBulkAddModalOpen(false);
    setBulkUsers([]);
  };

  const openEditModal = (user: UserWithDetails) => {
    setFormError(null);
    setEditingUser(user);
    setFormData({
      email: user.email,
      name: getSqlValue(user.name),
      password: '',
      roles: user.roles || [],
    });
    setIsEditModalOpen(true);
  };

  const closeEditModal = () => {
    setIsEditModalOpen(false);
    setEditingUser(null);
  };

  const handleAddUser = async (e: React.FormEvent) => {
    e.preventDefault();
    setFormError(null);

    if (!formData.email.trim() || !formData.email.includes('@')) {
        setFormError('Email tidak valid');
        return;
    }

    // Make password optional
    if (isAddModalOpen && !formData.password.trim()) {
        setFormError('Password wajib diisi jika tidak menggunakan Google OAuth');
        return;
    }

    setActionLoading(true);
    try {
    const payload: CreateUserRequest = {
            email: formData.email.trim(),
            name: formData.name.trim() || undefined,
            password: formData.password.trim() || undefined, // Allow undefined
            auth_provider: 'local',
            roles: formData.roles,
        };

    await createUserMutation.mutateAsync(payload);
    setIsAddModalOpen(false);
    } catch (error: any) {
        setFormError(error.message || 'Gagal menambahkan pengguna');
    } finally {
        setActionLoading(false);
    }
};

  const handleEditUser = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingUser) return;

    setFormError(null);

    if (!formData.email.trim() || !formData.email.includes('@')) {
        setFormError('Email tidak valid');
        return;
    }

    setActionLoading(true);
    try {
        const payload: UpdateUserRequest = {
            email: formData.email.trim(),
            name: formData.name.trim() || undefined,
            roles: formData.roles,
        };

        // Allow password to be optional
        if (formData.password.trim()) {
            payload.password = formData.password.trim();
        }

        await updateUserMutation.mutateAsync({ id: editingUser.id, data: payload });
        setIsEditModalOpen(false);
    } catch (error: any) {
        setFormError(error.message || 'Gagal mengupdate pengguna');
    } finally {
        setActionLoading(false);
    }
  };

  const handleDeleteUser = async (user: UserWithDetails) => {
    if (!confirm(`Apakah Anda yakin ingin menghapus pengguna "${getSqlValue(user.name) || user.email}"?`)) {
      return;
    }

    try {
      await deleteUserMutation.mutateAsync({ id: user.id });
    } catch (error: any) {
      alert(`Gagal menghapus pengguna: ${error.message}`);
    }
  };

  const addRole = (role: string) => {
    if (formData.roles.some((r) => r.role === role)) {
      setFormError(`Role ${getRoleLabel(role)} sudah ditambahkan`);
      return;
    }

    setFormData({
      ...formData,
      roles: [...formData.roles, { role }],
    });
  };

  const removeRole = (index: number) => {
    setFormData({
      ...formData,
      roles: formData.roles.filter((_, i) => i !== index),
    });
  };

  const updateRoleEntity = (index: number, entityType: string, entityId: string) => {
    const newRoles = [...formData.roles];
    if (entityId) {
      newRoles[index] = {
        ...newRoles[index],
        entity_type: entityType,
        entity_id: entityId,
      };
    } else {
      // Remove entity fields if no entity selected
      const { entity_type, entity_id, ...rest } = newRoles[index];
      newRoles[index] = rest;
    }
    setFormData({ ...formData, roles: newRoles });
  };



  const handleBulkAddUsers = async (e: React.FormEvent) => {
    e.preventDefault();
    setFormError(null);

    // Validate all users
    for (let i = 0; i < bulkUsers.length; i++) {
      const user = bulkUsers[i];
      if (!user.email?.trim() || !user.email.includes('@')) {
        setFormError(`User ${i + 1}: Email tidak valid`);
        return;
      }
      // Password is now optional
    }

    setActionLoading(true);
    try {
      // Create entities first if needed
      const pendingStudents = (window as any).pendingStudents || [];
      const pendingLecturers = (window as any).pendingLecturers || [];
      // Create students
      for (const student of pendingStudents) {
        try {
          await createStudentMutation.mutateAsync(student);
        } catch (error) {
          console.warn(`Failed to create student ${student.nim}:`, error);
        }
      }
      // Create lecturers
      for (const lecturer of pendingLecturers) {
        try {
          await createLecturerMutation.mutateAsync(lecturer);
        } catch (error) {
          console.warn(`Failed to create lecturer ${lecturer.id}:`, error);
        }
      }
      // Bulk create users
      const payload: BulkCreateUsersRequest = {
        users: bulkUsers.map(u => ({
          email: u.email!.trim(),
          name: u.name?.trim() || undefined,
          password: u.password?.trim() || undefined,
          auth_provider: 'local',
          roles: u.roles || [],
        })),
      };
      await bulkCreateUsersMutation.mutateAsync(payload);
      setIsBulkAddModalOpen(false);
      // Clear pending entities
      (window as any).pendingStudents = [];
      (window as any).pendingLecturers = [];
    } catch (error: any) {
      setFormError(error.message || 'Gagal menambahkan pengguna secara bulk');
    } finally {
      setActionLoading(false);
    }
  };

  const handleImportCSV = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const text = event.target?.result as string;
        const lines = text.split('\n').filter(line => line.trim());
        
        if (lines.length < 2) {
          setFormError('File CSV kosong atau tidak valid');
          return;
        }

        // Parse header
        const headers = lines[0].split(',').map(h => h.trim().toLowerCase());
        
        // Get column indices
        const getIdx = (name: string) => headers.indexOf(name);
        
        const emailIdx = getIdx('email');
        const nameIdx = getIdx('name') >= 0 ? getIdx('name') : getIdx('nama');
        const passwordIdx = getIdx('password');
        const roleIdx = getIdx('role');
        const entityTypeIdx = getIdx('entity_type');
        const entityIdIdx = getIdx('entity_id');

        // Mahasiswa fields
        const nimIdx = getIdx('nim');
        const mahasiswaEmailIdx = getIdx('mahasiswa_email');
        const mahasiswaNoHpIdx = getIdx('mahasiswa_no_hp');
        const kodeProdiMhsIdx = getIdx('mahasiswa_kode_prodi');
        const jenisKelaminIdx = getIdx('jenis_kelamin');
        const tempatLahirIdx = getIdx('tempat_lahir');
        const tanggalLahirIdx = getIdx('tanggal_lahir');
        const agamaIdx = getIdx('agama');
        const kewarganegaraanIdx = getIdx('kewarganegaraan');
        const nikIdx = getIdx('nik');
        const nisnIdx = getIdx('nisn');
        const npwpIdx = getIdx('npwp');
        const mulaiSemesterIdx = getIdx('mulai_semester');
        const tanggalMasukIdx = getIdx('tanggal_masuk');
        const jalurPendaftaranIdx = getIdx('jalur_pendaftaran');
        const jenisPendaftaranIdx = getIdx('jenis_pendaftaran');

        // Dosen fields
        const idDosenIdx = getIdx('id');
        const namaDosenIdx = getIdx('nama_dosen');
        const nuptkDosenIdx = getIdx('nuptk');
        const dosenEmailIdx = getIdx('dosen_email');
        const dosenNoHpIdx = getIdx('dosen_no_hp');
        const kodeProdiDsnIdx = getIdx('dosen_kode_prodi');

        if (emailIdx === -1) {
          setFormError('CSV harus memiliki kolom "email"');
          return;
        }

        // Parse data rows
        const users: CreateUserRequest[] = [];
        const pendingStudents: CreateMahasiswaRequest[] = [];
        const pendingLecturers: CreateDosenRequest[] = [];

        for (let i = 1; i < lines.length; i++) {
          const line = lines[i].trim();
          if (!line) continue;

          // CSV parsing (handles quoted fields)
          const values: string[] = [];
          let current = '';
          let inQuotes = false;

          for (let j = 0; j < line.length; j++) {
            const char = line[j];
            if (char === '"') {
              inQuotes = !inQuotes;
            } else if (char === ',' && !inQuotes) {
              values.push(current.trim());
              current = '';
            } else {
              current += char;
            }
          }
          values.push(current.trim());

          const getValue = (idx: number) => {
            if (idx === -1) return '';
            return values[idx]?.replace(/"/g, '').trim() || '';
          };

          const email = getValue(emailIdx);
          const name = getValue(nameIdx);
          const password = passwordIdx !== -1 ? getValue(passwordIdx) : '';
          const role = getValue(roleIdx);
          const entityType = getValue(entityTypeIdx);
          const entityId = getValue(entityIdIdx);

          if (!email) continue;

          // Build roles array
          let roles: RoleAssignmentRequest[] = [];
          if (role) {
            if (entityType && entityId) {
              roles.push({ role, entity_type: entityType, entity_id: entityId });
            } else {
              roles.push({ role });
            }
          }

          // Check if we need to create mahasiswa entity
          if (role === 'student' && entityType === 'mahasiswa') {
            const nim = getValue(nimIdx) || entityId;
            if (nim) {
              const mahasiswa: CreateMahasiswaRequest = {
                nim,
                nama: getValue(nameIdx) || email,
                email: getValue(mahasiswaEmailIdx) || email,
                no_hp: getValue(mahasiswaNoHpIdx) || undefined,
                kode_prodi: getValue(kodeProdiMhsIdx) || undefined,
                jenis_kelamin: getValue(jenisKelaminIdx) || undefined,
                tempat_lahir: getValue(tempatLahirIdx) || undefined,
                tanggal_lahir: getValue(tanggalLahirIdx) || undefined,
                agama: getValue(agamaIdx) || undefined,
                kewarganegaraan: getValue(kewarganegaraanIdx) || undefined,
                nik: getValue(nikIdx) || undefined,
                nisn: getValue(nisnIdx) || undefined,
                npwp: getValue(npwpIdx) || undefined,
                mulai_semester: getValue(mulaiSemesterIdx) || undefined,
                tanggal_masuk: getValue(tanggalMasukIdx) || undefined,
                jalur_pendaftaran: getValue(jalurPendaftaranIdx) || undefined,
                jenis_pendaftaran: getValue(jenisPendaftaranIdx) || undefined,
              };
              pendingStudents.push(mahasiswa);
            }
          }

          // Check if we need to create dosen entity
          if (role === 'lecturer' && entityType === 'dosen') {
            const id = getValue(idDosenIdx) || entityId;
            if (id) {
              const dosen: CreateDosenRequest = {
                id,
                nama_dosen: getValue(namaDosenIdx) || name || email,
                nuptk: getValue(nuptkDosenIdx) || undefined,
                email: getValue(dosenEmailIdx) || email,
                no_hp: getValue(dosenNoHpIdx) || undefined,
                kode_prodi: getValue(kodeProdiDsnIdx) || undefined,
              };
              pendingLecturers.push(dosen);
            }
          }

          users.push({
            email,
            name: name || undefined,
            password: password || undefined,
            auth_provider: 'local',
            roles,
          });
        }

        if (users.length === 0) {
          setFormError('Tidak ada data valid yang ditemukan dalam CSV');
          return;
        }

        // Store pending entities for later creation
        (window as any).pendingStudents = pendingStudents;
        (window as any).pendingLecturers = pendingLecturers;
        setBulkUsers(users);
        setFormError(null);
      } catch (error: any) {
        let debugMsg = '';
        if (error && (error.response?.data || error.data)) {
          debugMsg = '\n[DEBUG] ' + (error.response?.data?.message || error.data?.message || JSON.stringify(error.response?.data || error.data));
        } else if (error && error.message) {
          debugMsg = '\n[DEBUG] ' + error.message;
        } else {
          debugMsg = '\n[DEBUG] ' + JSON.stringify(error);
        }
        setFormError('Gagal membaca file CSV' + debugMsg);
      }
    };
    reader.readAsText(file);
    // Reset input
    e.target.value = '';
  };

  const downloadCSVTemplate = () => {
    const template = `email,name,password,role,entity_type,entity_id,nim,mahasiswa_email,mahasiswa_no_hp,mahasiswa_kode_prodi,jenis_kelamin,tempat_lahir,tanggal_lahir,agama,kewarganegaraan,nik,nisn,npwp,mulai_semester,tanggal_masuk,jalur_pendaftaran,jenis_pendaftaran,id_dosen,nama_dosen,dosen_email,dosen_no_hp,dosen_kode_prodi,nuptk
admin@example.com,Admin User,password123,admin,,,,,,,,,,,,,,,,,,,,,,,
student1@example.com,Student One,password456,student,mahasiswa,2024001,2024001,student1@example.com,081234567890,TI,L,Jakarta,2000-01-15,Islam,Indonesia,1234567890123456,1234567890,123456789012345,20241,2024-08-01,SNMPTN,Reguler,,,,,
lecturer1@example.com,Lecturer One,password789,lecturer,dosen,0123456789,,,,,,,,,,,,,,,,,0123456789,Dr. Lecturer One,lecturer1@example.com,081234567891,TI,1234567890123456`;
    
    const blob = new Blob([template], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'users_template.csv';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader className="h-8 w-8 animate-spin text-blue-600" />
      </div>
    );
  }

  if (loadError) {
    return (
      <div className="space-y-4">
        <div className="text-red-700 bg-red-50 border border-red-200 rounded p-3 text-sm">
          {(loadError as Error).message}
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <Users className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Manajemen Pengguna</h1>
            <p className="text-gray-600">Kelola akun pengguna sistem akademik</p>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <button onClick={openBulkAddModal} className="btn-secondary flex items-center space-x-2 cursor-pointer">
            <UserPlus className="h-4 w-4" />
            <span>Bulk Add</span>
          </button>
          <button onClick={openAddModal} className="btn-primary flex items-center space-x-2 cursor-pointer">
            <Plus className="h-4 w-4" />
            <span>Tambah Pengguna</span>
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Users className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Total Pengguna</p>
              <p className="text-2xl font-semibold text-gray-900">{userStats.total}</p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <Shield className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Admin</p>
              <p className="text-2xl font-semibold text-gray-900">{userStats.admins}</p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <Users className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Mahasiswa</p>
              <p className="text-2xl font-semibold text-gray-900">{userStats.students}</p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-purple-100 rounded-lg">
              <Users className="h-6 w-6 text-purple-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Dosen</p>
              <p className="text-2xl font-semibold text-gray-900">{userStats.lecturers}</p>
            </div>
          </div>
        </div>
      </div>

      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0 lg:space-x-4">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <input
            type="text"
            placeholder="Cari pengguna..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="input-field pl-10 w-full lg:w-80"
          />
        </div>
        <div className="flex items-center space-x-4">
          <select
            value={selectedRole}
            onChange={(e) => setSelectedRole(e.target.value)}
            className="input-field"
          >
            <option value="all">Semua Role</option>
            <option value="student">Mahasiswa</option>
            <option value="lecturer">Dosen</option>
            <option value="admin">Admin</option>
            <option value="staff">Staff</option>
            <option value="student_applicant">Pendaftar</option>
            <option value="alumni">Alumni</option>
          </select>
          <button className="btn-secondary flex items-center space-x-2">
            <Filter className="h-4 w-4" />
            <span>Filter</span>
          </button>
        </div>
      </div>

      <div className="card overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Pengguna
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Kontak
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Roles
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Entitas
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Terdaftar
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Aksi
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredUsers.map((user) => {
                const userName = getSqlValue(user.name) || user.email;
                const userRoles = user.roles || [];
                
                return (
                  <tr key={user.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="h-10 w-10 flex-shrink-0">
                          <div className="h-10 w-10 rounded-full bg-gray-300 flex items-center justify-center">
                            <span className="text-sm font-medium text-gray-700">
                              {userName.charAt(0).toUpperCase()}
                            </span>
                          </div>
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">{userName}</div>
                          <div className="text-sm text-gray-500">ID: {user.id}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">
                        <div className="flex items-center space-x-1">
                          <Mail className="h-4 w-4 text-gray-400" />
                          <span>{user.email}</span>
                        </div>
                        {user.studentInfo && getSqlValue(user.studentInfo.no_hp) && (
                          <div className="flex items-center space-x-1 mt-1">
                            <Phone className="h-4 w-4 text-gray-400" />
                            <span>{getSqlValue(user.studentInfo.no_hp)}</span>
                          </div>
                        )}
                        {user.lecturerInfo && getSqlValue(user.lecturerInfo.no_hp) && (
                          <div className="flex items-center space-x-1 mt-1">
                            <Phone className="h-4 w-4 text-gray-400" />
                            <span>{getSqlValue(user.lecturerInfo.no_hp)}</span>
                          </div>
                        )}
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex flex-wrap gap-1">
                        {userRoles.length > 0 ? (
                          userRoles.map((roleAssignment, idx) => (
                            <span
                              key={idx}
                              className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getRoleColor(
                                roleAssignment.role
                              )}`}
                            >
                              {getRoleLabel(roleAssignment.role)}
                            </span>
                          ))
                        ) : (
                          <span className="text-sm text-gray-400">Tidak ada role</span>
                        )}
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">
                      {user.studentInfo && (
                        <div>
                          <div className="font-medium">Mahasiswa: {user.studentInfo.nim}</div>
                          <div className="text-gray-500">{getSqlValue(user.studentInfo.kode_prodi)}</div>
                        </div>
                      )}
                      {user.lecturerInfo && (
                        <div>
                          <div className="font-medium">Dosen: {user.lecturerInfo.id}</div>
                          <div className="text-gray-500">{getSqlValue(user.lecturerInfo.kode_prodi)}</div>
                        </div>
                      )}
                      {!user.studentInfo && !user.lecturerInfo && <span className="text-gray-400">-</span>}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {new Date(user.created_at).toLocaleDateString('id-ID')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-2">
                        <button
                          onClick={() => openEditModal(user)}
                          className="text-blue-600 hover:text-blue-900"
                        >
                          <Edit className="h-4 w-4" />
                        </button>
                        <button
                          onClick={() => handleDeleteUser(user)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 className="h-4 w-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>

      {filteredUsers.length === 0 && (
        <div className="text-center py-12">
          <Users className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">Tidak ada pengguna</h3>
          <p className="mt-1 text-sm text-gray-500">Tidak ditemukan pengguna yang sesuai dengan filter.</p>
        </div>
      )}

      {/* Pagination Controls */}
      {totalUsers > itemsPerPage && (
        <div className="card p-4 mt-6">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-700">
              Menampilkan <span className="font-medium">{(currentPage - 1) * itemsPerPage + 1}</span> -{' '}
              <span className="font-medium">{Math.min(currentPage * itemsPerPage, totalUsers)}</span> dari{' '}
              <span className="font-medium">{totalUsers}</span> pengguna
            </div>
            <div className="flex items-center space-x-2">
              <button
                onClick={() => setCurrentPage(Math.max(1, currentPage - 1))}
                disabled={currentPage === 1}
                className="px-3 py-1 text-sm border rounded hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed flex items-center space-x-1"
              >
                <ChevronLeft className="h-4 w-4" />
                <span>Previous</span>
              </button>
              
              {/* Page numbers */}
              <div className="flex items-center space-x-1">
                {Array.from({ length: Math.min(5, Math.ceil(totalUsers / itemsPerPage)) }, (_, i) => {
                  const totalPages = Math.ceil(totalUsers / itemsPerPage);
                  let pageNum;
                  
                  // Show pages around current page
                  if (totalPages <= 5) {
                    pageNum = i + 1;
                  } else if (currentPage <= 3) {
                    pageNum = i + 1;
                  } else if (currentPage >= totalPages - 2) {
                    pageNum = totalPages - 4 + i;
                  } else {
                    pageNum = currentPage - 2 + i;
                  }
                  
                  return (
                    <button
                      key={i}
                      onClick={() => setCurrentPage(pageNum)}
                      className={`px-3 py-1 text-sm border rounded ${
                        currentPage === pageNum
                          ? 'bg-blue-600 text-white border-blue-600'
                          : 'hover:bg-gray-50'
                      }`}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>
              
              <button
                onClick={() => setCurrentPage(currentPage + 1)}
                disabled={currentPage * itemsPerPage >= totalUsers}
                className="px-3 py-1 text-sm border rounded hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed flex items-center space-x-1"
              >
                <span>Next</span>
                <ChevronRight className="h-4 w-4" />
              </button>
            </div>
          </div>
        </div>
      )}

      {(isAddModalOpen || isEditModalOpen) && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          <div className="absolute inset-0 bg-black/40" onClick={isAddModalOpen ? closeAddModal : closeEditModal} />
          <div className="relative z-10 w-full max-w-3xl mx-4 max-h-[90vh] overflow-y-auto">
            <div className="card p-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-lg font-semibold text-gray-900">
                  {isAddModalOpen ? 'Tambah Pengguna' : 'Edit Pengguna'}
                </h2>
                <button
                  aria-label="Tutup"
                  onClick={isAddModalOpen ? closeAddModal : closeEditModal}
                  className="p-2 rounded hover:bg-gray-100"
                >
                  <X className="h-5 w-5 text-gray-500" />
                </button>
              </div>
              {formError && (
                <div className="mb-4 rounded border border-red-200 bg-red-50 px-3 py-2 text-sm text-red-700">
                  {formError}
                </div>
              )}
              <form onSubmit={isAddModalOpen ? handleAddUser : handleEditUser} className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Email *</label>
                    <input
                      type="email"
                      className="input-field w-full"
                      value={formData.email}
                      onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                      placeholder="email@university.edu"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Nama</label>
                    <input
                      className="input-field w-full"
                      value={formData.name}
                      onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                      placeholder="Nama lengkap"
                    />
                  </div>
                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Password {isEditModalOpen && '(kosongkan jika tidak ingin mengubah)'}
                    </label>
                    <input
                      type="password"
                      className="input-field w-full"
                      value={formData.password}
                      onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                      placeholder={isEditModalOpen ? 'Kosongkan jika tidak ingin mengubah' : 'Password'}
                      required={isAddModalOpen}
                    />
                  </div>
                </div>

                <div className="border-t pt-4">
                  <div className="flex items-center justify-between mb-3">
                    <label className="block text-sm font-medium text-gray-700">Roles</label>
                    <div className="flex gap-2 flex-wrap">
                      <button type="button" onClick={() => addRole('admin')} className="text-xs px-2 py-1 bg-red-50 text-red-700 rounded hover:bg-red-100">
                        + Admin
                      </button>
                      <button type="button" onClick={() => addRole('staff')} className="text-xs px-2 py-1 bg-purple-50 text-purple-700 rounded hover:bg-purple-100">
                        + Staff
                      </button>
                      <button type="button" onClick={() => addRole('student')} className="text-xs px-2 py-1 bg-green-50 text-green-700 rounded hover:bg-green-100">
                        + Mahasiswa
                      </button>
                      <button type="button" onClick={() => addRole('lecturer')} className="text-xs px-2 py-1 bg-blue-50 text-blue-700 rounded hover:bg-blue-100">
                        + Dosen
                      </button>
                      <button type="button" onClick={() => addRole('student_applicant')} className="text-xs px-2 py-1 bg-yellow-50 text-yellow-700 rounded hover:bg-yellow-100">
                        + Pendaftar
                      </button>
                      <button type="button" onClick={() => addRole('alumni')} className="text-xs px-2 py-1 bg-gray-50 text-gray-700 rounded hover:bg-gray-100">
                        + Alumni
                      </button>
                    </div>
                  </div>

                  <div className="space-y-3">
                    {formData.roles.length === 0 && (
                      <p className="text-sm text-gray-500 italic">Belum ada role. Klik tombol di atas untuk menambahkan.</p>
                    )}
                    {formData.roles.map((roleAssignment, idx) => (
                      <div key={idx} className="border rounded-lg p-3 bg-gray-50">
                        <div className="flex items-start justify-between mb-2">
                          <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getRoleColor(roleAssignment.role)}`}>
                            {getRoleLabel(roleAssignment.role)}
                          </span>
                          <button type="button" onClick={() => removeRole(idx)} className="text-red-600 hover:text-red-800">
                            <X className="h-4 w-4" />
                          </button>
                        </div>

                        {roleAssignment.role === 'student' && (
                          <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Pilih Mahasiswa (NIM)</label>
                            <select
                              className="input-field w-full text-sm"
                              value={roleAssignment.entity_id || ''}
                              onChange={(e) => updateRoleEntity(idx, 'mahasiswa', e.target.value)}
                            >
                              <option value="">-- Pilih Mahasiswa --</option>
                              {students.map((s) => (
                                <option key={s.nim} value={s.nim}>
                                  {s.nim} - {s.nama}
                                </option>
                              ))}
                            </select>
                          </div>
                        )}

                        {roleAssignment.role === 'lecturer' && (
                          <div>
                            <label className="block text-xs font-medium text-gray-700 mb-1">Pilih Dosen (id_dosen)</label>
                            <select
                              className="input-field w-full text-sm"
                              value={roleAssignment.entity_id || ''}
                              onChange={(e) => updateRoleEntity(idx, 'dosen', e.target.value)}
                            >
                              <option value="">-- Pilih Dosen --</option>
                              {lecturers.map((l) => (
                                <option key={l.id} value={l.id}>
                                  {l.id} - {l.nama_dosen}
                                </option>
                              ))}
                            </select>
                          </div>
                        )}

                        {!['student', 'lecturer'].includes(roleAssignment.role) && (
                          <p className="text-xs text-gray-500 italic">
                            Role ini belum memiliki entitas yang perlu dipilih.
                          </p>
                        )}
                      </div>
                    ))}
                  </div>
                </div>

                <div className="flex items-center justify-end space-x-2 pt-2 border-t">
                  <button
                    type="button"
                    onClick={isAddModalOpen ? closeAddModal : closeEditModal}
                    className="btn-secondary"
                    disabled={actionLoading}
                  >
                    Batal
                  </button>
                  <button type="submit" className="btn-primary" disabled={actionLoading}>
                    {actionLoading ? (
                      <Loader className="h-4 w-4 animate-spin" />
                    ) : isAddModalOpen ? (
                      'Simpan'
                    ) : (
                      'Update'
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/* Bulk Add Users Modal - CSV Only */}
      {isBulkAddModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          <div className="absolute inset-0 bg-black/40" onClick={closeBulkAddModal} />
          <div className="relative z-10 w-full max-w-4xl mx-4 max-h-[90vh] overflow-y-auto">
            <div className="card p-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-lg font-semibold text-gray-900">Bulk Add Users via CSV</h2>
                <button
                  aria-label="Tutup"
                  onClick={closeBulkAddModal}
                  className="p-2 rounded hover:bg-gray-100"
                >
                  <X className="h-5 w-5 text-gray-500" />
                </button>
              </div>

              {formError && (
                <div className="mb-4 rounded border border-red-200 bg-red-50 px-3 py-2 text-sm text-red-700">
                  {formError}
                </div>
              )}

              {bulkUsers.length === 0 ? (
                <div>
                  {/* CSV Import Section */}
                  <div className="text-center py-8">
                    <Upload className="mx-auto h-12 w-12 text-gray-400" />
                    <h3 className="mt-4 text-lg font-medium text-gray-900">Import Users from CSV</h3>
                    <p className="mt-2 text-sm text-gray-500">
                      Upload a CSV file containing user data to add multiple users at once
                    </p>
                    <div className="mt-6 flex items-center justify-center space-x-4">
                      <button
                        type="button"
                        onClick={downloadCSVTemplate}
                        className="btn-secondary flex items-center space-x-2"
                      >
                        <span>Download Template</span>
                      </button>
                      <label className="btn-primary flex items-center space-x-2 cursor-pointer">
                        <Upload className="h-4 w-4" />
                        <span>Upload CSV</span>
                        <input
                          type="file"
                          accept=".csv"
                          onChange={handleImportCSV}
                          className="hidden"
                        />
                      </label>
                    </div>
                  </div>

                  {/* CSV Format Documentation */}
                  <div className="mt-6 p-4 bg-blue-50 rounded-lg border border-blue-200">
                    <p className="text-sm font-medium text-blue-900 mb-3">CSV Format Guide:</p>
                    <div className="text-xs text-blue-800 space-y-3">
                      <div>
                        <p className="font-semibold mb-1">Required Columns:</p>
                        <ul className="list-disc list-inside ml-2 space-y-1">
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">email</code> - User email (required, unique)</li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">password</code> - User password (optional)</li>
                        </ul>
                      </div>
                      
                      <div>
                        <p className="font-semibold mb-1">Optional User Columns:</p>
                        <ul className="list-disc list-inside ml-2 space-y-1">
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">name</code> or <code className="bg-blue-100 px-1 py-0.5 rounded">nama</code> - Full name</li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">role</code> - Role: admin, staff, student, lecturer, student_applicant, alumni</li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">entity_type</code> - mahasiswa or dosen (for student/lecturer roles)</li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">entity_id</code> - NIM (for student) or id_dosen (for lecturer)</li>
                        </ul>
                      </div>

                      <div>
                        <p className="font-semibold mb-1">Student Entity Columns (when role=student):</p>
                        <ul className="list-disc list-inside ml-2 space-y-0.5 text-[10px]">
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">nim</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">mahasiswa_email</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">mahasiswa_no_hp</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">mahasiswa_kode_prodi</code></li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">jenis_kelamin</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">tempat_lahir</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">tanggal_lahir</code> (YYYY-MM-DD)</li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">agama</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">kewarganegaraan</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">nik</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">nisn</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">npwp</code></li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">mulai_semester</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">tanggal_masuk</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">jalur_pendaftaran</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">jenis_pendaftaran</code></li>
                        </ul>
                      </div>

                      <div>
                        <p className="font-semibold mb-1">Lecturer Entity Columns (when role=lecturer):</p>
                        <ul className="list-disc list-inside ml-2 space-y-0.5">
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">id_dosen</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">nama_dosen</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">dosen_email</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">dosen_no_hp</code></li>
                          <li><code className="bg-blue-100 px-1 py-0.5 rounded">dosen_kode_prodi</code>, <code className="bg-blue-100 px-1 py-0.5 rounded">nuptk</code></li>
                        </ul>
                      </div>

                      <div className="pt-2 border-t border-blue-300">
                        <p className="text-blue-900 font-semibold">
                          💡 Tip: Download the template to see the correct format with examples.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              ) : (
                <form onSubmit={handleBulkAddUsers} className="space-y-4">
                  {/* Preview of imported users */}
                  <div className="bg-green-50 border border-green-200 rounded-lg p-4">
                    <div className="flex items-center space-x-2 mb-2">
                      <Upload className="h-5 w-5 text-green-600" />
                      <p className="text-sm font-medium text-green-900">
                        CSV Imported Successfully
                      </p>
                    </div>
                    <p className="text-xs text-green-700">
                      {bulkUsers.length} user{bulkUsers.length !== 1 ? 's' : ''} ready to be added. Review the list below and click "Save All Users" to proceed.
                    </p>
                  </div>

                  {/* Users List Preview */}
                  <div className="border rounded-lg overflow-hidden">
                    <div className="bg-gray-50 px-4 py-3 border-b">
                      <h3 className="text-sm font-medium text-gray-900">Users to be Added ({bulkUsers.length})</h3>
                    </div>
                    <div className="max-h-96 overflow-y-auto">
                      <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-100 sticky top-0">
                          <tr>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500">#</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500">Email</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500">Name</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500">Roles</th>
                          </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                          {bulkUsers.map((user, idx) => (
                            <tr key={idx} className="hover:bg-gray-50">
                              <td className="px-4 py-2 text-sm text-gray-500">{idx + 1}</td>
                              <td className="px-4 py-2 text-sm text-gray-900">{user.email}</td>
                              <td className="px-4 py-2 text-sm text-gray-900">{user.name || '-'}</td>
                              <td className="px-4 py-2 text-sm">
                                {user.roles && user.roles.length > 0 ? (
                                  <div className="flex flex-wrap gap-1">
                                    {user.roles.map((r, ridx) => (
                                      <span key={ridx} className={`inline-flex px-2 py-0.5 text-xs font-semibold rounded-full ${getRoleColor(r.role)}`}>
                                        {getRoleLabel(r.role)}
                                        {r.entity_id && <span className="ml-1">({r.entity_id})</span>}
                                      </span>
                                    ))}
                                  </div>
                                ) : (
                                  <span className="text-gray-400 text-xs">No role</span>
                                )}
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="flex items-center justify-between pt-4 border-t">
                    <button
                      type="button"
                      onClick={() => setBulkUsers([])}
                      className="text-sm text-blue-600 hover:text-blue-800 flex items-center space-x-1"
                    >
                      <Upload className="h-4 w-4" />
                      <span>Import Different CSV</span>
                    </button>
                    <div className="flex items-center space-x-3">
                      <button
                        type="button"
                        onClick={closeBulkAddModal}
                        className="btn-secondary"
                        disabled={actionLoading}
                      >
                        Cancel
                      </button>
                      <button type="submit" className="btn-primary" disabled={actionLoading}>
                        {actionLoading ? (
                          <div className="flex items-center space-x-2">
                            <Loader className="h-4 w-4 animate-spin" />
                            <span>Saving...</span>
                          </div>
                        ) : (
                          `Save All ${bulkUsers.length} User${bulkUsers.length !== 1 ? 's' : ''}`
                        )}
                      </button>
                    </div>
                  </div>
                </form>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserManagement;
