import React, { useMemo, useState } from 'react';
import { Plus, Edit, Trash2, Search, X, GraduationCap, AlertCircle, CheckCircle, Loader } from 'lucide-react';
import type { Prodi, CreateProdiRequest, UpdateProdiRequest } from '../../types';
import { extractString } from '../../utils/sqlNull';
import { usePrograms, useCreateProgram, useUpdateProgram, useDeleteProgram } from '../../hooks/usePrograms';

const Programs: React.FC = () => {
  const [programs, setPrograms] = useState<Prodi[]>([]);
  const { data: programsData, isLoading: isProgramsLoading, error: programsError } = usePrograms();
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedDegree, setSelectedDegree] = useState<string>('all');
  
  // Modal states
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [selectedProgram, setSelectedProgram] = useState<Prodi | null>(null);
  
  // Form states
  const [formData, setFormData] = useState<CreateProdiRequest>({
    kode_prodi: '',
    nama_prodi: '',
    jenjang: '',
  });
  const [formError, setFormError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Success message
  const [successMessage, setSuccessMessage] = useState<string | null>(null);

  // Sync programs from cache
  React.useEffect(() => {
    if (Array.isArray(programsData)) {
      setPrograms(programsData);
    } else if (programsData) {
      console.warn('Unexpected /programs response format:', programsData);
      setPrograms([]);
    }
  }, [programsData]);

  React.useEffect(() => {
    if (programsError) setError(programsError instanceof Error ? programsError.message : String(programsError));
    else setError(null);
  }, [programsError]);

  const { mutateAsync: createProgram, isPending: isCreatePending } = useCreateProgram();
  const handleCreateProgram = async (e: React.FormEvent) => {
    e.preventDefault();
    setFormError(null);
    setIsSubmitting(true);

    try {
      await createProgram(formData);
      setSuccessMessage('Program created successfully');
      setIsCreateModalOpen(false);
      setFormData({ kode_prodi: '', nama_prodi: '', jenjang: '' });
      setTimeout(() => setSuccessMessage(null), 3000);
    } catch (err) {
      setFormError(err instanceof Error ? err.message : 'Failed to create program');
    } finally {
      setIsSubmitting(false);
    }
  };

  const { mutateAsync: updateProgram, isPending: isUpdatePending } = useUpdateProgram();
  const handleUpdateProgram = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedProgram) return;

    setFormError(null);
    setIsSubmitting(true);

    try {
      const updateData: UpdateProdiRequest = {
        nama_prodi: formData.nama_prodi,
        jenjang: formData.jenjang,
      };
      await updateProgram({ kode_prodi: selectedProgram.kode_prodi, data: updateData });
      setSuccessMessage('Program updated successfully');
      setIsEditModalOpen(false);
      setSelectedProgram(null);
      setFormData({ kode_prodi: '', nama_prodi: '', jenjang: '' });
      setTimeout(() => setSuccessMessage(null), 3000);
    } catch (err) {
      setFormError(err instanceof Error ? err.message : 'Failed to update program');
    } finally {
      setIsSubmitting(false);
    }
  };

  const { mutateAsync: deleteProgram, isPending: isDeletePending } = useDeleteProgram();
  const handleDeleteProgram = async () => {
    if (!selectedProgram) return;

    setFormError(null);
    setIsSubmitting(true);

    try {
      await deleteProgram({ kode_prodi: selectedProgram.kode_prodi });
      setSuccessMessage('Program deleted successfully');
      setIsDeleteModalOpen(false);
      setSelectedProgram(null);
      setTimeout(() => setSuccessMessage(null), 3000);
    } catch (err) {
      setFormError(err instanceof Error ? err.message : 'Failed to delete program');
    } finally {
      setIsSubmitting(false);
    }
  };

  const openEditModal = (program: Prodi) => {
    setSelectedProgram(program);
    setFormData({
      kode_prodi: program.kode_prodi,
      nama_prodi: program.nama_prodi,
      jenjang: extractString(program.jenjang, ''),
    });
    setFormError(null);
    setIsEditModalOpen(true);
  };

  const openDeleteModal = (program: Prodi) => {
    setSelectedProgram(program);
    setFormError(null);
    setIsDeleteModalOpen(true);
  };

  const closeModals = () => {
    setIsCreateModalOpen(false);
    setIsEditModalOpen(false);
    setIsDeleteModalOpen(false);
    setSelectedProgram(null);
    setFormData({ kode_prodi: '', nama_prodi: '', jenjang: '' });
    setFormError(null);
  };

  // Ensure programs is always an array to avoid runtime errors when API returns null
  const safePrograms = useMemo(() => (Array.isArray(programs) ? programs : []), [programs]);

  // Filter programs
  const filteredPrograms = safePrograms.filter(program => {
    const matchesSearch = 
      program.kode_prodi.toLowerCase().includes(searchTerm.toLowerCase()) ||
      program.nama_prodi.toLowerCase().includes(searchTerm.toLowerCase());
    
    const programDegree = extractString(program.jenjang, '');
    const matchesDegree = selectedDegree === 'all' || programDegree === selectedDegree;
    
    return matchesSearch && matchesDegree;
  });

  // Get unique degrees for filter
  const uniqueDegrees = Array.from(
    new Set(
      safePrograms
        .map(p => extractString(p.jenjang, ''))
        .filter(Boolean)
    )
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Programs Management</h1>
          <p className="text-gray-600 mt-1">Manage study programs and their information</p>
        </div>
        <button
          onClick={() => {
            setFormData({ kode_prodi: '', nama_prodi: '', jenjang: '' });
            setFormError(null);
            setIsCreateModalOpen(true);
          }}
          className="btn-primary flex items-center gap-2"
        >
          <Plus className="w-5 h-5" />
          Add Program
        </button>
      </div>

      {/* Success Message */}
      {successMessage && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4 flex items-center gap-3">
          <CheckCircle className="w-5 h-5 text-green-600 flex-shrink-0" />
          <p className="text-green-800">{successMessage}</p>
        </div>
      )}

      {/* Error Message */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4 flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0" />
          <p className="text-red-800">{error}</p>
        </div>
      )}

      {/* Filters */}
      <div className="card">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
              <input
                type="text"
                placeholder="Search by code or name..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="input-field pl-10"
              />
            </div>
          </div>
          <div className="md:w-64">
            <select
              value={selectedDegree}
              onChange={(e) => setSelectedDegree(e.target.value)}
              className="input-field"
            >
              <option value="all">All Degrees</option>
              {uniqueDegrees.map(degree => (
                <option key={degree} value={degree}>{degree}</option>
              ))}
            </select>
          </div>
        </div>
      </div>

      {/* Programs Table */}
      <div className="card overflow-hidden">
        {isProgramsLoading ? (
          <div className="flex items-center justify-center py-12">
            <Loader className="w-8 h-8 animate-spin text-blue-600" />
          </div>
        ) : filteredPrograms.length === 0 ? (
          <div className="text-center py-12">
            <GraduationCap className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            <p className="text-gray-500 text-lg">No programs found</p>
            <p className="text-gray-400 text-sm mt-1">Try adjusting your search or filters</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b border-gray-200">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Code
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Program Name
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Degree
                  </th>
                  <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredPrograms.map((program) => (
                  <tr key={program.kode_prodi} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10 bg-blue-100 rounded-lg flex items-center justify-center">
                          <GraduationCap className="w-5 h-5 text-blue-600" />
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">{program.kode_prodi}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm text-gray-900">{program.nama_prodi}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-blue-100 text-blue-800">
                        {extractString(program.jenjang, 'N/A')}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button
                        onClick={() => openEditModal(program)}
                        className="text-blue-600 hover:text-blue-900 mr-4"
                        title="Edit"
                      >
                        <Edit className="w-5 h-5" />
                      </button>
                      <button
                        onClick={() => openDeleteModal(program)}
                        className="text-red-600 hover:text-red-900"
                        title="Delete"
                      >
                        <Trash2 className="w-5 h-5" />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Create Modal */}
      {isCreateModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-bold text-gray-900">Create New Program</h2>
              <button onClick={closeModals} className="text-gray-400 hover:text-gray-600">
                <X className="w-6 h-6" />
              </button>
            </div>

            {formError && (
              <div className="mb-4 bg-red-50 border border-red-200 rounded-lg p-3 flex items-center gap-2">
                <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0" />
                <p className="text-sm text-red-800">{formError}</p>
              </div>
            )}

            <form onSubmit={handleCreateProgram} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Program Code <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={formData.kode_prodi}
                  onChange={(e) => setFormData({ ...formData, kode_prodi: e.target.value })}
                  className="input-field"
                  required
                  placeholder="e.g., IF, SI, MT"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Program Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={formData.nama_prodi}
                  onChange={(e) => setFormData({ ...formData, nama_prodi: e.target.value })}
                  className="input-field"
                  required
                  placeholder="e.g., Teknik Informatika"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Degree Level
                </label>
                <select
                  value={formData.jenjang || ''}
                  onChange={(e) => setFormData({ ...formData, jenjang: e.target.value })}
                  className="input-field"
                >
                  <option value="">Select Degree</option>
                  <option value="D3">D3</option>
                  <option value="D4">D4</option>
                  <option value="S1">S1</option>
                  <option value="S2">S2</option>
                  <option value="S3">S3</option>
                </select>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={closeModals}
                  className="btn-secondary flex-1"
                  disabled={isSubmitting || isCreatePending}
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="btn-primary flex-1 flex items-center justify-center gap-2"
                  disabled={isSubmitting || isCreatePending}
                >
                  {isSubmitting || isCreatePending ? (
                    <>
                      <Loader className="w-4 h-4 animate-spin" />
                      Creating...
                    </>
                  ) : (
                    <>
                      <Plus className="w-4 h-4" />
                      Create Program
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Edit Modal */}
      {isEditModalOpen && selectedProgram && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-bold text-gray-900">Edit Program</h2>
              <button onClick={closeModals} className="text-gray-400 hover:text-gray-600">
                <X className="w-6 h-6" />
              </button>
            </div>

            {formError && (
              <div className="mb-4 bg-red-50 border border-red-200 rounded-lg p-3 flex items-center gap-2">
                <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0" />
                <p className="text-sm text-red-800">{formError}</p>
              </div>
            )}

            <form onSubmit={handleUpdateProgram} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Program Code
                </label>
                <input
                  type="text"
                  value={formData.kode_prodi}
                  className="input-field bg-gray-100"
                  disabled
                />
                <p className="text-xs text-gray-500 mt-1">Program code cannot be changed</p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Program Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={formData.nama_prodi}
                  onChange={(e) => setFormData({ ...formData, nama_prodi: e.target.value })}
                  className="input-field"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Degree Level
                </label>
                <select
                  value={formData.jenjang || ''}
                  onChange={(e) => setFormData({ ...formData, jenjang: e.target.value })}
                  className="input-field"
                >
                  <option value="">Select Degree</option>
                  <option value="D3">D3</option>
                  <option value="D4">D4</option>
                  <option value="S1">S1</option>
                  <option value="S2">S2</option>
                  <option value="S3">S3</option>
                </select>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={closeModals}
                  className="btn-secondary flex-1"
                  disabled={isSubmitting || isUpdatePending}
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="btn-primary flex-1 flex items-center justify-center gap-2"
                  disabled={isSubmitting || isUpdatePending}
                >
                  {isSubmitting || isUpdatePending ? (
                    <>
                      <Loader className="w-4 h-4 animate-spin" />
                      Updating...
                    </>
                  ) : (
                    <>
                      <Edit className="w-4 h-4" />
                      Update Program
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {isDeleteModalOpen && selectedProgram && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-bold text-gray-900">Delete Program</h2>
              <button onClick={closeModals} className="text-gray-400 hover:text-gray-600">
                <X className="w-6 h-6" />
              </button>
            </div>

            {formError && (
              <div className="mb-4 bg-red-50 border border-red-200 rounded-lg p-3 flex items-center gap-2">
                <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0" />
                <p className="text-sm text-red-800">{formError}</p>
              </div>
            )}

            <div className="mb-6">
              <div className="flex items-center gap-3 p-4 bg-red-50 rounded-lg border border-red-200">
                <AlertCircle className="w-6 h-6 text-red-600 flex-shrink-0" />
                <div>
                  <p className="text-sm font-medium text-red-900">
                    Are you sure you want to delete this program?
                  </p>
                  <p className="text-sm text-red-700 mt-1">
                    <strong>{selectedProgram.kode_prodi}</strong> - {selectedProgram.nama_prodi}
                  </p>
                  <p className="text-xs text-red-600 mt-2">
                    This action cannot be undone.
                  </p>
                </div>
              </div>
            </div>

            <div className="flex gap-3">
              <button
                type="button"
                onClick={closeModals}
                className="btn-secondary flex-1"
                disabled={isSubmitting || isDeletePending}
              >
                Cancel
              </button>
              <button
                onClick={handleDeleteProgram}
                className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2"
                disabled={isSubmitting || isDeletePending}
              >
                {isSubmitting || isDeletePending ? (
                  <>
                    <Loader className="w-4 h-4 animate-spin" />
                    Deleting...
                  </>
                ) : (
                  <>
                    <Trash2 className="w-4 h-4" />
                    Delete Program
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Stats Footer */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="card">
          <div className="flex items-center gap-4">
            <div className="bg-blue-100 p-3 rounded-lg">
              <GraduationCap className="w-6 h-6 text-blue-600" />
            </div>
            <div>
              <p className="text-sm text-gray-600">Total Programs</p>
              <p className="text-2xl font-bold text-gray-900">{programs.length}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center gap-4">
            <div className="bg-green-100 p-3 rounded-lg">
              <Search className="w-6 h-6 text-green-600" />
            </div>
            <div>
              <p className="text-sm text-gray-600">Filtered Results</p>
              <p className="text-2xl font-bold text-gray-900">{filteredPrograms.length}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center gap-4">
            <div className="bg-purple-100 p-3 rounded-lg">
              <GraduationCap className="w-6 h-6 text-purple-600" />
            </div>
            <div>
              <p className="text-sm text-gray-600">Degree Types</p>
              <p className="text-2xl font-bold text-gray-900">{uniqueDegrees.length}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Programs;
