import React, { useEffect, useMemo, useState } from 'react';
import { 
  Calendar, 
  BookOpen, 
  GraduationCap,
  ChevronDown,
  ChevronUp,
  Search,
  Filter,
  Loader2,
  AlertCircle
} from 'lucide-react';
import type { CourseWithClasses } from '../../types';
import { useAuth } from '../../contexts/AuthContext';
import { useCourses } from '../../hooks/useCourses';
import { usePrograms } from '../../hooks/usePrograms';

const ClassManagement: React.FC = () => {
  const { currentSemester } = useAuth();
  const [courses, setCourses] = useState<CourseWithClasses[]>([]);
  const [filteredCourses, setFilteredCourses] = useState<CourseWithClasses[]>([]);
  const [programs, setPrograms] = useState<Array<{ kode_prodi: string; nama_prodi: string }>>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [expandedCourses, setExpandedCourses] = useState<Set<string>>(new Set());
  const [filterMode] = useState<'all' | 'Offline' | 'Online' | 'Hybrid'>('all');
  const [filterProgram, setFilterProgram] = useState<string>('all');

  // React Query: programs and courses (cached)
  const { data: programsData } = usePrograms();
  const { data: coursesData, isLoading: isCoursesLoading, error: coursesError } = useCourses(
    useMemo(() => (
      currentSemester
        ? { semester: currentSemester, include_classes: true }
        : undefined
    ), [currentSemester])
  );

  // Sync hook data into local state for filtering/expansion logic
  useEffect(() => {
    if (programsData) {
      setPrograms(programsData);
    }
  }, [programsData]);

  useEffect(() => {
    // Filter courses based on search query, mode filter, and program filter
    let filtered = courses;

    // Search filter
    if (searchQuery) {
      filtered = filtered.filter(course => 
        course.nama_mk.toLowerCase().includes(searchQuery.toLowerCase()) ||
        course.kode_mk.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    // Program filter
    if (filterProgram !== 'all') {
      filtered = filtered.filter(course => course.kode_prodi === filterProgram);
    }

    // Mode filter
    if (filterMode !== 'all') {
      filtered = filtered.map(course => ({
        ...course,
        classes: course.classes?.filter(cls => cls.mode_kuliah === filterMode)
      })).filter(course => course.classes && course.classes.length > 0);
    }

    setFilteredCourses(filtered);
  }, [searchQuery, filterMode, filterProgram, courses]);

  // When courses hook resolves, propagate to local state and handle errors/loading
  useEffect(() => {
    setLoading(isCoursesLoading || (!currentSemester && true));
  }, [isCoursesLoading, currentSemester]);

  useEffect(() => {
    if (coursesError) {
      const msg = coursesError instanceof Error ? coursesError.message : 'Failed to load courses';
      setError(msg);
      setCourses([]);
      setFilteredCourses([]);
    } else {
      setError(null);
    }
  }, [coursesError]);

  useEffect(() => {
    if (Array.isArray(coursesData)) {
      const list = coursesData;
      setCourses(list);
      setFilteredCourses(list);
    }
  }, [coursesData]);

  const toggleCourseExpand = (kode_mk: string) => {
    const newExpanded = new Set(expandedCourses);
    if (newExpanded.has(kode_mk)) {
      newExpanded.delete(kode_mk);
    } else {
      newExpanded.add(kode_mk);
    }
    setExpandedCourses(newExpanded);
  };

  const getTotalSKS = (course: CourseWithClasses): number => {
    return (course.sks_tatap_muka || 0) + 
           (course.sks_praktek || 0) + 
           (course.sks_prak_lapangan || 0) + 
           (course.sks_simulasi || 0);
  };

  const formatDate = (dateString?: string): string => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('id-ID', { day: '2-digit', month: 'short', year: 'numeric' });
  };

  const getModeColor = (mode?: string): string => {
    switch (mode) {
      case 'Online': return 'bg-blue-100 text-blue-700';
      case 'Offline': return 'bg-green-100 text-green-700';
      case 'Hybrid': return 'bg-purple-100 text-purple-700';
      default: return 'bg-gray-100 text-gray-700';
    }
  };

  const getSemesterLabel = (semester: string): string => {
    if (!semester || semester.length < 5) return 'Unknown Semester';
    const year = semester.slice(0, 4);
    const term = semester.slice(4) === '1' ? 'Ganjil' : 'Genap';
    const academicYear = `${year}/${parseInt(year) + 1}`;
    return `${academicYear} ${term}`;
  };

  const totalCourses = filteredCourses.length;
  const totalClasses = filteredCourses.reduce((sum, course) => sum + (course.classes?.length || 0), 0);

  // Show loading if still fetching or if currentSemester is not yet available
  if (loading || !currentSemester) {
    return (
      <div className="max-w-7xl mx-auto">
        <div className="flex items-center justify-center py-12">
          <Loader2 className="h-8 w-8 text-blue-600 animate-spin" />
          <span className="ml-3 text-gray-600">
            {!currentSemester ? 'Loading semester information...' : 'Loading courses...'}
          </span>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-2">
          <h1 className="text-2xl font-bold text-gray-900">Class Management</h1>
          <div className="flex items-center gap-2 text-sm">
            <Calendar className="h-4 w-4 text-gray-500" />
            <span className="text-gray-500">Active Semester:</span>
            <span className="font-medium text-gray-900">{getSemesterLabel(currentSemester)}</span>
          </div>
        </div>
        
        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
          <div className="card bg-gradient-to-br from-blue-50 to-blue-100 border-blue-200">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-blue-600 rounded-lg">
                <BookOpen className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-blue-600 font-medium">Total Courses</p>
                <p className="text-2xl font-bold text-blue-900">{totalCourses}</p>
              </div>
            </div>
          </div>
          
          <div className="card bg-gradient-to-br from-green-50 to-green-100 border-green-200">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-green-600 rounded-lg">
                <GraduationCap className="h-5 w-5 text-white" />
              </div>
              <div>
                <p className="text-sm text-green-600 font-medium">Total Classes</p>
                <p className="text-2xl font-bold text-green-900">{totalClasses}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Filters & Search */}
      <div className="card mb-6">
        <div className="flex flex-col sm:flex-row gap-3">
          {/* Search */}
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
            <input
              type="text"
              placeholder="Search by course name or code..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="input-field pl-10 w-full"
            />
          </div>

          {/* Program Filter */}
          <div className="flex items-center gap-2 flex-shrink-0">
            <Filter className="h-5 w-5 text-gray-400 flex-shrink-0" />
            <select
              value={filterProgram}
              onChange={(e) => setFilterProgram(e.target.value)}
              className="input-field min-w-[200px]"
            >
              <option value="all">All Programs</option>
              {programs.map((prodi) => (
                <option key={prodi.kode_prodi} value={prodi.kode_prodi}>
                  {prodi.nama_prodi} ({prodi.kode_prodi})
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="card bg-red-50 border-red-200 mb-6">
          <div className="flex items-center gap-3 text-red-700">
            <AlertCircle className="h-5 w-5 flex-shrink-0" />
            <div>
              <p className="font-medium">Error loading courses</p>
              <p className="text-sm text-red-600">{error}</p>
            </div>
          </div>
        </div>
      )}

      {/* Courses List */}
      {filteredCourses.length === 0 ? (
        <div className="card text-center py-12">
          <BookOpen className="h-12 w-12 text-gray-300 mx-auto mb-3" />
          <p className="text-gray-500 mb-2">No courses found</p>
          <p className="text-sm text-gray-400">
            {searchQuery || filterMode !== 'all' 
              ? 'Try adjusting your filters or search query' 
              : 'No courses available for this semester'}
          </p>
        </div>
      ) : (
        <div className="space-y-2">
          {filteredCourses.map((course) => {
            const isExpanded = expandedCourses.has(course.kode_mk);
            const hasClasses = course.classes && course.classes.length > 0;
            
            return (
              <div key={course.kode_mk} className="card hover:shadow-md transition-shadow">
                {/* Compact Course Header - Single Line */}
                <div 
                  className="flex items-center justify-between gap-2 cursor-pointer"
                  onClick={() => hasClasses && toggleCourseExpand(course.kode_mk)}
                >
                  <div className="flex items-center gap-2 min-w-0 flex-1">
                    <div className="flex-shrink-0">
                      {hasClasses && (
                        <div className="text-gray-400">
                          {isExpanded ? (
                            <ChevronUp className="h-4 w-4" />
                          ) : (
                            <ChevronDown className="h-4 w-4" />
                          )}
                        </div>
                      )}
                    </div>
                    
                    {/* Course Info */}
                    <div className="min-w-0 flex-1">
                      <div className="flex items-center gap-2 flex-wrap">
                        <span className="px-2 py-0.5 bg-gray-100 text-gray-700 text-xs font-medium rounded whitespace-nowrap">
                          {course.kode_mk}
                        </span>
                        <h3 className="text-sm font-semibold text-gray-900 truncate">
                          {course.nama_mk}
                        </h3>
                        {course.jenis_mk && (
                          <span className="px-1.5 py-0.5 bg-blue-100 text-blue-700 text-xs font-medium rounded whitespace-nowrap">
                            {course.jenis_mk}
                          </span>
                        )}
                      </div>
                    </div>

                    {/* Course Meta - Compact */}
                    <div className="flex items-center gap-2 text-xs text-gray-500 flex-shrink-0">
                      <div className="flex items-center gap-0.5 whitespace-nowrap">
                        <GraduationCap className="h-3.5 w-3.5" />
                        <span>{getTotalSKS(course)} SKS</span>
                      </div>
                      
                      {hasClasses && (
                        <span className="px-1.5 py-0.5 bg-green-100 text-green-700 font-medium rounded whitespace-nowrap">
                          {course.classes!.length} classes
                        </span>
                      )}
                    </div>
                  </div>
                </div>

                {/* Classes List (Expanded) */}
                {isExpanded && hasClasses && (
                  <div className="mt-3 pt-3 border-t border-gray-200 space-y-2">
                    {course.classes!.map((kelas) => (
                      <div 
                        key={kelas.id}
                        className="p-2 bg-gray-50 rounded text-xs hover:bg-gray-100 transition-colors"
                      >
                        <div className="flex items-center justify-between gap-2 flex-wrap">
                          <div className="flex items-center gap-2">
                            <h4 className="font-medium text-gray-900 whitespace-nowrap">
                              Class {kelas.nama_kelas}
                            </h4>
                            {kelas.mode_kuliah && (
                              <span className={`px-1.5 py-0.5 text-xs font-medium rounded whitespace-nowrap ${getModeColor(kelas.mode_kuliah)}`}>
                                {kelas.mode_kuliah}
                              </span>
                            )}
                          </div>
                          
                          <div className="flex items-center gap-1.5 text-gray-600 flex-wrap">
                            {kelas.tgl_mulai && (
                              <span className="whitespace-nowrap">{formatDate(kelas.tgl_mulai)}</span>
                            )}
                            {kelas.sks_tatap_muka > 0 && (
                              <span className="whitespace-nowrap">{kelas.sks_tatap_muka}SKS</span>
                            )}
                          </div>
                        </div>
                        
                        {kelas.bahasan && (
                          <p className="text-gray-600 mt-1">
                            {kelas.bahasan}
                          </p>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default ClassManagement;
