import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';

/**
 * UnderDevelopment page shown when a feature is not yet available
 */
export default function UnderDevelopment() {
  const navigate = useNavigate();
  const { user } = useAuth();

  const handleGoBack = () => {
    // Navigate to role-specific dashboard
    const dashboardPath = user?.role ? `/${user.role}/dashboard` : '/dashboard';
    navigate(dashboardPath);
  };

  return (
    <div className="min-h-[calc(100vh-200px)] flex items-center justify-center p-6">
      <div className="card max-w-2xl w-full text-center space-y-6">
        {/* Icon */}
        <div className="flex justify-center">
          <div className="w-24 h-24 bg-yellow-100 rounded-full flex items-center justify-center">
            <svg
              className="w-12 h-12 text-yellow-600"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
              />
            </svg>
          </div>
        </div>

        {/* Title */}
        <h1 className="text-3xl font-bold text-gray-800">
          Under Development
        </h1>

        {/* Message */}
        <div className="space-y-3">
          <p className="text-lg text-gray-600">
            This feature is currently under development and not yet available.
          </p>
          <p className="text-sm text-gray-500">
            Our team is working hard to bring you this functionality. 
            Please check back later or contact your administrator for more information.
          </p>
        </div>

        {/* Action buttons */}
        <div className="flex gap-4 justify-center pt-4">
          <button
            onClick={handleGoBack}
            className="btn-primary"
          >
            Go to Dashboard
          </button>
          <button
            onClick={() => navigate(-1)}
            className="btn-secondary"
          >
            Go Back
          </button>
        </div>

        {/* Environment indicator (only in dev mode) */}
        {import.meta.env.DEV && (
          <div className="mt-6 p-4 bg-blue-50 rounded-lg">
            <p className="text-xs text-blue-600 font-mono">
              💡 Dev Note: Enable this page by setting the appropriate VITE_PAGE_* 
              environment variable to 'true' in your .env file
            </p>
          </div>
        )}
      </div>
    </div>
  );
}
