import React from 'react';
import { useAuth } from '../../contexts/AuthContext';

const Profile: React.FC = () => {
  const { user } = useAuth();

  return (
    <div className="max-w-3xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Profile</h1>

      <div className="card">
        <div className="flex items-center space-x-4">
          <div className="h-16 w-16 rounded-full bg-primary-600 text-white flex items-center justify-center text-xl font-semibold">
            {user?.name?.[0] || 'U'}
          </div>
          <div>
            <div className="text-lg font-semibold">{user?.name}</div>
            <div className="text-sm text-gray-500">{user?.email}</div>
            {user?.studentId && (
              <div className="text-sm text-gray-500">NIM: {user.studentId}</div>
            )}
          </div>
        </div>
      </div>

      <div className="card grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <div className="text-sm text-gray-500">Program</div>
          <div className="font-medium">Informatics (S1)</div>
        </div>
        <div>
          <div className="text-sm text-gray-500">Current Semester</div>
          <div className="font-medium">5</div>
        </div>
        <div>
          <div className="text-sm text-gray-500">Academic Status</div>
          <div className="font-medium">Active</div>
        </div>
        <div>
          <div className="text-sm text-gray-500">Advisor</div>
          <div className="font-medium">Dr. Ahmad Rahman</div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
