import React from 'react';
import { Award, Briefcase, Users, Calendar, FileText, TrendingUp, Mail, Building } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';

const AlumniDashboard: React.FC = () => {
  const { user } = useAuth();

  const alumniProfile = {
    graduationYear: 2022,
    program: 'Teknik Informatika',
    gpa: '3.85',
    currentEmployer: 'Tech Innovators Inc.',
    position: 'Senior Software Engineer',
  };

  const quickStats = [
    { label: 'Years Since Graduation', value: '3', icon: <Award className="h-6 w-6 text-blue-600" />, color: 'bg-blue-100' },
    { label: 'Network Connections', value: '156', icon: <Users className="h-6 w-6 text-green-600" />, color: 'bg-green-100' },
    { label: 'Events Attended', value: '8', icon: <Calendar className="h-6 w-6 text-purple-600" />, color: 'bg-purple-100' },
    { label: 'Contributions', value: '12', icon: <TrendingUp className="h-6 w-6 text-orange-600" />, color: 'bg-orange-100' },
  ];

  const upcomingEvents = [
    { 
      title: 'Alumni Networking Night 2025',
      date: 'Oct 15, 2025',
      location: 'Grand Ballroom, Campus Center',
      attendees: '120+ expected',
    },
    {
      title: 'Career Mentorship Program',
      date: 'Oct 20, 2025',
      location: 'Online via Zoom',
      attendees: '50+ mentors',
    },
    {
      title: 'Annual Alumni Reunion',
      date: 'Nov 5, 2025',
      location: 'University Main Hall',
      attendees: '300+ alumni',
    },
  ];

  const opportunities = [
    { 
      type: 'Mentorship',
      title: 'Be a Mentor for Current Students',
      description: 'Share your experience with students in your field',
      icon: <Users className="h-6 w-6 text-blue-600" />,
    },
    {
      type: 'Guest Lecture',
      title: 'Share Your Industry Experience',
      description: 'Conduct a guest lecture in your area of expertise',
      icon: <FileText className="h-6 w-6 text-green-600" />,
    },
    {
      type: 'Recruitment',
      title: 'Post Job Opportunities',
      description: 'Help fellow alumni and students find jobs',
      icon: <Briefcase className="h-6 w-6 text-purple-600" />,
    },
  ];

  const recentNews = [
    {
      title: 'New Alumni Directory Launched',
      date: 'Oct 1, 2025',
      preview: 'Connect with thousands of alumni worldwide through our new platform...',
    },
    {
      title: 'Alumni Success Story: John Doe',
      date: 'Sep 28, 2025',
      preview: 'Former IT student now leading innovation at Fortune 500 company...',
    },
    {
      title: 'Scholarship Fund Reaches $1M',
      date: 'Sep 25, 2025',
      preview: 'Thanks to alumni contributions, we can now support 50 more students...',
    },
  ];

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <div className="bg-gradient-to-r from-purple-600 to-purple-800 rounded-lg shadow-lg p-6 text-white">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold mb-2">Welcome back, {user?.name}!</h1>
            <p className="text-purple-100">Alumni Portal - Stay connected with your alma mater</p>
            <div className="mt-4 flex items-center space-x-6 text-sm">
              <div>
                <span className="text-purple-200">Graduated:</span>
                <span className="font-semibold ml-2">{alumniProfile.graduationYear}</span>
              </div>
              <div>
                <span className="text-purple-200">Program:</span>
                <span className="font-semibold ml-2">{alumniProfile.program}</span>
              </div>
              <div>
                <span className="text-purple-200">GPA:</span>
                <span className="font-semibold ml-2">{alumniProfile.gpa}</span>
              </div>
            </div>
          </div>
          <div className="hidden md:block">
            <Award className="h-16 w-16 text-purple-200 opacity-50" />
          </div>
        </div>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {quickStats.map((stat, index) => (
          <div key={index} className="card p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600 mb-1">{stat.label}</p>
                <p className="text-3xl font-bold text-gray-900">{stat.value}</p>
              </div>
              <div className={`p-3 rounded-lg ${stat.color}`}>
                {stat.icon}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Current Employment */}
      <div className="card p-6 bg-gradient-to-br from-blue-50 to-indigo-50">
        <div className="flex items-start space-x-4">
          <div className="p-3 bg-white rounded-lg shadow-sm">
            <Building className="h-8 w-8 text-blue-600" />
          </div>
          <div className="flex-1">
            <h2 className="text-lg font-semibold text-gray-900 mb-1">Current Employment</h2>
            <p className="text-xl font-bold text-blue-900">{alumniProfile.currentEmployer}</p>
            <p className="text-blue-700 mt-1">{alumniProfile.position}</p>
            <button className="mt-4 text-sm text-blue-600 hover:text-blue-800 font-medium cursor-pointer">
              Update Employment Information →
            </button>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Upcoming Events */}
        <div className="card p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Upcoming Alumni Events</h2>
            <Calendar className="h-5 w-5 text-gray-400" />
          </div>
          <div className="space-y-4">
            {upcomingEvents.map((event, index) => (
              <div key={index} className="p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors">
                <h3 className="font-semibold text-gray-900">{event.title}</h3>
                <div className="mt-2 space-y-1 text-sm text-gray-600">
                  <p className="flex items-center">
                    <Calendar className="h-4 w-4 mr-2" />
                    {event.date}
                  </p>
                  <p className="flex items-center">
                    <Building className="h-4 w-4 mr-2" />
                    {event.location}
                  </p>
                  <p className="flex items-center">
                    <Users className="h-4 w-4 mr-2" />
                    {event.attendees}
                  </p>
                </div>
                <button className="mt-3 text-sm text-blue-600 hover:text-blue-800 font-medium cursor-pointer">
                  Register Now →
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Get Involved */}
        <div className="space-y-6">
          <div className="card p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Get Involved</h2>
            <div className="space-y-4">
              {opportunities.map((opp, index) => (
                <div key={index} className="flex items-start space-x-3 p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors cursor-pointer">
                  <div className="flex-shrink-0 mt-1">{opp.icon}</div>
                  <div className="flex-1">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-medium text-blue-600 uppercase">{opp.type}</span>
                    </div>
                    <h3 className="font-medium text-gray-900 mt-1">{opp.title}</h3>
                    <p className="text-sm text-gray-600 mt-1">{opp.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Alumni News */}
          <div className="card p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Alumni News</h2>
            <div className="space-y-4">
              {recentNews.map((news, index) => (
                <div key={index} className="pb-4 border-b border-gray-200 last:border-0">
                  <h3 className="font-medium text-gray-900 hover:text-blue-600 cursor-pointer">
                    {news.title}
                  </h3>
                  <p className="text-xs text-gray-500 mt-1">{news.date}</p>
                  <p className="text-sm text-gray-600 mt-2">{news.preview}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="card p-6">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <button className="p-4 bg-blue-50 hover:bg-blue-100 rounded-lg transition-colors cursor-pointer">
            <Users className="h-8 w-8 text-blue-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Alumni Directory</p>
          </button>
          <button className="p-4 bg-green-50 hover:bg-green-100 rounded-lg transition-colors cursor-pointer">
            <Mail className="h-8 w-8 text-green-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Contact Alumni Office</p>
          </button>
          <button className="p-4 bg-purple-50 hover:bg-purple-100 rounded-lg transition-colors cursor-pointer">
            <TrendingUp className="h-8 w-8 text-purple-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Make a Donation</p>
          </button>
          <button className="p-4 bg-orange-50 hover:bg-orange-100 rounded-lg transition-colors cursor-pointer">
            <Briefcase className="h-8 w-8 text-orange-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Job Board</p>
          </button>
        </div>
      </div>
    </div>
  );
};

export default AlumniDashboard;
