import React, { useState } from 'react';
import { Plus, Edit, Trash2, Search, Filter, Database, Building2, MapPin, GraduationCap } from 'lucide-react';

interface Faculty {
  id: string;
  code: string;
  name: string;
  dean: string;
  established: string;
  totalPrograms: number;
}

interface Building {
  id: string;
  code: string;
  name: string;
  location: string;
  floors: number;
  totalRooms: number;
}

interface Room {
  id: string;
  code: string;
  name: string;
  building: string;
  floor: number;
  capacity: number;
  type: 'Lecture Hall' | 'Laboratory' | 'Office' | 'Meeting Room';
}

const MasterData: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'faculties' | 'buildings' | 'rooms'>('faculties');
  const [searchTerm, setSearchTerm] = useState('');

  // Mock data
  const faculties: Faculty[] = [
    {
      id: '1',
      code: 'FTI',
      name: 'Fakultas Teknologi Informasi',
      dean: 'Prof. Dr. John Doe',
      established: '2010',
      totalPrograms: 4
    },
    {
      id: '2',
      code: 'FEB',
      name: 'Fakultas Ekonomi dan Bisnis',
      dean: 'Prof. Dr. Jane Smith',
      established: '2008',
      totalPrograms: 3
    },
    {
      id: '3',
      code: 'FT',
      name: 'Fakultas Teknik',
      dean: 'Prof. Dr. Ahmad Rahman',
      established: '2005',
      totalPrograms: 5
    }
  ];

  const buildings: Building[] = [
    {
      id: '1',
      code: 'GD-A',
      name: 'Gedung A (Utama)',
      location: 'Kampus Utama',
      floors: 5,
      totalRooms: 25
    },
    {
      id: '2',
      code: 'GD-B',
      name: 'Gedung B (Laboratorium)',
      location: 'Kampus Utama',
      floors: 3,
      totalRooms: 15
    },
    {
      id: '3',
      code: 'GD-C',
      name: 'Gedung C (Administrasi)',
      location: 'Kampus Utama',
      floors: 4,
      totalRooms: 20
    }
  ];

  const rooms: Room[] = [
    {
      id: '1',
      code: 'A101',
      name: 'Ruang Kuliah A101',
      building: 'Gedung A',
      floor: 1,
      capacity: 50,
      type: 'Lecture Hall'
    },
    {
      id: '2',
      code: 'B201',
      name: 'Lab Komputer 1',
      building: 'Gedung B',
      floor: 2,
      capacity: 30,
      type: 'Laboratory'
    },
    {
      id: '3',
      code: 'A305',
      name: 'Ruang Kuliah A305',
      building: 'Gedung A',
      floor: 3,
      capacity: 40,
      type: 'Lecture Hall'
    }
  ];

  const tabs = [
    { id: 'faculties', label: 'Fakultas', icon: <GraduationCap className="h-4 w-4" /> },
    { id: 'buildings', label: 'Gedung', icon: <Building2 className="h-4 w-4" /> },
    { id: 'rooms', label: 'Ruang', icon: <MapPin className="h-4 w-4" /> }
  ];

  const renderFaculties = () => (
    <div className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {faculties.map((faculty) => (
          <div key={faculty.id} className="card p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <h3 className="font-semibold text-gray-900">{faculty.name}</h3>
                <p className="text-sm text-gray-500">Kode: {faculty.code}</p>
              </div>
              <div className="flex space-x-2">
                <button className="p-2 text-gray-400 hover:text-blue-600 transition-colors">
                  <Edit className="h-4 w-4" />
                </button>
                <button className="p-2 text-gray-400 hover:text-red-600 transition-colors">
                  <Trash2 className="h-4 w-4" />
                </button>
              </div>
            </div>
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-500">Dekan:</span>
                <span className="text-gray-900">{faculty.dean}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Didirikan:</span>
                <span className="text-gray-900">{faculty.established}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Program Studi:</span>
                <span className="text-gray-900">{faculty.totalPrograms}</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );

  const renderBuildings = () => (
    <div className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {buildings.map((building) => (
          <div key={building.id} className="card p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <h3 className="font-semibold text-gray-900">{building.name}</h3>
                <p className="text-sm text-gray-500">Kode: {building.code}</p>
              </div>
              <div className="flex space-x-2">
                <button className="p-2 text-gray-400 hover:text-blue-600 transition-colors">
                  <Edit className="h-4 w-4" />
                </button>
                <button className="p-2 text-gray-400 hover:text-red-600 transition-colors">
                  <Trash2 className="h-4 w-4" />
                </button>
              </div>
            </div>
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-500">Lokasi:</span>
                <span className="text-gray-900">{building.location}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Lantai:</span>
                <span className="text-gray-900">{building.floors}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-500">Total Ruang:</span>
                <span className="text-gray-900">{building.totalRooms}</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );

  const renderRooms = () => (
    <div className="space-y-4">
      <div className="overflow-x-auto">
        <table className="min-w-full bg-white border border-gray-200 rounded-lg">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Kode</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Nama Ruang</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Gedung</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Lantai</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Kapasitas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Tipe</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Aksi</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {rooms.map((room) => (
              <tr key={room.id} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{room.code}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{room.name}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{room.building}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{room.floor}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{room.capacity}</td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                    room.type === 'Lecture Hall' ? 'bg-blue-100 text-blue-800' :
                    room.type === 'Laboratory' ? 'bg-green-100 text-green-800' :
                    room.type === 'Office' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-purple-100 text-purple-800'
                  }`}>
                    {room.type}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                  <div className="flex space-x-2">
                    <button className="text-blue-600 hover:text-blue-900">
                      <Edit className="h-4 w-4" />
                    </button>
                    <button className="text-red-600 hover:text-red-900">
                      <Trash2 className="h-4 w-4" />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <Database className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Master Data</h1>
            <p className="text-gray-600">Kelola data master sistem akademik</p>
          </div>
        </div>
        <button className="btn-primary flex items-center space-x-2">
          <Plus className="h-4 w-4" />
          <span>Tambah Data</span>
        </button>
      </div>

      {/* Tabs */}
      <div className="border-b border-gray-200">
        <nav className="-mb-px flex space-x-8">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`flex items-center space-x-2 py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === tab.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {tab.icon}
              <span>{tab.label}</span>
            </button>
          ))}
        </nav>
      </div>

      {/* Search and Filter */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-4 sm:space-y-0">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <input
            type="text"
            placeholder="Cari data..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="input-field pl-10 w-full sm:w-80"
          />
        </div>
        <button className="btn-secondary flex items-center space-x-2">
          <Filter className="h-4 w-4" />
          <span>Filter</span>
        </button>
      </div>

      {/* Content */}
      {activeTab === 'faculties' && renderFaculties()}
      {activeTab === 'buildings' && renderBuildings()}
      {activeTab === 'rooms' && renderRooms()}
    </div>
  );
};

export default MasterData;