import React from 'react';
import { Briefcase, Users, Calendar, FileText, Settings as SettingsIcon, Bell, TrendingUp } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';

const StaffDashboard: React.FC = () => {
  const { user } = useAuth();

  const quickStats = [
    { label: 'Active Tasks', value: '12', icon: <Briefcase className="h-6 w-6 text-blue-600" />, color: 'bg-blue-100' },
    { label: 'Departments', value: '5', icon: <Users className="h-6 w-6 text-green-600" />, color: 'bg-green-100' },
    { label: 'Scheduled Meetings', value: '8', icon: <Calendar className="h-6 w-6 text-purple-600" />, color: 'bg-purple-100' },
    { label: 'Pending Documents', value: '15', icon: <FileText className="h-6 w-6 text-orange-600" />, color: 'bg-orange-100' },
  ];

  const recentActivities = [
    { action: 'Document approved', detail: 'Budget proposal Q4 2024', time: '10 minutes ago' },
    { action: 'Meeting scheduled', detail: 'Department heads meeting', time: '1 hour ago' },
    { action: 'Task completed', detail: 'Inventory check - IT equipment', time: '2 hours ago' },
    { action: 'Report submitted', detail: 'Monthly operations report', time: '5 hours ago' },
  ];

  const upcomingTasks = [
    { title: 'Review staff evaluations', dueDate: 'Today, 3:00 PM', priority: 'High' },
    { title: 'Prepare quarterly budget report', dueDate: 'Tomorrow', priority: 'Medium' },
    { title: 'Coordinate facility maintenance', dueDate: 'Oct 10, 2025', priority: 'Low' },
    { title: 'Update staff directory', dueDate: 'Oct 12, 2025', priority: 'Medium' },
  ];

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'High':
        return 'bg-red-100 text-red-800';
      case 'Medium':
        return 'bg-yellow-100 text-yellow-800';
      case 'Low':
        return 'bg-green-100 text-green-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <div className="bg-gradient-to-r from-blue-600 to-blue-800 rounded-lg shadow-lg p-6 text-white">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold mb-2">Welcome back, {user?.name}!</h1>
            <p className="text-blue-100">Staff Portal - Manage administrative tasks and operations</p>
          </div>
          <div className="hidden md:block">
            <Briefcase className="h-16 w-16 text-blue-200 opacity-50" />
          </div>
        </div>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {quickStats.map((stat, index) => (
          <div key={index} className="card p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-600 mb-1">{stat.label}</p>
                <p className="text-3xl font-bold text-gray-900">{stat.value}</p>
              </div>
              <div className={`p-3 rounded-lg ${stat.color}`}>
                {stat.icon}
              </div>
            </div>
          </div>
        ))}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Upcoming Tasks */}
        <div className="lg:col-span-2 card p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Upcoming Tasks</h2>
            <button className="text-sm text-blue-600 hover:text-blue-800 cursor-pointer">View All</button>
          </div>
          <div className="space-y-3">
            {upcomingTasks.map((task, index) => (
              <div key={index} className="flex items-start justify-between p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors">
                <div className="flex-1">
                  <h3 className="font-medium text-gray-900">{task.title}</h3>
                  <p className="text-sm text-gray-600 mt-1">{task.dueDate}</p>
                </div>
                <span className={`px-2.5 py-0.5 rounded-full text-xs font-medium ${getPriorityColor(task.priority)}`}>
                  {task.priority}
                </span>
              </div>
            ))}
          </div>
        </div>

        {/* Recent Activities */}
        <div className="card p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Recent Activities</h2>
            <Bell className="h-5 w-5 text-gray-400" />
          </div>
          <div className="space-y-4">
            {recentActivities.map((activity, index) => (
              <div key={index} className="flex items-start space-x-3">
                <div className="flex-shrink-0 w-2 h-2 mt-2 bg-blue-600 rounded-full"></div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium text-gray-900">{activity.action}</p>
                  <p className="text-xs text-gray-600 mt-1">{activity.detail}</p>
                  <p className="text-xs text-gray-400 mt-1">{activity.time}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="card p-6">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <button className="p-4 bg-blue-50 hover:bg-blue-100 rounded-lg transition-colors cursor-pointer">
            <FileText className="h-8 w-8 text-blue-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Submit Report</p>
          </button>
          <button className="p-4 bg-green-50 hover:bg-green-100 rounded-lg transition-colors cursor-pointer">
            <Calendar className="h-8 w-8 text-green-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Schedule Meeting</p>
          </button>
          <button className="p-4 bg-purple-50 hover:bg-purple-100 rounded-lg transition-colors cursor-pointer">
            <TrendingUp className="h-8 w-8 text-purple-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">View Analytics</p>
          </button>
          <button className="p-4 bg-gray-50 hover:bg-gray-100 rounded-lg transition-colors cursor-pointer">
            <SettingsIcon className="h-8 w-8 text-gray-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Settings</p>
          </button>
        </div>
      </div>
    </div>
  );
};

export default StaffDashboard;
