import React, { useState } from 'react';
import { Plus, Edit, Trash2, Search, Filter, Calendar, MapPin, Users, AlertTriangle } from 'lucide-react';

interface ClassSchedule {
  id: string;
  courseCode: string;
  courseName: string;
  className: string;
  lecturer: string;
  room: string;
  day: string;
  startTime: string;
  endTime: string;
  capacity: number;
  enrolled: number;
  semester: string;
  program: string;
  status: 'Active' | 'Canceled' | 'Moved' | 'Full';
  conflicts?: string[];
}

interface TimeSlot {
  time: string;
  monday?: ClassSchedule;
  tuesday?: ClassSchedule;
  wednesday?: ClassSchedule;
  thursday?: ClassSchedule;
  friday?: ClassSchedule;
  saturday?: ClassSchedule;
}

const ClassScheduling: React.FC = () => {
  const [viewMode, setViewMode] = useState<'grid' | 'table'>('grid');
  const [selectedRoom, setSelectedRoom] = useState<string>('all');
  const [selectedProgram, setSelectedProgram] = useState<string>('all');
  const [searchTerm, setSearchTerm] = useState('');

  // Mock data
  const schedules: ClassSchedule[] = [
    {
      id: '1',
      courseCode: 'IF101',
      courseName: 'Algoritma dan Pemrograman',
      className: 'IF101-A',
      lecturer: 'Dr. Ahmad Fauzi, M.Kom',
      room: 'A101',
      day: 'monday',
      startTime: '08:00',
      endTime: '10:30',
      capacity: 40,
      enrolled: 38,
      semester: '20241',
      program: 'Teknik Informatika',
      status: 'Full'
    },
    {
      id: '2',
      courseCode: 'IF102',
      courseName: 'Matematika Diskrit',
      className: 'IF102-A',
      lecturer: 'Dr. Siti Nurhaliza, M.T',
      room: 'A102',
      day: 'monday',
      startTime: '10:40',
      endTime: '13:10',
      capacity: 35,
      enrolled: 32,
      semester: '20241',
      program: 'Teknik Informatika',
      status: 'Active'
    },
    {
      id: '3',
      courseCode: 'SI201',
      courseName: 'Basis Data',
      className: 'SI201-A',
      lecturer: 'Prof. Dr. Budi Santoso',
      room: 'B201',
      day: 'tuesday',
      startTime: '08:00',
      endTime: '10:30',
      capacity: 30,
      enrolled: 28,
      semester: '20241',
      program: 'Sistem Informasi',
      status: 'Active'
    },
    {
      id: '4',
      courseCode: 'IF201',
      courseName: 'Struktur Data',
      className: 'IF201-A',
      lecturer: 'Dr. Ahmad Fauzi, M.Kom',
      room: 'A101',
      day: 'wednesday',
      startTime: '08:00',
      endTime: '10:30',
      capacity: 40,
      enrolled: 35,
      semester: '20241',
      program: 'Teknik Informatika',
      status: 'Active'
    },
    {
      id: '5',
      courseCode: 'IF301',
      courseName: 'Pemrograman Web',
      className: 'IF301-B',
      lecturer: 'Dr. Rina Kartika, M.Kom',
      room: 'B301',
      day: 'thursday',
      startTime: '13:20',
      endTime: '15:50',
      capacity: 25,
      enrolled: 0,
      semester: '20241',
      program: 'Teknik Informatika',
      status: 'Canceled',
      conflicts: ['Ruang tidak tersedia']
    }
  ];

  const rooms = ['A101', 'A102', 'A103', 'B201', 'B202', 'B301'];
  const programs = ['Teknik Informatika', 'Sistem Informasi', 'Manajemen'];
  
  const timeSlots = [
    '08:00 - 10:30',
    '10:40 - 13:10',
    '13:20 - 15:50',
    '16:00 - 18:30',
    '19:00 - 21:30'
  ];

  const days = [
    { key: 'monday', label: 'Senin' },
    { key: 'tuesday', label: 'Selasa' },
    { key: 'wednesday', label: 'Rabu' },
    { key: 'thursday', label: 'Kamis' },
    { key: 'friday', label: 'Jumat' },
    { key: 'saturday', label: 'Sabtu' }
  ];

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Active':
        return 'bg-green-100 text-green-800';
      case 'Full':
        return 'bg-blue-100 text-blue-800';
      case 'Canceled':
        return 'bg-red-100 text-red-800';
      case 'Moved':
        return 'bg-yellow-100 text-yellow-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getCapacityColor = (enrolled: number, capacity: number) => {
    const percentage = (enrolled / capacity) * 100;
    if (percentage >= 100) return 'text-red-600';
    if (percentage >= 80) return 'text-yellow-600';
    return 'text-green-600';
  };

  const filteredSchedules = schedules.filter(schedule => {
    const matchesSearch = schedule.courseName.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         schedule.courseCode.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         schedule.lecturer.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesRoom = selectedRoom === 'all' || schedule.room === selectedRoom;
    const matchesProgram = selectedProgram === 'all' || schedule.program === selectedProgram;
    
    return matchesSearch && matchesRoom && matchesProgram;
  });

  // Create time grid for grid view
  const createTimeGrid = (): TimeSlot[] => {
    return timeSlots.map(time => {
      const slot: TimeSlot = { time };
      filteredSchedules.forEach(schedule => {
        const scheduleTime = `${schedule.startTime} - ${schedule.endTime}`;
        if (scheduleTime === time) {
          slot[schedule.day as keyof Omit<TimeSlot, 'time'>] = schedule;
        }
      });
      return slot;
    });
  };

  const renderGridView = () => {
    const timeGrid = createTimeGrid();
    
    return (
      <div className="card overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-32">
                  Waktu
                </th>
                {days.map((day) => (
                  <th key={day.key} className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    {day.label}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {timeGrid.map((slot, index) => (
                <tr key={index} className="hover:bg-gray-50">
                  <td className="px-4 py-3 text-sm font-medium text-gray-900 bg-gray-50">
                    {slot.time}
                  </td>
                  {days.map((day) => {
                    const schedule = slot[day.key as keyof Omit<TimeSlot, 'time'>];
                    return (
                      <td key={day.key} className="px-4 py-3 text-sm">
                        {schedule ? (
                          <div className={`p-2 rounded-lg border-l-4 ${
                            schedule.status === 'Active' ? 'border-green-500 bg-green-50' :
                            schedule.status === 'Full' ? 'border-blue-500 bg-blue-50' :
                            schedule.status === 'Canceled' ? 'border-red-500 bg-red-50' :
                            'border-yellow-500 bg-yellow-50'
                          }`}>
                            <div className="font-medium text-gray-900 text-xs mb-1">
                              {schedule.className}
                            </div>
                            <div className="text-xs text-gray-600 mb-1">
                              {schedule.courseName}
                            </div>
                            <div className="flex items-center space-x-2 text-xs text-gray-500">
                              <div className="flex items-center space-x-1">
                                <MapPin className="h-3 w-3" />
                                <span>{schedule.room}</span>
                              </div>
                              <div className="flex items-center space-x-1">
                                <Users className="h-3 w-3" />
                                <span className={getCapacityColor(schedule.enrolled, schedule.capacity)}>
                                  {schedule.enrolled}/{schedule.capacity}
                                </span>
                              </div>
                            </div>
                            {schedule.conflicts && (
                              <div className="flex items-center space-x-1 mt-1">
                                <AlertTriangle className="h-3 w-3 text-red-500" />
                                <span className="text-xs text-red-600">Konflik</span>
                              </div>
                            )}
                          </div>
                        ) : (
                          <div className="h-16 border-2 border-dashed border-gray-200 rounded-lg flex items-center justify-center">
                            <span className="text-xs text-gray-400">Kosong</span>
                          </div>
                        )}
                      </td>
                    );
                  })}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  };

  const renderTableView = () => (
    <div className="card overflow-hidden">
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Mata Kuliah
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Dosen
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Jadwal
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Ruang
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Kapasitas
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Aksi
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredSchedules.map((schedule) => {
              const dayLabel = days.find(d => d.key === schedule.day)?.label || schedule.day;
              return (
                <tr key={schedule.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div>
                      <div className="text-sm font-medium text-gray-900">{schedule.className}</div>
                      <div className="text-sm text-gray-500">{schedule.courseName}</div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {schedule.lecturer}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{dayLabel}</div>
                    <div className="text-sm text-gray-500">{schedule.startTime} - {schedule.endTime}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center space-x-1">
                      <MapPin className="h-4 w-4 text-gray-400" />
                      <span className="text-sm text-gray-900">{schedule.room}</span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center space-x-1">
                      <Users className="h-4 w-4 text-gray-400" />
                      <span className={`text-sm font-medium ${getCapacityColor(schedule.enrolled, schedule.capacity)}`}>
                        {schedule.enrolled}/{schedule.capacity}
                      </span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex flex-col space-y-1">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(schedule.status)}`}>
                        {schedule.status}
                      </span>
                      {schedule.conflicts && (
                        <div className="flex items-center space-x-1">
                          <AlertTriangle className="h-3 w-3 text-red-500" />
                          <span className="text-xs text-red-600">Konflik</span>
                        </div>
                      )}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <div className="flex space-x-2">
                      <button className="text-blue-600 hover:text-blue-900">
                        <Edit className="h-4 w-4" />
                      </button>
                      <button className="text-red-600 hover:text-red-900">
                        <Trash2 className="h-4 w-4" />
                      </button>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <Calendar className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Penjadwalan Kelas</h1>
            <p className="text-gray-600">Kelola jadwal kuliah dan ruang kelas</p>
          </div>
        </div>
        <button className="btn-primary flex items-center space-x-2">
          <Plus className="h-4 w-4" />
          <span>Tambah Jadwal</span>
        </button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Calendar className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Total Kelas</p>
              <p className="text-2xl font-semibold text-gray-900">{schedules.length}</p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <Users className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Kelas Aktif</p>
              <p className="text-2xl font-semibold text-gray-900">
                {schedules.filter(s => s.status === 'Active').length}
              </p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-red-100 rounded-lg">
              <AlertTriangle className="h-6 w-6 text-red-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Konflik</p>
              <p className="text-2xl font-semibold text-gray-900">
                {schedules.filter(s => s.conflicts && s.conflicts.length > 0).length}
              </p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <MapPin className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Ruang Terpakai</p>
              <p className="text-2xl font-semibold text-gray-900">
                {new Set(schedules.filter(s => s.status === 'Active').map(s => s.room)).size}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* View Toggle and Filters */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0 lg:space-x-4">
        <div className="flex items-center space-x-4">
          <div className="flex bg-gray-100 rounded-lg p-1">
            <button
              onClick={() => setViewMode('grid')}
              className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                viewMode === 'grid'
                  ? 'bg-white text-gray-900 shadow-sm'
                  : 'text-gray-500 hover:text-gray-700'
              }`}
            >
              Grid
            </button>
            <button
              onClick={() => setViewMode('table')}
              className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                viewMode === 'table'
                  ? 'bg-white text-gray-900 shadow-sm'
                  : 'text-gray-500 hover:text-gray-700'
              }`}
            >
              Tabel
            </button>
          </div>
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
            <input
              type="text"
              placeholder="Cari jadwal..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="input-field pl-10 w-full lg:w-80"
            />
          </div>
        </div>
        <div className="flex space-x-4">
          <select
            value={selectedRoom}
            onChange={(e) => setSelectedRoom(e.target.value)}
            className="input-field"
          >
            <option value="all">Semua Ruang</option>
            {rooms.map((room) => (
              <option key={room} value={room}>{room}</option>
            ))}
          </select>
          <select
            value={selectedProgram}
            onChange={(e) => setSelectedProgram(e.target.value)}
            className="input-field"
          >
            <option value="all">Semua Program</option>
            {programs.map((program) => (
              <option key={program} value={program}>{program}</option>
            ))}
          </select>
          <button className="btn-secondary flex items-center space-x-2">
            <Filter className="h-4 w-4" />
            <span>Filter</span>
          </button>
        </div>
      </div>

      {/* Content */}
      {viewMode === 'grid' ? renderGridView() : renderTableView()}

      {filteredSchedules.length === 0 && (
        <div className="text-center py-12">
          <Calendar className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">Tidak ada jadwal</h3>
          <p className="mt-1 text-sm text-gray-500">Tidak ditemukan jadwal yang sesuai dengan filter.</p>
        </div>
      )}
    </div>
  );
};

export default ClassScheduling;