import React from 'react';
import { NavLink } from 'react-router-dom';
import {
  Home,
  BookOpen,
  Calendar,
  GraduationCap,
  Activity,
  Users,
  Settings,
  BarChart3,
  FileText,
  UserCog,
  School,
  Clock,
  Award,
  Database,
  UserPlus,
  ChevronLeft,
  ChevronRight,
  X,
} from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import { useSidebar } from '../../contexts/SidebarContext';
import { Environment } from '../../utils/environment';
import type { UserRole } from '../../types';

interface NavItem {
  icon: React.ReactNode;
  label: string;
  href: string;
  roles: UserRole[];
  isEnabled: boolean;
}

const navigationItems: NavItem[] = [
  // Student Navigation
  { icon: <BookOpen className="h-5 w-5" />, label: 'KRS Management', href: '/student/krs', roles: ['student'], isEnabled: Environment.pages.studentKRS },
  { icon: <Calendar className="h-5 w-5" />, label: 'Class & Schedule', href: '/student/schedule', roles: ['student'], isEnabled: Environment.pages.studentSchedule },
  { icon: <GraduationCap className="h-5 w-5" />, label: 'Grades (KHS)', href: '/student/grades', roles: ['student'], isEnabled: Environment.pages.studentGrades },
  { icon: <FileText className="h-5 w-5" />, label: 'Transcript', href: '/student/transcript', roles: ['student'], isEnabled: Environment.pages.studentTranscript },
  { icon: <Activity className="h-5 w-5" />, label: 'MBKM Activities', href: '/student/activities', roles: ['student'], isEnabled: Environment.pages.studentActivities },
  
  // Lecturer Navigation
  { icon: <Calendar className="h-5 w-5" />, label: 'Class Schedule', href: '/lecturer/schedule', roles: ['lecturer'], isEnabled: Environment.pages.lecturerSchedule },
  { icon: <Award className="h-5 w-5" />, label: 'Grade Input', href: '/lecturer/grade-input', roles: ['lecturer'], isEnabled: Environment.pages.lecturerGradeInput },
  { icon: <BarChart3 className="h-5 w-5" />, label: 'Student Performance', href: '/lecturer/performance', roles: ['lecturer'], isEnabled: Environment.pages.lecturerPerformance },
  { icon: <UserCog className="h-5 w-5" />, label: 'Academic Advisor', href: '/lecturer/advisor', roles: ['lecturer'], isEnabled: Environment.pages.lecturerAdvisor },
  
  // Admin Navigation
  { icon: <Users className="h-5 w-5" />, label: 'Class Management', href: '/admin/classes', roles: ['admin'], isEnabled: Environment.pages.adminClasses },
  { icon: <Database className="h-5 w-5" />, label: 'Master Data', href: '/admin/master-data', roles: ['admin'], isEnabled: Environment.pages.adminMasterData },
  { icon: <School className="h-5 w-5" />, label: 'Programs (Prodi)', href: '/admin/programs', roles: ['admin'], isEnabled: Environment.pages.adminPrograms },
  { icon: <BookOpen className="h-5 w-5" />, label: 'Curriculum', href: '/admin/curriculum', roles: ['admin'], isEnabled: Environment.pages.adminCurriculum },
  { icon: <Users className="h-5 w-5" />, label: 'User Management', href: '/admin/users', roles: ['admin'], isEnabled: Environment.pages.adminUsers },
  { icon: <UserPlus className="h-5 w-5" />, label: 'Role Requests', href: '/admin/role-requests', roles: ['admin'], isEnabled: Environment.pages.adminRoleRequests },
  { icon: <Clock className="h-5 w-5" />, label: 'Semester Setup', href: '/admin/semester', roles: ['admin'], isEnabled: Environment.pages.adminSemester },
  { icon: <Calendar className="h-5 w-5" />, label: 'Class Scheduling', href: '/admin/scheduling', roles: ['admin'], isEnabled: Environment.pages.adminScheduling },
  { icon: <FileText className="h-5 w-5" />, label: 'Reports', href: '/admin/reports', roles: ['admin'], isEnabled: Environment.pages.adminReports },
  
  // Guest Navigation
  { icon: <UserPlus className="h-5 w-5" />, label: 'Role Management', href: '/guest/role-management', roles: ['guest'], isEnabled: Environment.pages.guestRoleManagement },
  
  // Role-specific Profile/Settings (Academic/Professional configurations)
  { icon: <UserCog className="h-5 w-5" />, label: 'Academic Profile', href: '/student/profile', roles: ['student'], isEnabled: Environment.pages.studentProfile },
  { icon: <Settings className="h-5 w-5" />, label: 'Academic Settings', href: '/student/settings', roles: ['student'], isEnabled: Environment.pages.studentSettings },
  { icon: <UserCog className="h-5 w-5" />, label: 'Lecturer Profile', href: '/lecturer/profile', roles: ['lecturer'], isEnabled: Environment.pages.lecturerProfile },
  { icon: <Settings className="h-5 w-5" />, label: 'Lecturer Settings', href: '/lecturer/settings', roles: ['lecturer'], isEnabled: Environment.pages.lecturerSettings },
  { icon: <UserCog className="h-5 w-5" />, label: 'Admin Profile', href: '/admin/profile', roles: ['admin'], isEnabled: Environment.pages.adminProfile },
  { icon: <Settings className="h-5 w-5" />, label: 'Admin Settings', href: '/admin/settings', roles: ['admin'], isEnabled: Environment.pages.adminSettings },
  { icon: <UserCog className="h-5 w-5" />, label: 'Guest Profile', href: '/guest/profile', roles: ['guest'], isEnabled: Environment.pages.guestProfile },
];

const Sidebar: React.FC = () => {
  const { user } = useAuth();
  const { isCollapsed, toggleSidebar, isMobile } = useSidebar();
  const userRole = user?.role || 'guest';

  // Generate role-first dashboard link
  const dashboardHref = `/${userRole}/dashboard`;

  const filteredItems = navigationItems.filter(item => 
    item.roles.includes(userRole) && item.isEnabled
  );

  const getRoleTitle = (role: UserRole): string => {
    switch (role) {
      case 'student':
        return 'Student Portal';
      case 'lecturer':
        return 'Lecturer Portal';
      case 'admin':
        return 'Admin Portal';
      case 'guest':
        return 'Guest Portal';
      case 'staff':
        return 'Staff Portal';
      case 'student_applicant':
        return 'Applicant Portal';
      case 'alumni':
        return 'Alumni Portal';
      default:
        return 'Portal';
    }
  };

  const sidebarContent = (
    <>
      {/* Logo/Brand with Toggle Button */}
      <div className="flex items-center justify-between px-6 py-4 border-b border-gray-200">
        {!isCollapsed && (
          <div className="flex items-center space-x-3 flex-1">
            <img 
              src={`${import.meta.env.BASE_URL}logo.png`}
              alt="Institution Logo" 
              className="h-10 w-10 object-contain"
            />
            <div>
              <div className="font-semibold text-gray-900 text-sm">ADB-AS</div>
              <div className="text-xs text-gray-500">{getRoleTitle(userRole)}</div>
            </div>
          </div>
        )}
        {!isMobile && (
          <button
            onClick={toggleSidebar}
            className="flex items-center justify-center p-2 hover:bg-gray-100 rounded-lg transition-colors"
            aria-label={isCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
          >
            {isCollapsed ? (
              <ChevronRight className="h-5 w-5 text-gray-600" />
            ) : (
              <ChevronLeft className="h-5 w-5 text-gray-600" />
            )}
          </button>
        )}
        {isMobile && (
          <button
            onClick={toggleSidebar}
            className="flex items-center justify-center p-2 hover:bg-gray-100 rounded-lg transition-colors"
            aria-label="Close sidebar"
          >
            <X className="h-5 w-5 text-gray-600" />
          </button>
        )}
      </div>

      {/* Navigation */}
      <nav className="flex-1 px-4 py-6 space-y-1 overflow-y-auto">
        {/* Dashboard - always first */}
        <NavLink
          to={dashboardHref}
          title={isCollapsed ? 'Dashboard' : ''}
          onClick={() => isMobile && toggleSidebar()}
          className={({ isActive }) =>
            `flex items-center ${isCollapsed ? 'justify-center' : 'space-x-3'} px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
              isActive
                ? 'bg-primary-50 text-primary-700 border-r-2 border-primary-600'
                : 'text-gray-700 hover:bg-gray-100 hover:text-gray-900'
            }`
          }
        >
          <Home className="h-5 w-5 flex-shrink-0" />
          {!isCollapsed && <span>Dashboard</span>}
        </NavLink>
        
        {/* Other navigation items */}
        {filteredItems.map((item, index) => (
          <NavLink
            key={index}
            to={item.href}
            title={isCollapsed ? item.label : ''}
            onClick={() => isMobile && toggleSidebar()}
            className={({ isActive }) =>
              `flex items-center ${isCollapsed ? 'justify-center' : 'space-x-3'} px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                isActive
                  ? 'bg-primary-50 text-primary-700 border-r-2 border-primary-600'
                  : 'text-gray-700 hover:bg-gray-100 hover:text-gray-900'
              }`
            }
          >
            <span className="flex-shrink-0">{item.icon}</span>
            {!isCollapsed && <span>{item.label}</span>}
          </NavLink>
        ))}
      </nav>

      {/* Footer */}
      <div className="px-6 py-4 border-t border-gray-200">
        <div className="text-xs text-gray-500">
          Academic Year 2024/2025
        </div>
      </div>
    </>
  );

  // Mobile: Show as overlay when expanded
  if (isMobile) {
    return (
      <>
        {/* Mobile Sidebar - always rendered, slides in/out */}
        <aside
          className={`fixed left-0 top-0 h-full w-64 bg-white border-r border-gray-200 z-50 transition-transform duration-300 ease-in-out lg:hidden ${
            isCollapsed ? '-translate-x-full' : 'translate-x-0'
          }`}
        >
          <div className="flex flex-col h-full pt-16">
            {sidebarContent}
          </div>
        </aside>
      </>
    );
  }

  // Desktop: Show side-by-side
  return (
    <aside className={`fixed left-0 top-0 h-full bg-white border-r border-gray-200 z-50 transition-all duration-300 ease-in-out hidden lg:flex lg:flex-col ${
      isCollapsed ? 'w-20' : 'w-64'
    }`}>
      <div className="flex flex-col h-full">
        {sidebarContent}
      </div>
    </aside>
  );
};

export default Sidebar;