/**
 * Utility functions for handling SQL Null types from Go backend
 */

export interface SqlNullString {
  String: string;
  Valid: boolean;
}

export interface SqlNullInt64 {
  Int64: number;
  Valid: boolean;
}

export interface SqlNullTime {
  Time: string;
  Valid: boolean;
}

/**
 * Extract string value from SQL NullString or plain string
 * @param value - Can be SqlNullString, string, or null/undefined
 * @param fallback - Default value if null or invalid
 * @returns Extracted string or fallback
 */
export const extractString = (value: SqlNullString | string | null | undefined, fallback: string = ''): string => {
  if (!value) return fallback;
  
  // If it's a SQL NullString object with {String: "...", Valid: true/false}
  if (typeof value === 'object' && 'String' in value && 'Valid' in value) {
    return value.Valid ? (value.String || fallback) : fallback;
  }
  
  // If it's already a plain string
  if (typeof value === 'string') {
    return value;
  }
  
  return fallback;
};

/**
 * Extract number value from SQL NullInt64 or plain number
 * @param value - Can be SqlNullInt64, number, or null/undefined
 * @param fallback - Default value if null or invalid
 * @returns Extracted number or fallback
 */
export const extractNumber = (value: SqlNullInt64 | number | null | undefined, fallback: number = 0): number => {
  if (value === null || value === undefined) return fallback;
  
  // If it's a SQL NullInt64 object
  if (typeof value === 'object' && 'Int64' in value && 'Valid' in value) {
    return value.Valid ? (value.Int64 ?? fallback) : fallback;
  }
  
  // If it's already a plain number
  if (typeof value === 'number') {
    return value;
  }
  
  return fallback;
};

/**
 * Extract Date from SQL NullTime or plain string/Date
 * @param value - Can be SqlNullTime, string, Date, or null/undefined
 * @param fallback - Default value if null or invalid
 * @returns Extracted Date or fallback
 */
export const extractDate = (value: SqlNullTime | string | Date | null | undefined, fallback?: Date): Date | null => {
  if (!value) return fallback || null;
  
  // If it's a SQL NullTime object
  if (typeof value === 'object' && 'Time' in value && 'Valid' in value) {
    return value.Valid ? new Date(value.Time) : (fallback || null);
  }
  
  // If it's a Date object
  if (value instanceof Date) {
    return value;
  }
  
  // If it's a string
  if (typeof value === 'string') {
    return new Date(value);
  }
  
  return fallback || null;
};

/**
 * Check if a SQL Null value is valid (not null)
 * @param value - Any SQL Null type
 * @returns True if value is valid (not null)
 */
export const isValidSqlNull = (value: any): boolean => {
  if (!value) return false;
  if (typeof value === 'object' && 'Valid' in value) {
    return value.Valid === true;
  }
  return true; // If it's not a SQL Null type, consider it valid
};
