import React from 'react';
import { Award, TrendingUp } from 'lucide-react';

interface CourseGrade {
  id: string;
  code: string;
  name: string;
  credits: number;
  letter: 'A' | 'A-' | 'B+' | 'B' | 'B-' | 'C+' | 'C' | 'C-' | 'D+' | 'D' | 'E';
  gpa: number; // numeric value for calculation
}

const mockGrades: CourseGrade[] = [
  { id: '1', code: 'CS101', name: 'Introduction to Programming', credits: 3, letter: 'A', gpa: 4.0 },
  { id: '2', code: 'CS201', name: 'Data Structures', credits: 3, letter: 'B+', gpa: 3.3 },
  { id: '3', code: 'CS301', name: 'Database Systems', credits: 3, letter: 'A-', gpa: 3.7 },
];

const totalCredits = mockGrades.reduce((s, c) => s + c.credits, 0);
const weighted = mockGrades.reduce((s, c) => s + c.credits * c.gpa, 0);
const IPS = totalCredits ? (weighted / totalCredits) : 0; // Semester GPA

const GradesKHS: React.FC = () => {
  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Grades (KHS)</h1>
        <div className="text-sm text-gray-500">Semester: Ganjil 2024/2025</div>
      </div>

      {/* Summary */}
      <div className="card grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="flex items-center space-x-3">
          <Award className="h-5 w-5 text-primary-600" />
          <div>
            <div className="text-sm text-gray-500">Total Credits</div>
            <div className="text-lg font-semibold">{totalCredits} SKS</div>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <TrendingUp className="h-5 w-5 text-green-600" />
          <div>
            <div className="text-sm text-gray-500">Semester GPA (IPS)</div>
            <div className="text-lg font-semibold">{IPS.toFixed(2)}</div>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <Award className="h-5 w-5 text-amber-600" />
          <div>
            <div className="text-sm text-gray-500">Status</div>
            <div className="text-lg font-semibold">Good Standing</div>
          </div>
        </div>
      </div>

      {/* Table */}
      <div className="card overflow-x-auto">
        <table className="min-w-full text-sm">
          <thead>
            <tr className="text-left text-gray-600">
              <th className="py-2 pr-4">Code</th>
              <th className="py-2 pr-4">Course</th>
              <th className="py-2 pr-4">SKS</th>
              <th className="py-2 pr-4">Grade</th>
              <th className="py-2 pr-4">GPA</th>
            </tr>
          </thead>
          <tbody>
            {mockGrades.map((g) => (
              <tr key={g.id} className="border-t border-gray-100">
                <td className="py-2 pr-4 font-medium text-gray-900">{g.code}</td>
                <td className="py-2 pr-4">{g.name}</td>
                <td className="py-2 pr-4">{g.credits}</td>
                <td className="py-2 pr-4 font-medium">{g.letter}</td>
                <td className="py-2 pr-4">{g.gpa.toFixed(1)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default GradesKHS;
