import React from 'react';
import { FileText, CheckCircle, Clock, AlertCircle, Send, Calendar, BookOpen, Award } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';

const ApplicantDashboard: React.FC = () => {
  const { user } = useAuth();

  const applicationStatus = {
    step: 3,
    totalSteps: 5,
    status: 'In Review',
    statusColor: 'text-blue-600',
    bgColor: 'bg-blue-50',
  };

  const applicationSteps = [
    { label: 'Personal Information', completed: true, icon: <CheckCircle className="h-5 w-5" /> },
    { label: 'Academic Records', completed: true, icon: <CheckCircle className="h-5 w-5" /> },
    { label: 'Document Upload', completed: true, icon: <CheckCircle className="h-5 w-5" /> },
    { label: 'Verification', completed: false, current: true, icon: <Clock className="h-5 w-5" /> },
    { label: 'Final Decision', completed: false, icon: <FileText className="h-5 w-5" /> },
  ];

  const requiredDocuments = [
    { name: 'High School Diploma', status: 'Uploaded', verified: true },
    { name: 'Transcript', status: 'Uploaded', verified: true },
    { name: 'ID Card/Passport', status: 'Uploaded', verified: false },
    { name: 'Photo 4x6', status: 'Uploaded', verified: true },
    { name: 'Health Certificate', status: 'Pending', verified: false },
  ];

  const importantDates = [
    { event: 'Document Verification Deadline', date: 'Oct 15, 2025', icon: <AlertCircle className="h-5 w-5 text-red-600" /> },
    { event: 'Interview Schedule (If Required)', date: 'Oct 20, 2025', icon: <Calendar className="h-5 w-5 text-blue-600" /> },
    { event: 'Final Decision Announcement', date: 'Nov 1, 2025', icon: <Award className="h-5 w-5 text-green-600" /> },
  ];

  const selectedProgram = {
    name: 'Teknik Informatika',
    faculty: 'Fakultas Teknologi Informasi',
    degree: 'S1 - Sarjana',
    quota: '100 seats',
    applicants: '450 applicants',
  };

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <div className="bg-gradient-to-r from-green-600 to-green-800 rounded-lg shadow-lg p-6 text-white">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold mb-2">Welcome, {user?.name}!</h1>
            <p className="text-green-100">Applicant Portal - Track your application progress</p>
          </div>
          <div className="hidden md:block">
            <BookOpen className="h-16 w-16 text-green-200 opacity-50" />
          </div>
        </div>
      </div>

      {/* Application Status */}
      <div className="card p-6">
        <div className="flex items-center justify-between mb-6">
          <div>
            <h2 className="text-lg font-semibold text-gray-900">Application Status</h2>
            <p className="text-sm text-gray-600 mt-1">Application ID: APP2024{user?.id?.substring(0, 6)}</p>
          </div>
          <div className={`px-4 py-2 ${applicationStatus.bgColor} rounded-lg`}>
            <span className={`font-semibold ${applicationStatus.statusColor}`}>{applicationStatus.status}</span>
          </div>
        </div>

        {/* Progress Steps */}
        <div className="relative">
          <div className="flex items-center justify-between">
            {applicationSteps.map((step, index) => (
              <div key={index} className="flex flex-col items-center flex-1">
                <div
                  className={`flex items-center justify-center w-12 h-12 rounded-full border-2 ${
                    step.completed
                      ? 'bg-green-600 border-green-600 text-white'
                      : step.current
                      ? 'bg-blue-600 border-blue-600 text-white'
                      : 'bg-white border-gray-300 text-gray-400'
                  }`}
                >
                  {step.icon}
                </div>
                <p className={`text-xs mt-2 text-center ${step.completed || step.current ? 'font-medium text-gray-900' : 'text-gray-500'}`}>
                  {step.label}
                </p>
              </div>
            ))}
          </div>
          <div className="absolute top-6 left-0 right-0 h-0.5 bg-gray-300 -z-10"></div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Selected Program */}
        <div className="card p-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Selected Program</h2>
          <div className="space-y-3">
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="font-semibold text-blue-900 text-lg">{selectedProgram.name}</h3>
              <p className="text-sm text-blue-700 mt-1">{selectedProgram.faculty}</p>
              <p className="text-sm text-blue-600 mt-2">{selectedProgram.degree}</p>
              <div className="flex items-center justify-between mt-4 pt-4 border-t border-blue-200">
                <div>
                  <p className="text-xs text-blue-600">Available Quota</p>
                  <p className="font-medium text-blue-900">{selectedProgram.quota}</p>
                </div>
                <div>
                  <p className="text-xs text-blue-600">Total Applicants</p>
                  <p className="font-medium text-blue-900">{selectedProgram.applicants}</p>
                </div>
              </div>
            </div>
            <button className="w-full btn-secondary cursor-pointer">
              Change Program Selection
            </button>
          </div>
        </div>

        {/* Document Status */}
        <div className="card p-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Document Status</h2>
          <div className="space-y-3">
            {requiredDocuments.map((doc, index) => (
              <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-3">
                  <FileText className="h-5 w-5 text-gray-400" />
                  <div>
                    <p className="text-sm font-medium text-gray-900">{doc.name}</p>
                    <p className="text-xs text-gray-600">{doc.status}</p>
                  </div>
                </div>
                {doc.verified ? (
                  <CheckCircle className="h-5 w-5 text-green-600" />
                ) : doc.status === 'Pending' ? (
                  <AlertCircle className="h-5 w-5 text-orange-600" />
                ) : (
                  <Clock className="h-5 w-5 text-blue-600" />
                )}
              </div>
            ))}
          </div>
          <button className="w-full btn-primary mt-4 cursor-pointer flex items-center justify-center space-x-2">
            <Send className="h-4 w-4" />
            <span>Upload Missing Documents</span>
          </button>
        </div>
      </div>

      {/* Important Dates */}
      <div className="card p-6">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Important Dates</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {importantDates.map((item, index) => (
            <div key={index} className="p-4 bg-gray-50 rounded-lg">
              <div className="flex items-start space-x-3">
                <div className="flex-shrink-0 mt-1">{item.icon}</div>
                <div className="flex-1">
                  <p className="text-sm font-medium text-gray-900">{item.event}</p>
                  <p className="text-sm text-gray-600 mt-1">{item.date}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Quick Actions */}
      <div className="card p-6">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <button className="p-4 bg-blue-50 hover:bg-blue-100 rounded-lg transition-colors cursor-pointer">
            <FileText className="h-8 w-8 text-blue-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">View Application</p>
          </button>
          <button className="p-4 bg-green-50 hover:bg-green-100 rounded-lg transition-colors cursor-pointer">
            <Send className="h-8 w-8 text-green-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Upload Documents</p>
          </button>
          <button className="p-4 bg-purple-50 hover:bg-purple-100 rounded-lg transition-colors cursor-pointer">
            <Calendar className="h-8 w-8 text-purple-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">Schedule Interview</p>
          </button>
          <button className="p-4 bg-orange-50 hover:bg-orange-100 rounded-lg transition-colors cursor-pointer">
            <BookOpen className="h-8 w-8 text-orange-600 mx-auto mb-2" />
            <p className="text-sm font-medium text-gray-900">FAQs</p>
          </button>
        </div>
      </div>
    </div>
  );
};

export default ApplicantDashboard;
