import React, { useMemo, useState } from 'react';
import { Save, FileUp, CheckCircle2 } from 'lucide-react';

type ComponentWeights = {
  attendance: number;
  assignment: number;
  midterm: number;
  final: number;
};

interface GradeRow {
  studentId: string;
  name: string;
  attendance?: number;
  assignment?: number;
  midterm?: number;
  final?: number;
}

const initialWeights: ComponentWeights = {
  attendance: 10,
  assignment: 30,
  midterm: 25,
  final: 35,
};

const GradeInput: React.FC = () => {
  const [selectedClass, setSelectedClass] = useState('CLS-101');
  const [weights, setWeights] = useState<ComponentWeights>(initialWeights);
  const [rows, setRows] = useState<GradeRow[]>(
    Array.from({ length: 10 }).map((_, i) => ({
      studentId: `22010${(i + 1).toString().padStart(3, '0')}`,
      name: `Student ${i + 1}`,
      attendance: 100,
      assignment: 80,
      midterm: 75,
      final: 85,
    }))
  );
  const [uploaded, setUploaded] = useState(false);

  const totalWeight = weights.attendance + weights.assignment + weights.midterm + weights.final;

  const totals = useMemo(() => {
    return rows.map(r => {
      const total =
        (r.attendance ?? 0) * (weights.attendance / 100) +
        (r.assignment ?? 0) * (weights.assignment / 100) +
        (r.midterm ?? 0) * (weights.midterm / 100) +
        (r.final ?? 0) * (weights.final / 100);
      return Math.round(total * 100) / 100;
    });
  }, [rows, weights]);

  const onUpload = () => {
    setUploaded(true);
    setTimeout(() => setUploaded(false), 2000);
  };

  const updateWeight = (key: keyof ComponentWeights, value: number) => {
    setWeights(prev => ({ ...prev, [key]: value }));
  };

  const updateRow = (idx: number, key: keyof GradeRow, value: number) => {
    setRows(prev => prev.map((r, i) => (i === idx ? { ...r, [key]: value } : r)));
  };

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Grade Input</h1>
        <div className="text-sm text-gray-500">Upload or input grades per class</div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="card lg:col-span-2">
          <div className="flex items-center justify-between mb-4">
            <div>
              <div className="font-semibold text-gray-900">Class</div>
              <div className="text-sm text-gray-500">Select a class to input grades</div>
            </div>
            <select
              className="border rounded-lg px-3 py-2"
              value={selectedClass}
              onChange={(e) => setSelectedClass(e.target.value)}
            >
              <option value="CLS-101">Database Systems (CS101)</option>
              <option value="CLS-202">Advanced Algorithms (CS202)</option>
            </select>
          </div>

          <div className="overflow-x-auto">
            <table className="min-w-full text-sm">
              <thead>
                <tr className="text-left text-gray-500">
                  <th className="py-2 pr-4">Student ID</th>
                  <th className="py-2 pr-4">Name</th>
                  <th className="py-2 pr-4">Attendance</th>
                  <th className="py-2 pr-4">Assignments</th>
                  <th className="py-2 pr-4">UTS</th>
                  <th className="py-2 pr-4">UAS</th>
                  <th className="py-2 pr-4">Total</th>
                </tr>
              </thead>
              <tbody>
                {rows.map((r, idx) => (
                  <tr className="border-t" key={r.studentId}>
                    <td className="py-2 pr-4">{r.studentId}</td>
                    <td className="py-2 pr-4">{r.name}</td>
                    {(['attendance', 'assignment', 'midterm', 'final'] as const).map((k) => (
                      <td key={k} className="py-2 pr-4">
                        <input
                          type="number"
                          min={0}
                          max={100}
                          value={r[k] ?? 0}
                          onChange={(e) => updateRow(idx, k, Number(e.target.value))}
                          className="w-24 border rounded px-2 py-1"
                        />
                      </td>
                    ))}
                    <td className="py-2 pr-4 font-medium text-gray-900">{totals[idx]}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <div className="mt-4 flex items-center space-x-3">
            <button className="btn btn-primary inline-flex items-center">
              <Save className="h-4 w-4 mr-2" /> Save Draft
            </button>
            <button className="btn inline-flex items-center">
              <CheckCircle2 className="h-4 w-4 mr-2" /> Submit Final Grades
            </button>
          </div>
        </div>

        <div className="space-y-6">
          <div className="card">
            <div className="font-semibold text-gray-900 mb-2">Upload from File</div>
            <p className="text-sm text-gray-600 mb-3">Upload CSV/XLSX template to fill grades in bulk.</p>
            <button onClick={onUpload} className="btn inline-flex items-center">
              <FileUp className="h-4 w-4 mr-2" /> Choose File
            </button>
            {uploaded && (
              <div className="mt-3 text-green-600 text-sm inline-flex items-center">
                <CheckCircle2 className="h-4 w-4 mr-1" /> File processed successfully
              </div>
            )}
          </div>

          <div className="card">
            <div className="font-semibold text-gray-900 mb-2">Evaluation Components</div>
            <div className="space-y-3">
              {(
                [
                  { key: 'attendance', label: 'Attendance' },
                  { key: 'assignment', label: 'Assignments' },
                  { key: 'midterm', label: 'UTS' },
                  { key: 'final', label: 'UAS' },
                ] as const
              ).map(({ key, label }) => (
                <div key={key} className="flex items-center justify-between">
                  <label className="text-sm text-gray-700">{label}</label>
                  <div className="flex items-center">
                    <input
                      type="number"
                      min={0}
                      max={100}
                      value={weights[key]}
                      onChange={(e) => updateWeight(key, Number(e.target.value))}
                      className="w-20 border rounded px-2 py-1 mr-2 text-right"
                    />
                    <span className="text-gray-500 text-sm">%</span>
                  </div>
                </div>
              ))}
              <div className="pt-2 text-sm text-gray-600">Total: <span className={totalWeight === 100 ? 'text-green-600 font-medium' : 'text-red-600 font-medium'}>{totalWeight}%</span></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GradeInput;
