import React, { useState, useEffect } from 'react';
import { Plus, Clock, CheckCircle, XCircle, FileText, User, Send, Search } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import type { UserRole, RoleRequest } from '../../types';

interface RoleRequestFormData {
  requestedRole: UserRole;
  reason: string;
}

interface FormErrors {
  requestedRole?: string;
  reason?: string;
}

const RoleManagement: React.FC = () => {
  const { user, submitRoleRequest, getRoleRequests } = useAuth();
  const [activeTab, setActiveTab] = useState<'request' | 'status'>('request');
  const [requests, setRequests] = useState<RoleRequest[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedStatus, setSelectedStatus] = useState<string>('all');
  
  // Form state
  const [formData, setFormData] = useState<RoleRequestFormData>({
    requestedRole: 'student',
    reason: ''
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  // Load role requests on component mount
  useEffect(() => {
    const loadRequests = async () => {
      try {
        const userRequests = await getRoleRequests();
        setRequests(userRequests);
      } catch (error) {
        console.error('Failed to load role requests:', error);
      }
    };
    loadRequests();
  }, [getRoleRequests]);

  const availableRoles: { value: UserRole; label: string; description: string }[] = [
    { 
      value: 'student_applicant', 
      label: 'Student Applicant', 
      description: 'Apply for admission to academic programs' 
    },
    { 
      value: 'student', 
      label: 'Student', 
      description: 'Access for enrolled students' 
    },
    { 
      value: 'lecturer', 
      label: 'Lecturer', 
      description: 'Faculty members and teaching staff' 
    },
    { 
      value: 'staff', 
      label: 'Staff', 
      description: 'Administrative and support staff' 
    },
    { 
      value: 'alumni', 
      label: 'Alumni', 
      description: 'Graduates and former students' 
    },
    { 
      value: 'admin', 
      label: 'Administrator', 
      description: 'System administration access' 
    }
  ];

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.requestedRole) {
      newErrors.requestedRole = 'Please select a role';
    }

    if (!formData.reason.trim()) {
      newErrors.reason = 'Please provide a reason for your role request';
    } else if (formData.reason.trim().length < 20) {
      newErrors.reason = 'Please provide a more detailed reason (at least 20 characters)';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    setIsSubmitting(true);
    setSubmitSuccess(false);

    try {
      await submitRoleRequest({
        userId: user?.id || '',
        requestedRole: formData.requestedRole,
        currentRole: user?.role || 'guest',
        reason: formData.reason
      });
      
      // Reset form
      setFormData({
        requestedRole: 'student',
        reason: ''
      });
      setErrors({});
      setSubmitSuccess(true);
      
      // Refresh requests list
      const updatedRequests = await getRoleRequests();
      setRequests(updatedRequests);
      
      // Switch to status tab to show the new request
      setActiveTab('status');
      
    } catch (error) {
      console.error('Failed to submit role request:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'approved':
        return 'bg-green-100 text-green-800';
      case 'rejected':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'pending':
        return <Clock className="h-4 w-4" />;
      case 'approved':
        return <CheckCircle className="h-4 w-4" />;
      case 'rejected':
        return <XCircle className="h-4 w-4" />;
      default:
        return <FileText className="h-4 w-4" />;
    }
  };

  const getRoleDisplayName = (role: string) => {
    const roleNames = {
      guest: 'Guest',
      staff: 'Staff',
      student_applicant: 'Student Applicant',
      student: 'Student',
      lecturer: 'Lecturer',
      alumni: 'Alumni',
      admin: 'Administrator'
    };
    return roleNames[role as keyof typeof roleNames] || role;
  };

  const filteredRequests = requests.filter(request => {
    const matchesSearch = request.id.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         request.reason.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = selectedStatus === 'all' || request.status === selectedStatus;
    return matchesSearch && matchesStatus;
  });

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Role Management</h1>
          <p className="text-gray-600 mt-1">Request new roles or check the status of your requests</p>
        </div>
      </div>

      {/* Success Message */}
      {submitSuccess && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
          <div className="flex items-center">
            <CheckCircle className="h-5 w-5 text-green-600 mr-2" />
            <span className="text-green-800 font-medium">Role request submitted successfully!</span>
          </div>
        </div>
      )}

      {/* Tab Navigation */}
      <div className="border-b border-gray-200">
        <nav className="-mb-px flex space-x-8">
          <button
            onClick={() => setActiveTab('request')}
            className={`py-2 px-1 border-b-2 font-medium text-sm ${
              activeTab === 'request'
                ? 'border-primary-500 text-primary-600'
                : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <Plus className="h-4 w-4 inline mr-2" />
            Request Role
          </button>
          <button
            onClick={() => setActiveTab('status')}
            className={`py-2 px-1 border-b-2 font-medium text-sm ${
              activeTab === 'status'
                ? 'border-primary-500 text-primary-600'
                : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            <FileText className="h-4 w-4 inline mr-2" />
            Request Status
            {requests.filter(r => r.status === 'pending').length > 0 && (
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full">
                {requests.filter(r => r.status === 'pending').length}
              </span>
            )}
          </button>
        </nav>
      </div>

      {/* Request Role Tab */}
      {activeTab === 'request' && (
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <div className="mb-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-2">Request New Role</h2>
            <p className="text-gray-600">
              Choose the role you would like to request and provide a detailed reason for your request.
            </p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Current Role Display */}
            <div className="bg-gray-50 rounded-lg p-4">
              <div className="flex items-center">
                <User className="h-5 w-5 text-gray-500 mr-2" />
                <span className="text-sm text-gray-600">Current Role:</span>
                <span className="ml-2 text-sm font-medium text-gray-900">
                  {getRoleDisplayName(user?.role || 'guest')}
                </span>
              </div>
            </div>

            {/* Role Selection */}
            <div>
              <label htmlFor="requestedRole" className="block text-sm font-medium text-gray-700 mb-2">
                Requested Role
              </label>
              <select
                id="requestedRole"
                value={formData.requestedRole}
                onChange={(e) => setFormData({ ...formData, requestedRole: e.target.value as UserRole })}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 ${
                  errors.requestedRole ? 'border-red-300' : 'border-gray-300'
                }`}
              >
                {availableRoles.map((role) => (
                  <option key={role.value} value={role.value}>
                    {role.label}
                  </option>
                ))}
              </select>
              {errors.requestedRole && (
                <p className="mt-1 text-sm text-red-600">{errors.requestedRole}</p>
              )}
              
              {/* Role Description */}
              {formData.requestedRole && (
                <div className="mt-2 p-3 bg-blue-50 rounded-lg">
                  <p className="text-sm text-blue-800">
                    {availableRoles.find(r => r.value === formData.requestedRole)?.description}
                  </p>
                </div>
              )}
            </div>

            {/* Reason */}
            <div>
              <label htmlFor="reason" className="block text-sm font-medium text-gray-700 mb-2">
                Reason for Request
              </label>
              <textarea
                id="reason"
                rows={4}
                value={formData.reason}
                onChange={(e) => setFormData({ ...formData, reason: e.target.value })}
                placeholder="Please provide a detailed explanation for why you need this role..."
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 ${
                  errors.reason ? 'border-red-300' : 'border-gray-300'
                }`}
              />
              {errors.reason && (
                <p className="mt-1 text-sm text-red-600">{errors.reason}</p>
              )}
              <p className="mt-1 text-sm text-gray-500">
                Minimum 20 characters. Current: {formData.reason.length}
              </p>
            </div>

            {/* Submit Button */}
            <div className="flex justify-end">
              <button
                type="submit"
                disabled={isSubmitting}
                className="btn-primary flex items-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <Send className="h-4 w-4" />
                <span>{isSubmitting ? 'Submitting...' : 'Submit Request'}</span>
              </button>
            </div>
          </form>
        </div>
      )}

      {/* Request Status Tab */}
      {activeTab === 'status' && (
        <div className="space-y-6">
          {/* Search and Filter */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <div className="flex flex-col sm:flex-row gap-4">
              <div className="flex-1">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="text"
                    placeholder="Search by request ID or reason..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                  />
                </div>
              </div>
              <div>
                <select
                  value={selectedStatus}
                  onChange={(e) => setSelectedStatus(e.target.value)}
                  className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500"
                >
                  <option value="all">All Status</option>
                  <option value="pending">Pending</option>
                  <option value="approved">Approved</option>
                  <option value="rejected">Rejected</option>
                </select>
              </div>
            </div>
          </div>

          {/* Requests List */}
          <div className="space-y-4">
            {filteredRequests.length > 0 ? (
              filteredRequests.map((request) => (
                <div key={request.id} className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
                  <div className="flex items-start justify-between mb-4">
                    <div>
                      <div className="flex items-center space-x-3 mb-2">
                        <h3 className="text-lg font-semibold text-gray-900">
                          Request #{request.id}
                        </h3>
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(request.status)}`}>
                          {getStatusIcon(request.status)}
                          <span className="ml-1 capitalize">{request.status}</span>
                        </span>
                      </div>
                      <p className="text-sm text-gray-600">
                        <span className="font-medium">Requested Role:</span> {getRoleDisplayName(request.requestedRole)}
                      </p>
                      <p className="text-sm text-gray-600">
                        <span className="font-medium">Submitted:</span> {request.submittedAt}
                      </p>
                    </div>
                  </div>

                  <div className="space-y-3">
                    <div>
                      <h4 className="text-sm font-medium text-gray-900 mb-1">Reason:</h4>
                      <p className="text-sm text-gray-700">{request.reason}</p>
                    </div>

                    {request.comments && (
                      <div>
                        <h4 className="text-sm font-medium text-gray-900 mb-1">Comments:</h4>
                        <p className="text-sm text-gray-700">{request.comments}</p>
                      </div>
                    )}

                    {request.reviewedAt && (
                      <div className="text-sm text-gray-600">
                        <span className="font-medium">Reviewed:</span> {request.reviewedAt}
                        {request.reviewedBy && (
                          <span> by {request.reviewedBy}</span>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              ))
            ) : (
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-12 text-center">
                <FileText className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-medium text-gray-900 mb-2">No role requests found</h3>
                <p className="text-gray-600 mb-6">
                  {searchTerm || selectedStatus !== 'all' 
                    ? 'No requests match your search criteria.' 
                    : 'You haven\'t submitted any role requests yet.'
                  }
                </p>
                {!searchTerm && selectedStatus === 'all' && (
                  <button
                    onClick={() => setActiveTab('request')}
                    className="btn-primary"
                  >
                    Submit Your First Request
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default RoleManagement;