import React, { useEffect, useState } from 'react';
import { Users, BookOpen, Clock, Calendar as CalendarIcon, Edit2 } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import { useLecturerClasses } from '../../hooks/useClasses';
import { useClassStudents, useMeetingAttendances, useCreateAttendance, useUpdateAttendance, useUpdateMeeting } from '../../hooks/useAttendances';
import { extractString } from '../../utils/sqlNull';
import type { Mahasiswa } from '../../types';

type Day = 'Monday' | 'Tuesday' | 'Wednesday' | 'Thursday' | 'Friday';

type WeekDay = 'Sunday' | 'Monday' | 'Tuesday' | 'Wednesday' | 'Thursday' | 'Friday' | 'Saturday';

interface ScheduleItem {
  id: string;
  day: Day;
  startTime: string;
  endTime: string;
  courseCode: string;
  courseName: string;
  className: string;
  credits: number;
  room: string;
  topic: string;
  weekNumber: number;
  classId: number;
  actualDate?: Date;
  meetingId: number;
}

// Helper to extract day name from date string
const getDayFromDate = (dateStr: string): Day | null => {
  const date = new Date(dateStr);
  const dayIndex = date.getDay();
  const daysMap: Record<number, Day> = {
    1: 'Monday',
    2: 'Tuesday',
    3: 'Wednesday',
    4: 'Thursday',
    5: 'Friday',
  };
  return daysMap[dayIndex] || null;
};

// Helper to extract time from datetime string
const getTimeFromDate = (dateStr: string): string => {
  const timePart = dateStr.split('T')[1]?.split('.')[0] || dateStr.split('T')[1]?.split('Z')[0];
  if (timePart) {
    return timePart.substring(0, 5);
  }
  const date = new Date(dateStr);
  return date.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit', hour12: false });
};

// Helper to get current week's start (Sunday) and end (Saturday)
const getCurrentWeekRange = (): { start: Date; end: Date } => {
  const today = new Date();
  const dayOfWeek = today.getDay();
  
  const daysToSunday = dayOfWeek === 0 ? 0 : dayOfWeek;
  
  const start = new Date(today);
  start.setDate(today.getDate() - daysToSunday);
  start.setHours(0, 0, 0, 0);
  
  const end = new Date(start);
  end.setDate(start.getDate() + 6);
  end.setHours(23, 59, 59, 999);
  
  return { start, end };
};

const LecturerDashboard: React.FC = () => {
  const { user, currentSemester } = useAuth();
  const [thisWeekItems, setThisWeekItems] = useState<(ScheduleItem & { weekDay: WeekDay })[]>([]);
  const [selectedMeeting, setSelectedMeeting] = useState<{
    meetingId: number;
    classId: number;
    title: string;
    subtitle: string;
    dateLabel?: string;
    meetingDateTime?: Date;
  } | null>(null);

  // Compute this week's range and fetch lecturer classes via React Query
  const { start, end } = React.useMemo(() => getCurrentWeekRange(), []);
  const startDay = start.toISOString().split('T')[0];
  const endDay = end.toISOString().split('T')[0];

  const { data: rawClasses, isLoading: loading } = useLecturerClasses(
    user?.employeeId || '',
    { semester: currentSemester, start_day: startDay, end_day: endDay }
  );
  const classes = React.useMemo(() => rawClasses ?? [], [rawClasses]);

  // Fetch students and attendances when a meeting is selected
  const { data: studentsRaw, isLoading: studentsLoading } = useClassStudents(
    selectedMeeting?.classId
  );
  const { data: attendancesRaw, isLoading: attendancesLoading } = useMeetingAttendances(
    selectedMeeting?.meetingId
  );
  const students = React.useMemo(() => studentsRaw ?? [], [studentsRaw]);
  const attendances = React.useMemo(() => attendancesRaw ?? [], [attendancesRaw]);

  const createAttendanceMutation = useCreateAttendance();
  const updateAttendanceMutation = useUpdateAttendance();
  const updateMeetingMutation = useUpdateMeeting();

  const [editingStates, setEditingStates] = React.useState<Record<string, string>>({});
  const [isBulkEditing, setIsBulkEditing] = React.useState(false);
  const [isSavingAll, setIsSavingAll] = React.useState(false);
  const [isEditingMeeting, setIsEditingMeeting] = React.useState(false);
  const [editedMeetingDate, setEditedMeetingDate] = React.useState('');
  const [editedMeetingTime, setEditedMeetingTime] = React.useState('');

  const mergedAttendance: Array<Mahasiswa & { status: string; attendanceId?: number }> = React.useMemo(() => {
    if (!students || students.length === 0) return [];
    const attendanceByNim = new Map(
      attendances.map((a) => [
        a.nim, 
        { 
          status: extractString((a as any).status_kehadiran as any, ''), 
          id: a.id 
        }
      ])
    );
    return students.map((s) => {
      const record = attendanceByNim.get(s.nim);
      return {
        ...s,
        status: record?.status || 'Belum Absen',
        attendanceId: record?.id,
      };
    });
  }, [students, attendances]);

  // Check if the meeting is accessible (5 minutes before start time or later)
  const isMeetingAccessible = React.useMemo(() => {
    if (!selectedMeeting?.meetingDateTime) return false;
    
    const now = new Date();
    const meetingTime = new Date(selectedMeeting.meetingDateTime);
    const fiveMinutesBeforeMeeting = new Date(meetingTime.getTime() - 5 * 60 * 1000);
    
    return now >= fiveMinutesBeforeMeeting;
  }, [selectedMeeting]);

  const handleStatusChange = async (nim: string, newStatus: string, attendanceId?: number) => {
    if (!selectedMeeting) return;

    // Get current time in ISO format for waktu_masuk
    const currentTime = new Date().toISOString();

    try {
      if (attendanceId) {
        // Update existing attendance
        await updateAttendanceMutation.mutateAsync({
          id: attendanceId,
          data: { 
            status_kehadiran: newStatus,
            waktu_masuk: currentTime
          }
        });
      } else {
        // Create new attendance
        await createAttendanceMutation.mutateAsync({
          id_pertemuan: selectedMeeting.meetingId,
          nim,
          status_kehadiran: newStatus,
          waktu_masuk: currentTime
        });
      }
    } catch (error) {
      console.error('Failed to update attendance:', error);
      alert(`Failed to update attendance: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  };

  const handleSaveAll = async () => {
    if (!selectedMeeting || Object.keys(editingStates).length === 0) return;

    setIsSavingAll(true);
    const errors: string[] = [];
    
    // Get current time in ISO format for waktu_masuk
    const currentTime = new Date().toISOString();

    for (const nim of Object.keys(editingStates)) {
      const newStatus = editingStates[nim];
      const student = mergedAttendance.find(s => s.nim === nim);
      
      if (!student) continue;

      try {
        if (student.attendanceId) {
          await updateAttendanceMutation.mutateAsync({
            id: student.attendanceId,
            data: { 
              status_kehadiran: newStatus,
              waktu_masuk: currentTime
            }
          });
        } else {
          await createAttendanceMutation.mutateAsync({
            id_pertemuan: selectedMeeting.meetingId,
            nim,
            status_kehadiran: newStatus,
            waktu_masuk: currentTime
          });
        }
      } catch (error) {
        console.error(`Failed to update attendance for ${nim}:`, error);
        errors.push(`${student.nama} (${nim})`);
      }
    }

    setIsSavingAll(false);
    
    if (errors.length > 0) {
      alert(`Failed to update attendance for: ${errors.join(', ')}`);
    } else {
      setEditingStates({});
      setIsBulkEditing(false);
    }
  };

  const handleEditAll = () => {
    const allStates: Record<string, string> = {};
    mergedAttendance.forEach(s => {
      allStates[s.nim] = s.status === 'Belum Absen' ? 'Hadir' : (s.status || 'Hadir');
    });
    setEditingStates(allStates);
    setIsBulkEditing(true);
  };

  const handleCancelAll = () => {
    setEditingStates({});
    setIsBulkEditing(false);
  };

  const handleUpdateMeeting = async () => {
    if (!selectedMeeting || !editedMeetingDate || !editedMeetingTime) return;

    try {
      // Format: YYYY-MM-DD for date, HH:mm:ss for time (backend expects time with seconds)
      const timeWithSeconds = editedMeetingTime.length === 5 ? `${editedMeetingTime}:00` : editedMeetingTime;
      
      await updateMeetingMutation.mutateAsync({
        id: selectedMeeting.meetingId,
        data: {
          tgl_pertemuan: editedMeetingDate,
          jam_pertemuan: timeWithSeconds,
        }
      });

      // Update the local state
      const [hours, minutes] = editedMeetingTime.split(':').map(Number);
      const newDateTime = new Date(editedMeetingDate);
      newDateTime.setHours(hours, minutes, 0, 0);
      
      setSelectedMeeting({
        ...selectedMeeting,
        meetingDateTime: newDateTime,
        dateLabel: newDateTime.toLocaleDateString('id-ID', {
          weekday: 'short',
          day: 'numeric',
          month: 'short',
        }),
      });
      
      setIsEditingMeeting(false);
      alert('Meeting schedule updated successfully!');
    } catch (error) {
      console.error('Failed to update meeting:', error);
      alert(`Failed to update meeting: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  };

  const handleStartEditMeeting = () => {
    if (selectedMeeting?.meetingDateTime) {
      const date = new Date(selectedMeeting.meetingDateTime);
      // Format date as YYYY-MM-DD
      const dateStr = date.toISOString().split('T')[0];
      // Format time as HH:mm
      const timeStr = `${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`;
      
      setEditedMeetingDate(dateStr);
      setEditedMeetingTime(timeStr);
      setIsEditingMeeting(true);
    }
  };

  useEffect(() => {
    const items: ScheduleItem[] = [];
    (rawClasses ?? []).forEach(classData => {
      classData.meeting_schedules.forEach(meeting => {
        const day = getDayFromDate(meeting.tgl_pertemuan);
        
        if (!day) return;
        
        const startTime = getTimeFromDate(meeting.jam_pertemuan);
        const actualDate = new Date(meeting.tgl_pertemuan);
        
        const totalSKS = classData.sks_tatap_muka + classData.sks_praktek + classData.sks_prak_lapangan + classData.sks_simulasi;
        const durationMinutes = totalSKS * 50;
        
        const [startHours, startMinutes] = startTime.split(':').map(Number);
        const totalMinutes = startHours * 60 + startMinutes + durationMinutes;
        const endHours = Math.floor(totalMinutes / 60) % 24;
        const endMinutes = totalMinutes % 60;
        const endTime = `${String(endHours).padStart(2, '0')}:${String(endMinutes).padStart(2, '0')}`;

        items.push({
          id: `${classData.kelas_id}-${meeting.id}`,
          day,
          startTime,
          endTime,
          courseCode: classData.kode_mk,
          courseName: classData.nama_mk,
          className: classData.nama_kelas,
          credits: totalSKS,
          room: meeting.media_pembelajaran || 'TBA',
          topic: meeting.topik || '',
          weekNumber: meeting.pertemuan_ke,
          classId: classData.kelas_id,
          actualDate,
          meetingId: meeting.id,
        });
      });
    });

    // Backend already filtered by this week's date range, just add weekDay labels
    const weekItems: (ScheduleItem & { weekDay: WeekDay })[] = items
      .filter(item => item.actualDate) // Ensure valid date
      .map(item => {
        const weekDayIndex = item.actualDate!.getDay();
        const weekDaysMap: Record<number, WeekDay> = {
          0: 'Sunday',
          1: 'Monday',
          2: 'Tuesday',
          3: 'Wednesday',
          4: 'Thursday',
          5: 'Friday',
          6: 'Saturday',
        };
        return {
          ...item,
          weekDay: weekDaysMap[weekDayIndex] || 'Monday',
        };
      })
      .sort((a, b) => {
        if (!a.actualDate || !b.actualDate) return 0;
        return a.actualDate.getTime() - b.actualDate.getTime();
      });
    
    setThisWeekItems(weekItems);
  }, [rawClasses]);

  // Calculate stats
  const totalStudents = classes.reduce((sum) => {
    // Estimate: each class might have ~30 students on average
    // In real scenario, this would come from actual enrollment data
    return sum + 30;
  }, 0);

  const activeClasses = classes.length;
  
  const teachingHours = classes.reduce((sum, classData) => {
    const totalSKS = classData.sks_tatap_muka + classData.sks_praktek + classData.sks_prak_lapangan + classData.sks_simulasi;
    return sum + totalSKS;
  }, 0);

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Lecturer Dashboard</h1>
        <div className="text-sm text-gray-500">Academic Year 2024/2025</div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-primary-100 rounded-lg">
              <Users className="h-6 w-6 text-primary-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Total Students</div>
              <div className="text-2xl font-bold text-gray-900">{loading ? '...' : totalStudents}</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <BookOpen className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Active Classes</div>
              <div className="text-2xl font-bold text-gray-900">{loading ? '...' : activeClasses}</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Clock className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Teaching Hours</div>
              <div className="text-2xl font-bold text-gray-900">{loading ? '...' : teachingHours}</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <CalendarIcon className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">This Week Classes</div>
              <div className="text-2xl font-bold text-gray-900">{loading ? '...' : thisWeekItems.length}</div>
            </div>
          </div>
        </div>
      </div>

      <div className="card">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">This Week's Schedule</h2>
        {loading ? (
          <div className="text-center py-8 text-gray-500">Loading this week's schedule...</div>
        ) : thisWeekItems.length === 0 ? (
          <div className="text-center py-8 text-gray-500">No classes scheduled for this week</div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {thisWeekItems.map(item => {
              // Combine date and time to create meeting datetime
              const meetingDateTime = item.actualDate ? new Date(item.actualDate) : null;
              if (meetingDateTime && item.startTime) {
                const [hours, minutes] = item.startTime.split(':').map(Number);
                meetingDateTime.setHours(hours, minutes, 0, 0);
              }

              return (
              <button
                key={item.id}
                type="button"
                onClick={() =>
                  setSelectedMeeting({
                    meetingId: item.meetingId,
                    classId: item.classId,
                    title: `${item.courseCode} • ${item.courseName}`,
                    subtitle: `Week ${item.weekNumber}`,
                    dateLabel: item.actualDate?.toLocaleDateString('id-ID', {
                      weekday: 'short',
                      day: 'numeric',
                      month: 'short',
                    }),
                    meetingDateTime: meetingDateTime || undefined,
                  })
                }
                className="text-left p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors flex flex-col cursor-pointer"
              >
                <div className="flex-1">
                  <div className="flex items-start justify-between gap-2 mb-2">
                    <div className="flex items-center gap-2 flex-wrap">
                      <span className="inline-block px-2.5 py-0.5 bg-primary-100 text-primary-700 text-xs font-semibold rounded">
                        {item.weekDay}
                      </span>
                      {item.actualDate && (
                        <span className="text-xs text-gray-500 px-2 py-0.5 bg-gray-100 rounded">
                          {item.actualDate.toLocaleDateString('id-ID', { day: 'numeric', month: 'short' })}
                        </span>
                      )}
                    </div>
                    <span className="text-xs text-gray-500">Week {item.weekNumber}</span>
                  </div>
                  <div className="text-xs font-semibold text-primary-600 mb-1">{item.courseCode}</div>
                  <h3 className="text-xs font-medium text-gray-900 line-clamp-2 mb-1">{item.courseName}</h3>
                </div>
                <div className="flex flex-col gap-1 text-xs text-gray-600 border-t border-gray-100 pt-2 mt-2">
                  <div className="flex items-center gap-1.5">
                    <Clock className="h-3 w-3 flex-shrink-0" />
                    <span>{item.startTime} - {item.endTime}</span>
                  </div>
                </div>
              </button>
              );
            })}
          </div>
        )}
      </div>

      {/* Attendance Modal */}
      {selectedMeeting && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-2 sm:p-4 bg-black/30">
          <div className="w-full h-full max-h-screen sm:max-h-[90vh] max-w-sm sm:max-w-2xl lg:max-w-4xl bg-white rounded-lg shadow-lg flex flex-col">
            <div className="px-3 sm:px-4 py-3 border-b border-gray-200 flex items-center justify-between flex-shrink-0">
              <div className="flex-1 min-w-0 pr-2">
                <div className="text-xs sm:text-sm text-gray-500 truncate">{selectedMeeting.subtitle}</div>
                <h3 className="text-sm sm:text-base font-semibold text-gray-900 line-clamp-2">{selectedMeeting.title}</h3>
                {selectedMeeting.dateLabel && (
                  <div className="text-xs text-gray-500 mt-0.5">{selectedMeeting.dateLabel}</div>
                )}
              </div>
              <div className="flex items-center gap-1 sm:gap-2 flex-shrink-0">
                {!isEditingMeeting && (
                  <button
                    onClick={handleStartEditMeeting}
                    className="px-2 py-1 text-xs font-medium rounded bg-blue-600 text-white hover:bg-blue-700 cursor-pointer flex items-center gap-1 whitespace-nowrap"
                    title="Edit meeting schedule"
                  >
                    <Edit2 className="h-3 w-3" />
                    <span className="hidden sm:inline">Edit Schedule</span>
                    <span className="sm:hidden">Edit</span>
                  </button>
                )}
                <button
                  onClick={() => {
                    setSelectedMeeting(null);
                    setEditingStates({});
                    setIsBulkEditing(false);
                    setIsEditingMeeting(false);
                  }}
                  className="px-2 py-1 text-sm rounded hover:bg-gray-100 cursor-pointer"
                  aria-label="Close"
                >
                  Close
                </button>
              </div>
            </div>
            <div className="flex-1 overflow-y-auto p-3 sm:p-4">
              {/* Meeting Edit Form */}
              {isEditingMeeting && (
                <div className="mb-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
                  <h4 className="text-sm font-semibold text-gray-900 mb-3">Edit Meeting Schedule</h4>
                  <div className="grid grid-cols-2 gap-3 mb-3">
                    <div>
                      <label className="block text-xs font-medium text-gray-700 mb-1">Date</label>
                      <input
                        type="date"
                        value={editedMeetingDate}
                        onChange={(e) => setEditedMeetingDate(e.target.value)}
                        className="input-field w-full text-sm"
                      />
                    </div>
                    <div>
                      <label className="block text-xs font-medium text-gray-700 mb-1">Time</label>
                      <input
                        type="time"
                        value={editedMeetingTime}
                        onChange={(e) => setEditedMeetingTime(e.target.value)}
                        className="input-field w-full text-sm"
                      />
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <button
                      onClick={handleUpdateMeeting}
                      disabled={updateMeetingMutation.isPending || !editedMeetingDate || !editedMeetingTime}
                      className="px-3 py-1.5 text-sm font-medium rounded bg-green-600 text-white hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer"
                    >
                      {updateMeetingMutation.isPending ? 'Saving...' : 'Save Changes'}
                    </button>
                    <button
                      onClick={() => setIsEditingMeeting(false)}
                      disabled={updateMeetingMutation.isPending}
                      className="px-3 py-1.5 text-sm font-medium rounded bg-gray-200 text-gray-700 hover:bg-gray-300 disabled:opacity-50 cursor-pointer"
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              )}

              {!isMeetingAccessible ? (
                <div className="text-center py-8">
                  <Clock className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <h3 className="text-lg font-medium text-gray-900 mb-2">Attendance Not Available Yet</h3>
                  <p className="text-gray-600">
                    Attendance editing will be available 5 minutes before the class starts.
                  </p>
                  {selectedMeeting.meetingDateTime && (
                    <p className="text-sm text-gray-500 mt-2">
                      Class starts at: {selectedMeeting.meetingDateTime.toLocaleString('en-US', { 
                        weekday: 'short',
                        year: 'numeric',
                        month: 'short',
                        day: 'numeric',
                        hour: '2-digit',
                        minute: '2-digit'
                      })}
                    </p>
                  )}
                </div>
              ) : (studentsLoading || attendancesLoading) ? (
                <div className="text-center text-gray-500 py-6">Loading students and attendances...</div>
              ) : mergedAttendance.length === 0 ? (
                <div className="text-center text-gray-500 py-6">No students found for this class.</div>
              ) : (
                <>
                  {/* Bulk Action Buttons */}
                  <div className="mb-3 sm:mb-4 flex gap-1 sm:gap-2 justify-end flex-wrap">
                    {isBulkEditing ? (
                      <>
                        <button
                          onClick={handleSaveAll}
                          disabled={isSavingAll || Object.keys(editingStates).length === 0}
                          className="px-2 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded bg-green-600 text-white hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer whitespace-nowrap"
                        >
                          {isSavingAll ? 'Saving...' : `Save (${Object.keys(editingStates).length})`}
                        </button>
                        <button
                          onClick={handleCancelAll}
                          disabled={isSavingAll}
                          className="px-2 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded bg-gray-200 text-gray-700 hover:bg-gray-300 disabled:opacity-50 cursor-pointer whitespace-nowrap"
                        >
                          Cancel
                        </button>
                      </>
                    ) : (
                      <button
                        onClick={handleEditAll}
                        className="px-2 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded bg-primary-600 text-white hover:bg-primary-700 cursor-pointer whitespace-nowrap"
                      >
                        Edit All
                      </button>
                    )}
                  </div>

                  <div className="space-y-2 sm:overflow-x-auto sm:border sm:border-gray-200 sm:rounded-lg">
                    {/* Mobile Card View */}
                    <div className="sm:hidden space-y-2">
                      {mergedAttendance.map((s) => {
                        const status = s.status;
                        const isEditing = editingStates[s.nim] !== undefined;
                        const displayStatus = isEditing ? editingStates[s.nim] : status;
                        
                        return (
                          <div key={s.nim} className="border border-gray-200 rounded-lg p-2 bg-white">
                            <div className="flex items-start justify-between gap-2 mb-2">
                              <div className="flex-1 min-w-0">
                                <p className="text-xs font-semibold text-gray-900 break-words">{s.nama}</p>
                                <p className="text-xs text-gray-500 font-mono">{s.nim}</p>
                              </div>
                              {!isEditing && (
                                <span className={`flex-shrink-0 px-2 py-0.5 rounded text-xs font-medium inline-block ${
                                  status === 'Hadir' ? 'bg-green-100 text-green-700' :
                                  status === 'Izin' || status === 'Sakit' ? 'bg-yellow-100 text-yellow-700' :
                                  status === 'Terlambat' ? 'bg-blue-100 text-blue-700' :
                                  status === 'Belum Absen' ? 'bg-gray-100 text-gray-700' :
                                  'bg-red-100 text-red-700'
                                }`}>
                                  {status || 'Belum Absen'}
                                </span>
                              )}
                            </div>
                            
                            {isEditing ? (
                              <div className="space-y-2">
                                <div className="grid grid-cols-3 gap-0.5">
                                  {['Hadir', 'Izin', 'Sakit', 'Alfa', 'Terlambat'].map(statusOption => (
                                    <button
                                      key={statusOption}
                                      type="button"
                                      onClick={() => setEditingStates(prev => ({ ...prev, [s.nim]: statusOption }))}
                                      className={`px-1 py-1 rounded text-xs font-medium transition-colors cursor-pointer truncate ${
                                        displayStatus === statusOption
                                          ? statusOption === 'Hadir' ? 'bg-green-600 text-white' :
                                            statusOption === 'Izin' || statusOption === 'Sakit' ? 'bg-yellow-600 text-white' :
                                            statusOption === 'Terlambat' ? 'bg-blue-600 text-white' :
                                            'bg-red-600 text-white'
                                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                      }`}
                                    >
                                      {statusOption}
                                    </button>
                                  ))}
                                </div>
                                <div className="flex gap-1">
                                  <button
                                    onClick={async () => {
                                      await handleStatusChange(s.nim, editingStates[s.nim], s.attendanceId);
                                      setEditingStates(prev => {
                                        const next = { ...prev };
                                        delete next[s.nim];
                                        return next;
                                      });
                                    }}
                                    disabled={createAttendanceMutation.isPending || updateAttendanceMutation.isPending}
                                    className="flex-1 px-2 py-1 text-xs font-medium rounded bg-green-600 text-white hover:bg-green-700 disabled:opacity-50 cursor-pointer"
                                  >
                                    {createAttendanceMutation.isPending || updateAttendanceMutation.isPending ? '...' : 'Save'}
                                  </button>
                                  <button
                                    onClick={() => setEditingStates(prev => {
                                      const next = { ...prev };
                                      delete next[s.nim];
                                      return next;
                                    })}
                                    disabled={createAttendanceMutation.isPending || updateAttendanceMutation.isPending}
                                    className="px-2 py-1 text-xs font-medium rounded bg-gray-200 text-gray-700 hover:bg-gray-300 disabled:opacity-50 cursor-pointer"
                                  >
                                    X
                                  </button>
                                </div>
                              </div>
                            ) : (
                              <button
                                onClick={() => setEditingStates(prev => ({ ...prev, [s.nim]: status === 'Belum Absen' ? 'Hadir' : (status || 'Hadir') }))}
                                className="w-full px-2 py-1 text-xs font-medium rounded bg-primary-600 text-white hover:bg-primary-700 cursor-pointer"
                              >
                                Edit
                              </button>
                            )}
                          </div>
                        );
                      })}
                    </div>

                    {/* Desktop Table View */}
                    <table className="hidden sm:table w-full text-xs sm:text-sm">
                      <thead>
                        <tr className="bg-gray-50 border-b border-gray-200">
                          <th className="px-2 sm:px-3 py-2 text-left font-semibold text-gray-700 whitespace-nowrap">NIM</th>
                          <th className="px-2 sm:px-3 py-2 text-left font-semibold text-gray-700">Name</th>
                          <th className="px-2 sm:px-3 py-2 text-left font-semibold text-gray-700 whitespace-nowrap">Status</th>
                          <th className="px-2 sm:px-3 py-2 text-left font-semibold text-gray-700 whitespace-nowrap">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {mergedAttendance.map((s) => {
                          const status = s.status;
                          const isEditing = editingStates[s.nim] !== undefined;
                          const displayStatus = isEditing ? editingStates[s.nim] : status;
                          
                          return (
                            <tr key={s.nim} className="border-b border-gray-100 hover:bg-gray-50">
                              <td className="px-2 sm:px-3 py-2 font-mono text-gray-900 whitespace-nowrap text-xs">{s.nim}</td>
                              <td className="px-2 sm:px-3 py-2 text-gray-900">{s.nama}</td>
                              <td className="px-2 sm:px-3 py-2">
                                {isEditing ? (
                                  <div className="flex flex-wrap gap-1">
                                    {['Hadir', 'Izin', 'Sakit', 'Alfa', 'Terlambat'].map(statusOption => (
                                      <button
                                        key={statusOption}
                                        type="button"
                                        onClick={() => setEditingStates(prev => ({ ...prev, [s.nim]: statusOption }))}
                                        className={`px-2 py-1 rounded text-xs font-medium transition-colors cursor-pointer whitespace-nowrap ${
                                          displayStatus === statusOption
                                            ? statusOption === 'Hadir' ? 'bg-green-600 text-white' :
                                              statusOption === 'Izin' || statusOption === 'Sakit' ? 'bg-yellow-600 text-white' :
                                              statusOption === 'Terlambat' ? 'bg-blue-600 text-white' :
                                              'bg-red-600 text-white'
                                            : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                        }`}
                                      >
                                        {statusOption}
                                      </button>
                                    ))}
                                  </div>
                                ) : (
                                  <span className={`px-2 py-1 rounded text-xs font-medium inline-block ${
                                    status === 'Hadir' ? 'bg-green-100 text-green-700' :
                                    status === 'Izin' || status === 'Sakit' ? 'bg-yellow-100 text-yellow-700' :
                                    status === 'Terlambat' ? 'bg-blue-100 text-blue-700' :
                                    status === 'Belum Absen' ? 'bg-gray-100 text-gray-700' :
                                    'bg-red-100 text-red-700'
                                  }`}>
                                    {status || 'Belum Absen'}
                                  </span>
                                )}
                              </td>
                              <td className="px-2 sm:px-3 py-2 whitespace-nowrap">
                                {isEditing ? (
                                  <div className="flex gap-1">
                                    <button
                                      onClick={async () => {
                                        await handleStatusChange(s.nim, editingStates[s.nim], s.attendanceId);
                                        setEditingStates(prev => {
                                          const next = { ...prev };
                                          delete next[s.nim];
                                          return next;
                                        });
                                      }}
                                      disabled={createAttendanceMutation.isPending || updateAttendanceMutation.isPending}
                                      className="px-2 py-1 text-xs font-medium rounded bg-green-600 text-white hover:bg-green-700 disabled:opacity-50 cursor-pointer"
                                    >
                                      {createAttendanceMutation.isPending || updateAttendanceMutation.isPending ? '...' : 'Save'}
                                    </button>
                                    <button
                                      onClick={() => setEditingStates(prev => {
                                        const next = { ...prev };
                                        delete next[s.nim];
                                        return next;
                                      })}
                                      disabled={createAttendanceMutation.isPending || updateAttendanceMutation.isPending}
                                      className="px-2 py-1 text-xs font-medium rounded bg-gray-200 text-gray-700 hover:bg-gray-300 disabled:opacity-50 cursor-pointer"
                                    >
                                      X
                                    </button>
                                  </div>
                                ) : (
                                  <button
                                    onClick={() => setEditingStates(prev => ({ ...prev, [s.nim]: status === 'Belum Absen' ? 'Hadir' : (status || 'Hadir') }))}
                                    className="px-2 py-1 text-xs font-medium rounded bg-primary-600 text-white hover:bg-primary-700 cursor-pointer"
                                  >
                                    Edit
                                  </button>
                                )}
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default LecturerDashboard;
