import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import type { UseMutationOptions, UseQueryOptions } from '@tanstack/react-query';
import { apiService, type MessageResponse } from '../services/api';
import type { Prodi, CreateProdiRequest, UpdateProdiRequest } from '../types';

const queryKey = ['programs'] as const;

export function usePrograms(options?: Omit<UseQueryOptions<Prodi[], Error>, 'queryKey' | 'queryFn'>) {
  return useQuery<Prodi[], Error>({
    queryKey,
    queryFn: () => apiService.getPrograms(),
    // Allow callers to override defaults (e.g., staleTime)
    ...(options ?? {}),
  });
}

export const programsQueryKey = queryKey;

// Mutations
export function useCreateProgram(options?: UseMutationOptions<MessageResponse, Error, CreateProdiRequest>) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, CreateProdiRequest>({
    mutationFn: (payload: CreateProdiRequest) => apiService.createProgram(payload),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

export function useUpdateProgram(options?: UseMutationOptions<MessageResponse, Error, { kode_prodi: string; data: UpdateProdiRequest }>) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, { kode_prodi: string; data: UpdateProdiRequest }>({
    mutationFn: ({ kode_prodi, data }) => apiService.updateProgram(kode_prodi, data),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

export function useDeleteProgram(options?: UseMutationOptions<MessageResponse, Error, { kode_prodi: string }>) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, { kode_prodi: string }>({
    mutationFn: ({ kode_prodi }) => apiService.deleteProgram(kode_prodi),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}
