import { useQuery, useMutation, useQueryClient, type UseMutationOptions, type UseQueryOptions } from '@tanstack/react-query';
import { apiService, type MessageResponse } from '../services/api';
import type { BackendUser, PaginatedUsersResponse, CreateUserRequest, UpdateUserRequest, Mahasiswa, Dosen, BulkCreateUsersRequest } from '../types';

// Users list with pagination/search
export function useUsersList(
  params: { q?: string; limit?: number; offset?: number },
  options?: Omit<UseQueryOptions<PaginatedUsersResponse, Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['users', params.q ?? null, params.limit ?? null, params.offset ?? null] as const;
  return useQuery<PaginatedUsersResponse, Error>({
    queryKey: key,
    queryFn: () => apiService.getUsers(params),
    ...(options ?? {}),
  });
}

export const usersQueryRootKey = ['users'] as const;

// Students and lecturers master lists (optionally filter by program)
export function useStudentsList(
  kode_prodi?: string,
  options?: Omit<UseQueryOptions<Mahasiswa[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['students', kode_prodi ?? null] as const;
  return useQuery<Mahasiswa[], Error>({
    queryKey: key,
    queryFn: () => apiService.getStudents(kode_prodi),
    ...(options ?? {}),
  });
}

export function useLecturersList(
  kode_prodi?: string,
  options?: Omit<UseQueryOptions<Dosen[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['lecturers', kode_prodi ?? null] as const;
  return useQuery<Dosen[], Error>({
    queryKey: key,
    queryFn: () => apiService.getLecturers(kode_prodi),
    ...(options ?? {}),
  });
}

// User mutations
export function useCreateUser(
  options?: UseMutationOptions<BackendUser, Error, CreateUserRequest>
) {
  const qc = useQueryClient();
  return useMutation<BackendUser, Error, CreateUserRequest>({
    mutationFn: (payload) => apiService.createUser(payload),
    onSuccess: (...args) => {
      // Invalidate all users queries
      qc.invalidateQueries({ queryKey: usersQueryRootKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

export function useUpdateUser(
  options?: UseMutationOptions<MessageResponse, Error, { id: number; data: UpdateUserRequest }>
) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, { id: number; data: UpdateUserRequest}>({
    mutationFn: ({ id, data }) => apiService.updateUser(id, data),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey: usersQueryRootKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

export function useDeleteUser(
  options?: UseMutationOptions<MessageResponse, Error, { id: number }>
) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, { id: number }>({
    mutationFn: ({ id }) => apiService.deleteUser(id),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey: usersQueryRootKey });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

