import React from 'react';
import { BookOpen, Calendar, GraduationCap, TrendingUp, Clock, CheckCircle, Loader2, X } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import { useEffect, useState } from 'react';
import type { StudentAttendanceResponse } from '../../types';
import { useStudentClasses, useStudentAttendances, useCheckInAttendance } from '../../hooks/useClasses';

type Day = 'Monday' | 'Tuesday' | 'Wednesday' | 'Thursday' | 'Friday';

type WeekDay = 'Sunday' | 'Monday' | 'Tuesday' | 'Wednesday' | 'Thursday' | 'Friday' | 'Saturday';

interface ScheduleItem {
  id: string;
  day: Day;
  startTime: string;
  endTime: string;
  courseCode: string;
  courseName: string;
  className: string;
  credits: number;
  room: string;
  topic: string;
  weekNumber: number;
  classId: number;
  actualDate?: Date;
  meetingId: number;
}

// Helper to extract day name from date string
const getDayFromDate = (dateStr: string): Day | null => {
  const date = new Date(dateStr);
  const dayIndex = date.getDay();
  const daysMap: Record<number, Day> = {
    1: 'Monday',
    2: 'Tuesday',
    3: 'Wednesday',
    4: 'Thursday',
    5: 'Friday',
  };
  return daysMap[dayIndex] || null;
};

// Helper to extract time from datetime string
const getTimeFromDate = (dateStr: string): string => {
  const timePart = dateStr.split('T')[1]?.split('.')[0] || dateStr.split('T')[1]?.split('Z')[0];
  if (timePart) {
    return timePart.substring(0, 5);
  }
  const date = new Date(dateStr);
  return date.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit', hour12: false });
};

// Helper to get current week's start (Sunday) and end (Saturday)
const getCurrentWeekRange = (): { start: Date; end: Date } => {
  const today = new Date();
  const dayOfWeek = today.getDay();
  
  const daysToSunday = dayOfWeek === 0 ? 0 : dayOfWeek;
  
  const start = new Date(today);
  start.setDate(today.getDate() - daysToSunday);
  start.setHours(0, 0, 0, 0);
  
  const end = new Date(start);
  end.setDate(start.getDate() + 6);
  end.setHours(23, 59, 59, 999);
  
  return { start, end };
};

const StudentDashboard: React.FC = () => {
  const { user } = useAuth();
  const [thisWeekItems, setThisWeekItems] = useState<(ScheduleItem & { weekDay: WeekDay })[]>([]);
  const [showCheckInModal, setShowCheckInModal] = useState(false);
  const [checkInMeetingId, setCheckInMeetingId] = useState<number | null>(null);
  const [checkInCourseName, setCheckInCourseName] = useState('');
  const [selectedStatus, setSelectedStatus] = useState<'Hadir' | 'Izin' | 'Sakit' | 'Alfa' | 'Terlambat'>('Hadir');
  const [description, setDescription] = useState('');

  // Get this week's date range for backend filtering
  const { start, end } = React.useMemo(() => getCurrentWeekRange(), []);
  const startDay = start.toISOString().split('T')[0];
  const endDate = end.toISOString().split('T')[0];

  // Fetch classes with this week's date range
  const { data: classes = [] } = useStudentClasses(
    user?.studentId || '',
    { start_day: startDay, end_day: endDate }
  );

  // Fetch attendances
  const { data: attendances = [] } = useStudentAttendances();

  // Check-in mutation
  const checkInMutation = useCheckInAttendance();

  const openCheckInModal = (meetingId: number, courseName: string) => {
    setCheckInMeetingId(meetingId);
    setCheckInCourseName(courseName);
    setSelectedStatus('Hadir');
    setDescription('');
    setShowCheckInModal(true);
  };

  const closeCheckInModal = () => {
    setShowCheckInModal(false);
    setCheckInMeetingId(null);
    setCheckInCourseName('');
    setSelectedStatus('Hadir');
    setDescription('');
  };

  const handleCheckIn = async () => {
    if (!checkInMeetingId || checkInMutation.isPending) return;

    try {
      await checkInMutation.mutateAsync({ 
        id_pertemuan: checkInMeetingId,
        status_kehadiran: selectedStatus,
        deskripsi: description.trim() || undefined
      });
      
      closeCheckInModal();
      
      const statusLabels = {
        'Hadir': 'Present',
        'Izin': 'Permission',
        'Sakit': 'Sick',
        'Alfa': 'Absent',
        'Terlambat': 'Late',
      };
      alert(`Successfully checked in as ${statusLabels[selectedStatus]}!`);
    } catch (err) {
      console.error('Check-in failed:', err);
      alert(`Check-in failed: ${err instanceof Error ? err.message : 'Unknown error'}`);
    }
  };

  const isCheckedIn = (meetingId: number): boolean => {
    return Array.isArray(attendances) && attendances.some(att => att.id_pertemuan === meetingId);
  };

  const getAttendanceInfo = (meetingId: number): StudentAttendanceResponse | undefined => {
    return Array.isArray(attendances) ? attendances.find(att => att.id_pertemuan === meetingId) : undefined;
  };

  const canCheckIn = (item: ScheduleItem): { allowed: boolean; reason?: string } => {
    if (!item.actualDate) {
      return { allowed: false, reason: 'Invalid meeting date' };
    }

    if (isCheckedIn(item.meetingId)) {
      return { allowed: false, reason: 'Already checked in' };
    }

    const [hours, minutes] = item.startTime.split(':').map(Number);
    const meetingDateTime = new Date(item.actualDate);
    meetingDateTime.setHours(hours, minutes, 0, 0);

    const now = new Date();
    const fiveMinutesBeforeMeeting = new Date(meetingDateTime.getTime() - 5 * 60 * 1000);

    if (now < fiveMinutesBeforeMeeting) {
      return { 
        allowed: false
      };
    }

    return { allowed: true };
  };

  const getAttendanceColors = (status: string): { border: string; bg: string; text: string; icon: string } => {
    const normalizedStatus = status.toLowerCase();
    if (normalizedStatus === 'hadir') {
      return { 
        border: 'border-green-300', 
        bg: 'bg-green-50', 
        text: 'text-green-700', 
        icon: 'text-green-600' 
      };
    } else if (normalizedStatus === 'izin' || normalizedStatus === 'sakit') {
      return { 
        border: 'border-yellow-300', 
        bg: 'bg-yellow-50', 
        text: 'text-yellow-700', 
        icon: 'text-yellow-600' 
      };
    } else if (normalizedStatus === 'alfa') {
      return { 
        border: 'border-red-300', 
        bg: 'bg-red-50', 
        text: 'text-red-700', 
        icon: 'text-red-600' 
      };
    } else if (normalizedStatus === 'terlambat') {
      return { 
        border: 'border-orange-300', 
        bg: 'bg-orange-50', 
        text: 'text-orange-700', 
        icon: 'text-orange-600' 
      };
    }
    return { 
      border: 'border-green-300', 
      bg: 'bg-green-50', 
      text: 'text-green-700', 
      icon: 'text-green-600' 
    };
  };

  const formatDateTime = (dateString: string | null | undefined): { date: string; time: string } | null => {
    if (!dateString) return null;
    try {
      const date = new Date(dateString);
      if (isNaN(date.getTime())) return null;
      
      return {
        date: date.toLocaleDateString('id-ID', { day: 'numeric', month: 'short' }),
        time: date.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit', hour12: false })
      };
    } catch {
      return null;
    }
  };

  useEffect(() => {
    const items: ScheduleItem[] = [];
    classes.forEach(classData => {
      classData.meeting_schedules.forEach(meeting => {
        const day = getDayFromDate(meeting.tgl_pertemuan);
        
        if (!day) return;
        
        const startTime = getTimeFromDate(meeting.jam_pertemuan);
        const actualDate = new Date(meeting.tgl_pertemuan);
        
        const totalSKS = classData.sks_tatap_muka + classData.sks_praktek + classData.sks_prak_lapangan + classData.sks_simulasi;
        const durationMinutes = totalSKS * 50;
        
        const [startHours, startMinutes] = startTime.split(':').map(Number);
        const totalMinutes = startHours * 60 + startMinutes + durationMinutes;
        const endHours = Math.floor(totalMinutes / 60) % 24;
        const endMinutes = totalMinutes % 60;
        const endTime = `${String(endHours).padStart(2, '0')}:${String(endMinutes).padStart(2, '0')}`;

        items.push({
          id: `${classData.kelas_id}-${meeting.id}`,
          day,
          startTime,
          endTime,
          courseCode: classData.kode_mk,
          courseName: classData.nama_mk,
          className: classData.nama_kelas,
          credits: totalSKS,
          room: meeting.media_pembelajaran || 'TBA',
          topic: meeting.topik || '',
          weekNumber: meeting.pertemuan_ke,
          classId: classData.kelas_id,
          actualDate,
          meetingId: meeting.id,
        });
      });
    });

    // Backend already filtered by this week's date range, just add weekDay labels
    const weekItems: (ScheduleItem & { weekDay: WeekDay })[] = items
      .filter(item => item.actualDate) // Ensure valid date
      .map(item => {
        const weekDayIndex = item.actualDate!.getDay();
        const weekDaysMap: Record<number, WeekDay> = {
          0: 'Sunday',
          1: 'Monday',
          2: 'Tuesday',
          3: 'Wednesday',
          4: 'Thursday',
          5: 'Friday',
          6: 'Saturday',
        };
        return {
          ...item,
          weekDay: weekDaysMap[weekDayIndex] || 'Monday',
        };
      })
      .sort((a, b) => {
        if (!a.actualDate || !b.actualDate) return 0;
        return a.actualDate.getTime() - b.actualDate.getTime();
      });
    
    setThisWeekItems(weekItems);
  }, [classes]);
  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Student Dashboard</h1>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-primary-100 rounded-lg">
              <BookOpen className="h-6 w-6 text-primary-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Total Credits</div>
              <div className="text-2xl font-bold text-gray-900">xxx</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <TrendingUp className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Current GPA</div>
              <div className="text-2xl font-bold text-gray-900">xxx</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <GraduationCap className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Semester</div>
              <div className="text-2xl font-bold text-gray-900">xxx</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <Calendar className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Classes Today</div>
              <div className="text-2xl font-bold text-gray-900">xxx</div>
            </div>
          </div>
        </div>
      </div>

      {/* This Week's Schedule */}
      {thisWeekItems.length > 0 && (
        <div className="card">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">This Week's Schedule</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {thisWeekItems.map(item => {
              const checkedIn = isCheckedIn(item.meetingId);
              const isCheckingInThis = checkInMutation.isPending;
              const checkInStatus = canCheckIn(item);
              const attendanceInfo = getAttendanceInfo(item.meetingId);
              const colors = attendanceInfo ? getAttendanceColors(attendanceInfo.status_kehadiran) : null;
              
              return (
                <div 
                  key={item.id} 
                  onClick={() => checkInStatus.allowed && !isCheckingInThis && openCheckInModal(item.meetingId, item.courseName)}
                  className={`p-3 border rounded-lg transition-colors flex flex-col ${
                    checkedIn && colors
                      ? `${colors.border} ${colors.bg} cursor-default` 
                      : isCheckingInThis
                      ? 'border-blue-300 bg-blue-50 cursor-wait'
                      : checkInStatus.allowed
                      ? 'border-gray-200 hover:bg-blue-50 hover:border-blue-300 cursor-pointer'
                      : 'border-gray-300 bg-gray-100 cursor-not-allowed'
                  }`}
                  title={!checkInStatus.allowed ? checkInStatus.reason : undefined}
                >
                  <div className="flex-1">
                    <div className="flex items-start justify-between gap-2 mb-2">
                      <div className="flex items-center gap-2 flex-wrap">
                        <span className="inline-block px-2.5 py-0.5 bg-primary-100 text-primary-700 text-xs font-semibold rounded">
                          {item.weekDay}
                        </span>
                        {item.actualDate && (
                          <span className="text-xs text-gray-500 px-2 py-0.5 bg-gray-100 rounded">
                            {item.actualDate.toLocaleDateString('id-ID', { day: 'numeric', month: 'short' })}
                          </span>
                        )}
                      </div>
                      <div className="flex flex-col items-end gap-0.5">
                        {checkedIn && attendanceInfo && colors && (
                          <div className={`flex items-center gap-1 text-xs ${colors.text} font-medium`}>
                            <CheckCircle className={`h-3 w-3 ${colors.icon}`} />
                            <span>Checked In</span>
                          </div>
                        )}
                        <span className="text-xs text-gray-500">Week {item.weekNumber}</span>
                      </div>
                    </div>
                    <div className="text-xs font-semibold text-primary-600 mb-1">{item.courseCode}</div>
                    <h3 className="text-xs font-medium text-gray-900 line-clamp-2 mb-1">{item.courseName}</h3>
                    {item.topic && (
                      <div className="text-xs text-gray-600 mb-2 italic line-clamp-2">
                        {item.topic}
                      </div>
                    )}
                  </div>
                  <div className="flex flex-col gap-1 text-xs text-gray-600 border-t border-gray-100 pt-2 mt-2">
                    <div className="flex items-center gap-1.5">
                      <Clock className="h-3 w-3 flex-shrink-0" />
                      <span>{item.startTime} - {item.endTime}</span>
                    </div>
                  </div>
                  {checkedIn && attendanceInfo && colors && (
                    <div className={`mt-2 pt-2 border-t ${colors.border}`}>
                      <div className="text-xs text-gray-600 space-y-0.5">
                        {(() => {
                          const checkInTime = formatDateTime(attendanceInfo.waktu_masuk);
                          const checkOutTime = attendanceInfo.waktu_keluar ? formatDateTime(attendanceInfo.waktu_keluar) : null;
                          
                          return (
                            <>
                              {checkInTime && (
                                <div className="flex items-center justify-between">
                                  <span>Check-in:</span>
                                  <span className="font-medium">
                                    {checkInTime.date} {checkInTime.time}
                                  </span>
                                </div>
                              )}
                              {checkOutTime && (
                                <div className="flex items-center justify-between">
                                  <span>Check-out:</span>
                                  <span className="font-medium">
                                    {checkOutTime.time}
                                  </span>
                                </div>
                              )}
                              <div className="flex items-center justify-between">
                                <span>Status:</span>
                                <span className={`font-medium ${colors.text}`}>{attendanceInfo.status_kehadiran}</span>
                              </div>
                            </>
                          );
                        })()}
                      </div>
                    </div>
                  )}
                  {isCheckingInThis && (
                    <div className="mt-2 text-xs text-blue-700 font-medium text-center">
                      Checking in...
                    </div>
                  )}
                  {!checkedIn && !isCheckingInThis && !checkInStatus.allowed && (
                    <div className="mt-2 text-xs text-gray-600 text-center italic">
                      {checkInStatus.reason}
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        </div>
      )}

      {/* Check-in Modal */}
      {showCheckInModal && (
        <>
          {/* Blurred Backdrop */}
          <div className="fixed inset-0 backdrop-blur-md z-40" onClick={closeCheckInModal} />
          {/* Modal */}
          <div className="fixed inset-0 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between p-4 border-b border-gray-200 sticky top-0 bg-white">
              <h3 className="text-base lg:text-lg font-semibold text-gray-900">Check-in Attendance</h3>
              <button
                onClick={closeCheckInModal}
                className="text-gray-400 hover:text-gray-600 transition-colors cursor-pointer"
                disabled={checkInMutation.isPending}
              >
                <X className="h-5 w-5" />
              </button>
            </div>

            <div className="p-4 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Course</label>
                <div className="text-sm text-gray-900">{checkInCourseName}</div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Attendance Status *</label>
                <div className="grid grid-cols-2 gap-2">
                  {(['Hadir', 'Izin', 'Sakit', 'Alfa', 'Terlambat'] as const).map(status => (
                    <button
                      key={status}
                      onClick={() => setSelectedStatus(status)}
                      disabled={checkInMutation.isPending}
                      className={`px-3 lg:px-4 py-2 text-xs lg:text-sm font-medium rounded-lg transition-colors cursor-pointer ${
                        selectedStatus === status
                          ? status === 'Hadir'
                            ? 'bg-green-600 text-white'
                            : status === 'Alfa'
                            ? 'bg-red-600 text-white'
                            : 'bg-yellow-600 text-white'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      } ${checkInMutation.isPending ? 'opacity-50 cursor-not-allowed' : ''}`}
                    >
                      <span className="hidden sm:inline">
                        {status === 'Hadir' && '✓ Present'}
                        {status === 'Izin' && '📋 Permission'}
                        {status === 'Sakit' && '🤒 Sick'}
                        {status === 'Alfa' && '✗ Absent'}
                        {status === 'Terlambat' && '⏰ Late'}
                      </span>
                      <span className="sm:hidden">
                        {status === 'Hadir' && '✓'}
                        {status === 'Izin' && '📋'}
                        {status === 'Sakit' && '🤒'}
                        {status === 'Alfa' && '✗'}
                        {status === 'Terlambat' && '⏰'}
                        {' '}{status}
                      </span>
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
                  Description (Optional)
                </label>
                <textarea
                  id="description"
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  disabled={checkInMutation.isPending}
                  placeholder="Add a note about your attendance..."
                  rows={3}
                  className="input-field w-full text-sm resize-none"
                  maxLength={500}
                />
                <div className="text-xs text-gray-500 mt-1">
                  {description.length}/500 characters
                </div>
              </div>
            </div>

            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-end gap-2 sm:gap-3 p-4 border-t border-gray-200 sticky bottom-0 bg-white">
              <button
                onClick={closeCheckInModal}
                disabled={checkInMutation.isPending}
                className="w-full sm:w-auto px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer order-2 sm:order-1"
              >
                Cancel
              </button>
              <button
                onClick={handleCheckIn}
                disabled={checkInMutation.isPending}
                className="w-full sm:w-auto px-4 py-2 text-sm font-medium text-white bg-primary-600 rounded-lg hover:bg-primary-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer flex items-center justify-center gap-2 order-1 sm:order-2"
              >
                {checkInMutation.isPending ? (
                  <>
                    <Loader2 className="h-4 w-4 animate-spin" />
                    <span>Checking in...</span>
                  </>
                ) : (
                  <span>Confirm Check-in</span>
                )}
              </button>
            </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default StudentDashboard;
