import React, { useState } from 'react';
import { User, Mail, Phone, Calendar, MapPin, Edit, Save, X } from 'lucide-react';

interface GuestProfile {
  id: string;
  email: string;
  name: string;
  phone?: string;
  dateOfBirth?: string;
  address?: string;
  city?: string;
  country?: string;
  joinedAt: string;
}

const Profile: React.FC = () => {
  const [isEditing, setIsEditing] = useState(false);
  const [profile, setProfile] = useState<GuestProfile>({
    id: 'guest-001',
    email: 'guest@example.com',
    name: 'Guest User',
    phone: '+62-812-3456-7890',
    dateOfBirth: '1995-06-15',
    address: 'Jl. Contoh No. 123',
    city: 'Jakarta',
    country: 'Indonesia',
    joinedAt: '2024-10-01'
  });

  const [editedProfile, setEditedProfile] = useState<GuestProfile>(profile);

  const handleSave = () => {
    setProfile(editedProfile);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setEditedProfile(profile);
    setIsEditing(false);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'long',
      year: 'numeric'
    });
  };

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Guest Profile</h1>
          <p className="text-gray-600">Manage your personal information and preferences</p>
        </div>
        {!isEditing ? (
          <button
            onClick={() => setIsEditing(true)}
            className="btn-primary flex items-center space-x-2"
          >
            <Edit className="h-4 w-4" />
            <span>Edit Profile</span>
          </button>
        ) : (
          <div className="flex space-x-2">
            <button
              onClick={handleCancel}
              className="btn-secondary flex items-center space-x-2"
            >
              <X className="h-4 w-4" />
              <span>Cancel</span>
            </button>
            <button
              onClick={handleSave}
              className="btn-primary flex items-center space-x-2"
            >
              <Save className="h-4 w-4" />
              <span>Save Changes</span>
            </button>
          </div>
        )}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Profile Card */}
        <div className="lg:col-span-1">
          <div className="card p-6 text-center">
            <div className="w-24 h-24 mx-auto bg-gradient-to-br from-blue-400 to-blue-600 rounded-full flex items-center justify-center mb-4">
              <User className="h-12 w-12 text-white" />
            </div>
            <h2 className="text-xl font-semibold text-gray-900 mb-2">{profile.name}</h2>
            <p className="text-gray-600 mb-1">{profile.email}</p>
            <div className="inline-flex px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm">
              Guest User
            </div>
            <div className="mt-4 pt-4 border-t border-gray-200">
              <p className="text-sm text-gray-500">
                Member since {formatDate(profile.joinedAt)}
              </p>
            </div>
          </div>
        </div>

        {/* Profile Details */}
        <div className="lg:col-span-2">
          <div className="card p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-6">Personal Information</h3>
            
            <div className="space-y-6">
              {/* Basic Information */}
              <div>
                <h4 className="text-sm font-medium text-gray-700 mb-3 flex items-center space-x-2">
                  <User className="h-4 w-4" />
                  <span>Basic Information</span>
                </h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Full Name</label>
                    {isEditing ? (
                      <input
                        type="text"
                        value={editedProfile.name}
                        onChange={(e) => setEditedProfile(prev => ({ ...prev, name: e.target.value }))}
                        className="input-field w-full"
                      />
                    ) : (
                      <p className="text-gray-900">{profile.name}</p>
                    )}
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Email Address</label>
                    <p className="text-gray-900 flex items-center space-x-2">
                      <Mail className="h-4 w-4 text-gray-400" />
                      <span>{profile.email}</span>
                    </p>
                    <p className="text-xs text-gray-500 mt-1">Email cannot be changed</p>
                  </div>
                </div>
              </div>

              {/* Contact Information */}
              <div>
                <h4 className="text-sm font-medium text-gray-700 mb-3 flex items-center space-x-2">
                  <Phone className="h-4 w-4" />
                  <span>Contact Information</span>
                </h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Phone Number</label>
                    {isEditing ? (
                      <input
                        type="tel"
                        value={editedProfile.phone || ''}
                        onChange={(e) => setEditedProfile(prev => ({ ...prev, phone: e.target.value }))}
                        className="input-field w-full"
                        placeholder="Enter phone number"
                      />
                    ) : (
                      <p className="text-gray-900">{profile.phone || 'Not provided'}</p>
                    )}
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Date of Birth</label>
                    {isEditing ? (
                      <input
                        type="date"
                        value={editedProfile.dateOfBirth || ''}
                        onChange={(e) => setEditedProfile(prev => ({ ...prev, dateOfBirth: e.target.value }))}
                        className="input-field w-full"
                      />
                    ) : (
                      <p className="text-gray-900 flex items-center space-x-2">
                        <Calendar className="h-4 w-4 text-gray-400" />
                        <span>{profile.dateOfBirth ? formatDate(profile.dateOfBirth) : 'Not provided'}</span>
                      </p>
                    )}
                  </div>
                </div>
              </div>

              {/* Address Information */}
              <div>
                <h4 className="text-sm font-medium text-gray-700 mb-3 flex items-center space-x-2">
                  <MapPin className="h-4 w-4" />
                  <span>Address Information</span>
                </h4>
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Address</label>
                    {isEditing ? (
                      <textarea
                        value={editedProfile.address || ''}
                        onChange={(e) => setEditedProfile(prev => ({ ...prev, address: e.target.value }))}
                        className="input-field w-full"
                        rows={2}
                        placeholder="Enter your address"
                      />
                    ) : (
                      <p className="text-gray-900">{profile.address || 'Not provided'}</p>
                    )}
                  </div>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">City</label>
                      {isEditing ? (
                        <input
                          type="text"
                          value={editedProfile.city || ''}
                          onChange={(e) => setEditedProfile(prev => ({ ...prev, city: e.target.value }))}
                          className="input-field w-full"
                          placeholder="Enter city"
                        />
                      ) : (
                        <p className="text-gray-900">{profile.city || 'Not provided'}</p>
                      )}
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Country</label>
                      {isEditing ? (
                        <input
                          type="text"
                          value={editedProfile.country || ''}
                          onChange={(e) => setEditedProfile(prev => ({ ...prev, country: e.target.value }))}
                          className="input-field w-full"
                          placeholder="Enter country"
                        />
                      ) : (
                        <p className="text-gray-900">{profile.country || 'Not provided'}</p>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Additional Information */}
      <div className="card p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Account Information</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h4 className="text-sm font-medium text-gray-700 mb-2">Account Status</h4>
            <div className="flex items-center space-x-2">
              <div className="w-2 h-2 bg-gray-400 rounded-full"></div>
              <span className="text-sm text-gray-600">Guest Access</span>
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Limited access to public information and role request features
            </p>
          </div>
          <div>
            <h4 className="text-sm font-medium text-gray-700 mb-2">Last Activity</h4>
            <p className="text-sm text-gray-600">Today, 2:30 PM</p>
            <p className="text-xs text-gray-500 mt-1">
              Last login from your current session
            </p>
          </div>
        </div>
      </div>

      {/* Help Section */}
      <div className="card p-6 bg-blue-50 border-blue-200">
        <h3 className="text-lg font-semibold text-gray-900 mb-2">Need More Access?</h3>
        <p className="text-gray-700 mb-4">
          If you need access to additional features, you can request a specific role that matches your relationship with our institution.
        </p>
        <button className="btn-primary">
          Request Role Access
        </button>
      </div>
    </div>
  );
};

export default Profile;