import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Navigate } from 'react-router-dom';
import { LogIn } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import type { User, UserRole } from '../types';
import { initGoogleSignIn, loadGoogleScript, renderGoogleButton, resetGoogleInit } from '../utils/googleAuth';
import { Environment, logEnvironmentInfo } from '../utils/environment';

const LoginPage: React.FC = () => {
  const { isAuthenticated, login } = useAuth();
  const [isLoading, setIsLoading] = useState(false);
  const [gisReady, setGisReady] = useState(false);
  const [error, setError] = useState<string>('');
  const googleBtnRef = useRef<HTMLDivElement | null>(null);
  const mountedRef = useRef(true);
  
  // Use environment-based configuration
  const clientId = Environment.googleClientId;
  const showDemoLogin = Environment.isDevelopment;

  // Log environment info on mount (only in debug mode)
  useEffect(() => {
    logEnvironmentInfo();
  }, []);

  const handleGoogleCredential = useCallback(
    async (credential: string) => {
      try {
        // Log the token for backend testing
        console.log('Google JWT Token:', credential);
        console.log('Token for backend testing - copy this value:', credential);
        
        setIsLoading(true);
        setError('');
        
        try {
          // Try backend authentication
          await login(credential);
          console.log('✅ Backend authentication successful');
        } catch (backendError) {
          console.error('❌ Backend authentication failed:', backendError);
          setError('User not found. Please contact your administrator for access.');
          throw backendError;
        }
      } catch (e) {
        console.error('Failed to login:', e);
      } finally {
        setIsLoading(false);
      }
    },
    [login]
  );

  // Initialize Google Identity Services and render button
  useEffect(() => {
    mountedRef.current = true;

    if (!clientId) {
      console.warn('VITE_GOOGLE_CLIENT_ID is not set. Google login will be disabled.');
      setGisReady(false);
      return () => {
        mountedRef.current = false;
      };
    }

    setIsLoading(true);
    setGisReady(false);

    // Add a small delay to ensure DOM is stable after redirects
    const initDelay = setTimeout(() => {
      if (!mountedRef.current) return;

      loadGoogleScript()
        .then(() => {
          if (!mountedRef.current) return;
          
          // Reset any previous initialization
          resetGoogleInit();
          
          setGisReady(true);
          initGoogleSignIn(clientId, handleGoogleCredential);
          
          // Ensure button container exists before rendering
          if (googleBtnRef.current) {
            renderGoogleButton(googleBtnRef.current);
          }
        })
        .catch((err) => {
          if (mountedRef.current) {
            console.error('Google script loading failed:', err);
            setGisReady(false);
          }
        })
        .finally(() => {
          if (mountedRef.current) {
            setIsLoading(false);
          }
        });
    }, 100);

    return () => {
      mountedRef.current = false;
      clearTimeout(initDelay);
    };
  }, [clientId, handleGoogleCredential]);

  // Re-render button when container ref changes
  useEffect(() => {
    if (gisReady && googleBtnRef.current && !isLoading) {
      // Small delay to ensure DOM is ready
      const renderDelay = setTimeout(() => {
        if (mountedRef.current && googleBtnRef.current) {
          renderGoogleButton(googleBtnRef.current);
        }
      }, 50);

      return () => clearTimeout(renderDelay);
    }
  }, [gisReady, isLoading]);

  const handleDemoLogin = async (role: UserRole) => {
    const demoUsers: Record<UserRole, User> = {
      guest: {
        id: 'guest-demo',
        email: 'guest@university.edu',
        name: 'Guest User',
        role: 'guest',
      },
      staff: {
        id: 'staff-1',
        email: 'staff@university.edu',
        name: 'Maria Staff',
        role: 'staff',
        employeeId: 'STF001',
      },
      student_applicant: {
        id: 'applicant-1',
        email: 'applicant@university.edu',
        name: 'John Applicant',
        role: 'student_applicant',
        applicationId: 'APP2024001',
      },
      student: {
        id: 'student-1',
        email: 'student@university.edu',
        name: 'Ahmad Mahasiswa',
        role: 'student',
        studentId: '2021001',
      },
      lecturer: {
        id: 'lecturer-1',
        email: 'lecturer@university.edu',
        name: 'Dr. Sarah Dosen',
        role: 'lecturer',
        employeeId: 'LEC001',
      },
      alumni: {
        id: 'alumni-1',
        email: 'alumni@university.edu',
        name: 'David Alumni',
        role: 'alumni',
        studentId: '2018001',
        graduationYear: 2022,
      },
      admin: {
        id: 'admin-1',
        email: 'admin@university.edu',
        name: 'Admin System',
        role: 'admin',
        employeeId: 'ADM001',
      },
    };

    await login(demoUsers[role]);
  };

  // Don't render redirect until we're sure we're not authenticated
  if (isAuthenticated) {
    return <Navigate to="/dashboard" replace />;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-100 flex items-center justify-center p-4">
      <div className="max-w-md w-full">
        {/* Logo and Title */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 mb-4">
            <img 
              src={import.meta.env.BASE_URL + 'logo.png'} 
              alt="ADB Academic System Logo" 
              className="w-16 h-16 object-contain"
            />
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            ADB Academic System
          </h1>
          <p className="text-gray-600">
            Sign in to access your academic portal
          </p>
        </div>

        {/* Login Card */}
        <div className="bg-white rounded-xl shadow-lg p-8">
          <div className="space-y-6">
            {/* Error Message */}
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg text-sm">
                <p className="font-medium">Authentication Failed</p>
                <p className="mt-1">{error}</p>
              </div>
            )}

            {/* Google OAuth Button */}
            <div className="w-full flex flex-col items-center">
              {isLoading && (
                <div className="w-full flex items-center justify-center px-4 py-3 border border-gray-300 rounded-lg bg-white text-sm text-gray-700">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-primary-600 mr-3"></div>
                  Preparing Google sign-in…
                </div>
              )}
              {!isLoading && gisReady && clientId && (
                <div ref={googleBtnRef} className="w-full flex justify-center" />
              )}
              {!isLoading && (!gisReady || !clientId) && (
                <button
                  disabled
                  className="w-full flex items-center justify-center px-4 py-3 border border-gray-300 rounded-lg shadow-sm bg-white text-sm font-medium text-gray-700 opacity-60"
                >
                  <LogIn className="h-5 w-5 mr-3" />
                  Google sign-in unavailable
                </button>
              )}
            </div>

            {/* Demo Login Section - Only in demo mode */}
            {showDemoLogin && (
              <>
                <div className="relative">
                  <div className="absolute inset-0 flex items-center">
                    <div className="w-full border-t border-gray-300" />
                  </div>
                  <div className="relative flex justify-center text-sm">
                    <span className="px-2 bg-white text-gray-500">Demo Login</span>
                  </div>
                </div>

                <div className="space-y-3">
                  <button
                    onClick={() => handleDemoLogin('student')}
                    className="w-full btn-primary cursor-pointer"
                  >
                    Login as Student
                  </button>
                  <button
                    onClick={() => handleDemoLogin('lecturer')}
                    className="w-full btn-secondary cursor-pointer"
                  >
                    Login as Lecturer
                  </button>
                  <button
                    onClick={() => handleDemoLogin('admin')}
                    className="w-full btn-secondary cursor-pointer"
                  >
                    Login as Admin
                  </button>
                </div>
              </>
            )}
          </div>

          <div className="mt-6 text-center">
            <p className="text-xs text-gray-500">
              By signing in, you agree to our Terms of Service and Privacy Policy
            </p>
          </div>
        </div>

        {/* Footer */}
        <div className="text-center mt-8 text-sm text-gray-500">
          © 2024 ADB Academic System. All rights reserved.
        </div>
      </div>
    </div>
  );
};

export default LoginPage;