import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { useAuth } from './contexts/AuthContext';
import { AuthProvider } from './contexts/AuthContext';
import { SidebarProvider } from './contexts/SidebarContext';
import { Environment } from './utils/environment';
import MainLayout from './layouts/MainLayout';
import PageGuard from './components/guards/PageGuard';
import LoginPage from './pages/LoginPage';
import UnderDevelopment from './pages/shared/UnderDevelopment';
import StudentDashboard from './pages/student/StudentDashboard';
import LecturerDashboard from './pages/lecturer/LecturerDashboard';
import AdminDashboard from './pages/admin/AdminDashboard';
import KRSManagement from './pages/student/KRSManagement';
import ClassManagement from './pages/admin/ClassManagement.tsx';
import LecturerClassSchedule from './pages/lecturer/ClassSchedule.tsx';
import GradeInput from './pages/lecturer/GradeInput.tsx';
import StudentPerformance from './pages/lecturer/StudentPerformance.tsx';
import AcademicAdvisor from './pages/lecturer/AcademicAdvisor.tsx';
import ClassSchedule from './pages/student/ClassSchedule';
import GradesKHS from './pages/student/GradesKHS';
import Transcript from './pages/student/Transcript';
import MBKMActivities from './pages/student/MBKMActivities';
import StudentProfile from './pages/student/Profile';
import StudentSettings from './pages/student/Settings';
import LecturerProfile from './pages/lecturer/Profile';
import LecturerSettings from './pages/lecturer/Settings';
import AdminProfile from './pages/admin/Profile';
import AdminSettings from './pages/admin/Settings';
import MasterData from './pages/admin/MasterData';
import Programs from './pages/admin/Programs';
import Curriculum from './pages/admin/Curriculum';
import UserManagement from './pages/admin/UserManagement';
import SemesterSetup from './pages/admin/SemesterSetup';
import ClassScheduling from './pages/admin/ClassScheduling';
import Reports from './pages/admin/Reports';
import RoleRequestManagement from './pages/admin/RoleRequestManagement';
import GuestDashboard from './pages/guest/GuestDashboard';
import RoleManagement from './pages/guest/RoleManagement';
import GuestProfile from './pages/guest/Profile';
import StaffDashboard from './pages/staff/StaffDashboard';
import ApplicantDashboard from './pages/applicant/ApplicantDashboard';
import AlumniDashboard from './pages/alumni/AlumniDashboard';

function RoleDashboardRedirect() {
  const { user, isAuthenticated, isLoading } = useAuth();
  
  // Wait for auth to finish loading
  if (isLoading) {
    return null;
  }
  
  if (!isAuthenticated || !user) {
    return <Navigate to="/login" replace />;
  }
  
  // Determine dashboard based on user roles
  // Priority order: admin > lecturer > staff > student > alumni > student_applicant > guest
  const roles = user.availableRoles || [user.role];
  
  let to = '/guest/dashboard'; // default to guest
  
  if (roles.includes('admin')) {
    to = '/admin/dashboard';
  } else if (roles.includes('lecturer')) {
    to = '/lecturer/dashboard';
  } else if (roles.includes('staff')) {
    to = '/staff/dashboard';
  } else if (roles.includes('student')) {
    to = '/student/dashboard';
  } else if (roles.includes('alumni')) {
    to = '/alumni/dashboard';
  } else if (roles.includes('student_applicant')) {
    to = '/applicant/dashboard';
  } else if (roles.includes('guest')) {
    to = '/guest/dashboard';
  }
  
  return <Navigate to={to} replace />;
}

function App() {
  return (
    <AuthProvider>
      <SidebarProvider>
        <Router>
          <Routes>
          <Route path="/login" element={<LoginPage />} />
          
          {/* Under Development page */}
          <Route
            path="/under-development"
            element={
              <MainLayout>
                <UnderDevelopment />
              </MainLayout>
            }
          />
          
          {/* Legacy /dashboard -> role-first redirect */}
          <Route path="/dashboard" element={<RoleDashboardRedirect />} />
          
          {/* Role-first dashboards */}
          <Route
            path="/student/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentDashboard}>
                  <StudentDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerDashboard}>
                  <LecturerDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/admin/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.adminDashboard}>
                  <AdminDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/staff/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.staffDashboard}>
                  <StaffDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student_applicant/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.applicantDashboard}>
                  <ApplicantDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/alumni/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.alumniDashboard}>
                  <AlumniDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          {/* Role-first student pages */}
          <Route
            path="/student/krs"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentKRS}>
                  <KRSManagement />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student/schedule"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentSchedule}>
                  <ClassSchedule />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student/grades"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentGrades}>
                  <GradesKHS />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student/transcript"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentTranscript}>
                  <Transcript />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student/activities"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentActivities}>
                  <MBKMActivities />
                </PageGuard>
              </MainLayout>
            }
          />
          {/* Role-first profile/settings for each role */}
          <Route
            path="/student/profile"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentProfile}>
                  <StudentProfile />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/student/settings"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.studentSettings}>
                  <StudentSettings />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/profile"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerProfile}>
                  <LecturerProfile />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/settings"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerSettings}>
                  <LecturerSettings />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/admin/profile"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.adminProfile}>
                  <AdminProfile />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/admin/settings"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.adminSettings}>
                  <AdminSettings />
                </PageGuard>
              </MainLayout>
            }
          />

          {/* Role-first lecturer pages */}
          <Route
            path="/lecturer/schedule"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerSchedule}>
                  <LecturerClassSchedule />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/grade-input"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerGradeInput}>
                  <GradeInput />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/performance"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerPerformance}>
                  <StudentPerformance />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/lecturer/advisor"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.lecturerAdvisor}>
                  <AcademicAdvisor />
                </PageGuard>
              </MainLayout>
            }
          />
          {/* Removed legacy lecturer routes and redirects */}
          {/* Role-first admin pages */}
          <Route path="/admin/classes" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminClasses}><ClassManagement /></PageGuard></MainLayout>} />
          <Route path="/admin/master-data" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminMasterData}><MasterData /></PageGuard></MainLayout>} />
          <Route path="/admin/programs" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminPrograms}><Programs /></PageGuard></MainLayout>} />
          <Route path="/admin/curriculum" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminCurriculum}><Curriculum /></PageGuard></MainLayout>} />
          <Route path="/admin/users" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminUsers}><UserManagement /></PageGuard></MainLayout>} />
          <Route path="/admin/role-requests" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminRoleRequests}><RoleRequestManagement /></PageGuard></MainLayout>} />
          <Route path="/admin/semester" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminSemester}><SemesterSetup /></PageGuard></MainLayout>} />
          <Route path="/admin/scheduling" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminScheduling}><ClassScheduling /></PageGuard></MainLayout>} />
          <Route path="/admin/reports" element={<MainLayout><PageGuard isEnabled={Environment.pages.adminReports}><Reports /></PageGuard></MainLayout>} />

          {/* Guest pages */}
          <Route
            path="/guest/dashboard"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.guestDashboard}>
                  <GuestDashboard />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/guest/role-management"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.guestRoleManagement}>
                  <RoleManagement />
                </PageGuard>
              </MainLayout>
            }
          />
          <Route
            path="/guest/profile"
            element={
              <MainLayout>
                <PageGuard isEnabled={Environment.pages.guestProfile}>
                  <GuestProfile />
                </PageGuard>
              </MainLayout>
            }
          />

          {/* Short redirects for bare role paths */}
          <Route path="/student" element={<Navigate to="/student/dashboard" replace />} />
          <Route path="/lecturer" element={<Navigate to="/lecturer/dashboard" replace />} />
          <Route path="/admin" element={<Navigate to="/admin/dashboard" replace />} />
          <Route path="/guest" element={<Navigate to="/guest/dashboard" replace />} />
          <Route path="/" element={<Navigate to="/dashboard" replace />} />
          <Route path="*" element={<Navigate to="/dashboard" replace />} />
        </Routes>
      </Router>
      </SidebarProvider>
    </AuthProvider>
  );
}

export default App;
