import React, { useState } from 'react';

const Settings: React.FC = () => {
  const [emailNotif, setEmailNotif] = useState(true);
  const [darkMode, setDarkMode] = useState(false);

  return (
    <div className="max-w-3xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Settings</h1>

      <div className="card space-y-4">
        <div className="flex items-center justify-between">
          <div>
            <div className="font-medium">Email Notifications</div>
            <div className="text-sm text-gray-500">Receive email updates about academic activities</div>
          </div>
          <input type="checkbox" className="h-4 w-4" checked={emailNotif} onChange={e => setEmailNotif(e.target.checked)} />
        </div>
        <div className="flex items-center justify-between">
          <div>
            <div className="font-medium">Dark Mode</div>
            <div className="text-sm text-gray-500">Experimental preference</div>
          </div>
          <input type="checkbox" className="h-4 w-4" checked={darkMode} onChange={e => setDarkMode(e.target.checked)} />
        </div>
        <div className="pt-2">
          <button className="btn-primary">Save Changes</button>
        </div>
      </div>
    </div>
  );
};

export default Settings;
