import React, { useState } from 'react';
import { Download, Eye, Filter, Calendar, TrendingUp, Users, BookOpen, FileText, BarChart3, PieChart } from 'lucide-react';

interface Report {
  id: string;
  title: string;
  description: string;
  category: 'Academic' | 'Administrative' | 'Financial' | 'Statistical';
  lastGenerated: string;
  format: 'PDF' | 'Excel' | 'CSV';
  parameters?: string[];
}

interface ReportStat {
  label: string;
  value: string;
  change: string;
  changeType: 'positive' | 'negative' | 'neutral';
  icon: React.ReactNode;
}

const Reports: React.FC = () => {
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [selectedPeriod, setSelectedPeriod] = useState<string>('current-semester');

  // Mock data
  const reports: Report[] = [
    {
      id: '1',
      title: 'Laporan Mahasiswa Aktif',
      description: 'Data mahasiswa aktif per program studi dan semester',
      category: 'Academic',
      lastGenerated: '2024-10-05 09:00',
      format: 'PDF',
      parameters: ['Program Studi', 'Semester', 'Status']
    },
    {
      id: '2',
      title: 'Laporan Dosen dan Beban Mengajar',
      description: 'Data dosen dan distribusi beban mengajar',
      category: 'Academic',
      lastGenerated: '2024-10-04 14:30',
      format: 'Excel',
      parameters: ['Fakultas', 'Semester', 'Jenis Dosen']
    },
    {
      id: '3',
      title: 'Laporan Nilai dan IPK',
      description: 'Statistik nilai dan IPK mahasiswa',
      category: 'Academic',
      lastGenerated: '2024-10-03 16:45',
      format: 'PDF',
      parameters: ['Program Studi', 'Angkatan', 'Semester']
    },
    {
      id: '4',
      title: 'Laporan Kelulusan',
      description: 'Data kelulusan mahasiswa dan masa studi',
      category: 'Statistical',
      lastGenerated: '2024-10-02 11:20',
      format: 'Excel',
      parameters: ['Tahun Akademik', 'Program Studi']
    },
    {
      id: '5',
      title: 'Laporan Keuangan Mahasiswa',
      description: 'Status pembayaran SPP dan biaya lainnya',
      category: 'Financial',
      lastGenerated: '2024-10-01 10:15',
      format: 'Excel',
      parameters: ['Semester', 'Program Studi', 'Status Bayar']
    },
    {
      id: '6',
      title: 'Laporan Utilisasi Ruang',
      description: 'Tingkat penggunaan ruang kuliah dan laboratorium',
      category: 'Administrative',
      lastGenerated: '2024-09-30 08:30',
      format: 'PDF',
      parameters: ['Gedung', 'Semester', 'Hari']
    },
    {
      id: '7',
      title: 'Laporan Kehadiran Dosen',
      description: 'Data kehadiran dan aktivitas mengajar dosen',
      category: 'Administrative',
      lastGenerated: '2024-09-29 15:45',
      format: 'CSV',
      parameters: ['Fakultas', 'Periode', 'Status Dosen']
    },
    {
      id: '8',
      title: 'Laporan Evaluasi Mata Kuliah',
      description: 'Hasil evaluasi mata kuliah oleh mahasiswa',
      category: 'Academic',
      lastGenerated: '2024-09-28 13:20',
      format: 'PDF',
      parameters: ['Semester', 'Program Studi', 'Mata Kuliah']
    }
  ];

  const reportStats: ReportStat[] = [
    {
      label: 'Mahasiswa Aktif',
      value: '1,247',
      change: '+3.2%',
      changeType: 'positive',
      icon: <Users className="h-6 w-6 text-blue-600" />
    },
    {
      label: 'Dosen Aktif',
      value: '84',
      change: '+1.2%',
      changeType: 'positive',
      icon: <BookOpen className="h-6 w-6 text-green-600" />
    },
    {
      label: 'Tingkat Kelulusan',
      value: '92.5%',
      change: '+2.1%',
      changeType: 'positive',
      icon: <TrendingUp className="h-6 w-6 text-purple-600" />
    },
    {
      label: 'IPK Rata-rata',
      value: '3.42',
      change: '-0.03',
      changeType: 'negative',
      icon: <BarChart3 className="h-6 w-6 text-orange-600" />
    }
  ];

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'Academic':
        return 'bg-blue-100 text-blue-800';
      case 'Administrative':
        return 'bg-green-100 text-green-800';
      case 'Financial':
        return 'bg-yellow-100 text-yellow-800';
      case 'Statistical':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getFormatColor = (format: string) => {
    switch (format) {
      case 'PDF':
        return 'bg-red-100 text-red-800';
      case 'Excel':
        return 'bg-green-100 text-green-800';
      case 'CSV':
        return 'bg-blue-100 text-blue-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredReports = reports.filter(report => {
    const matchesCategory = selectedCategory === 'all' || report.category === selectedCategory;
    return matchesCategory;
  });

  const categories = ['Academic', 'Administrative', 'Financial', 'Statistical'];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <FileText className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Laporan</h1>
            <p className="text-gray-600">Generate dan unduh laporan sistem akademik</p>
          </div>
        </div>
        <button className="btn-primary flex items-center space-x-2">
          <FileText className="h-4 w-4" />
          <span>Buat Laporan Custom</span>
        </button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {reportStats.map((stat, index) => (
          <div key={index} className="card p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-500">{stat.label}</p>
                <p className="text-2xl font-semibold text-gray-900">{stat.value}</p>
                <p className={`text-sm font-medium ${
                  stat.changeType === 'positive' ? 'text-green-600' :
                  stat.changeType === 'negative' ? 'text-red-600' : 'text-gray-600'
                }`}>
                  {stat.change} dari periode sebelumnya
                </p>
              </div>
              <div className="p-2 bg-gray-50 rounded-lg">
                {stat.icon}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="card p-6 border-2 border-dashed border-blue-200 hover:border-blue-400 transition-colors cursor-pointer">
          <div className="text-center">
            <div className="p-3 bg-blue-100 rounded-lg inline-block mb-4">
              <BarChart3 className="h-8 w-8 text-blue-600" />
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Laporan Akademik</h3>
            <p className="text-sm text-gray-500 mb-4">Generate laporan mahasiswa, nilai, dan kelulusan</p>
            <button className="btn-secondary w-full">
              Generate Sekarang
            </button>
          </div>
        </div>
        <div className="card p-6 border-2 border-dashed border-green-200 hover:border-green-400 transition-colors cursor-pointer">
          <div className="text-center">
            <div className="p-3 bg-green-100 rounded-lg inline-block mb-4">
              <PieChart className="h-8 w-8 text-green-600" />
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Laporan Statistik</h3>
            <p className="text-sm text-gray-500 mb-4">Analisis data dan tren akademik</p>
            <button className="btn-secondary w-full">
              Generate Sekarang
            </button>
          </div>
        </div>
        <div className="card p-6 border-2 border-dashed border-purple-200 hover:border-purple-400 transition-colors cursor-pointer">
          <div className="text-center">
            <div className="p-3 bg-purple-100 rounded-lg inline-block mb-4">
              <FileText className="h-8 w-8 text-purple-600" />
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Laporan Custom</h3>
            <p className="text-sm text-gray-500 mb-4">Buat laporan sesuai kebutuhan spesifik</p>
            <button className="btn-secondary w-full">
              Buat Laporan
            </button>
          </div>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0 lg:space-x-4">
        <div className="flex items-center space-x-4">
          <select
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
            className="input-field"
          >
            <option value="all">Semua Kategori</option>
            {categories.map((category) => (
              <option key={category} value={category}>{category}</option>
            ))}
          </select>
          <select
            value={selectedPeriod}
            onChange={(e) => setSelectedPeriod(e.target.value)}
            className="input-field"
          >
            <option value="current-semester">Semester Saat Ini</option>
            <option value="last-semester">Semester Lalu</option>
            <option value="current-year">Tahun Akademik Saat Ini</option>
            <option value="last-year">Tahun Akademik Lalu</option>
          </select>
        </div>
        <div className="flex space-x-4">
          <button className="btn-secondary flex items-center space-x-2">
            <Filter className="h-4 w-4" />
            <span>Filter Lanjut</span>
          </button>
          <button className="btn-secondary flex items-center space-x-2">
            <Calendar className="h-4 w-4" />
            <span>Jadwal Laporan</span>
          </button>
        </div>
      </div>

      {/* Reports Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {filteredReports.map((report) => (
          <div key={report.id} className="card p-6">
            <div className="flex items-start justify-between mb-4">
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-gray-900 mb-2">{report.title}</h3>
                <p className="text-sm text-gray-600 mb-3">{report.description}</p>
                <div className="flex items-center space-x-2 mb-3">
                  <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getCategoryColor(report.category)}`}>
                    {report.category}
                  </span>
                  <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getFormatColor(report.format)}`}>
                    {report.format}
                  </span>
                </div>
                <div className="text-xs text-gray-500">
                  Terakhir dibuat: {new Date(report.lastGenerated).toLocaleDateString('id-ID', {
                    day: 'numeric',
                    month: 'long',
                    year: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </div>
              </div>
            </div>

            {report.parameters && (
              <div className="mb-4">
                <p className="text-xs font-medium text-gray-700 mb-2">Parameter:</p>
                <div className="flex flex-wrap gap-1">
                  {report.parameters.map((param, index) => (
                    <span key={index} className="bg-gray-100 text-gray-700 px-2 py-1 rounded text-xs">
                      {param}
                    </span>
                  ))}
                </div>
              </div>
            )}

            <div className="flex space-x-2">
              <button className="flex-1 btn-secondary flex items-center justify-center space-x-2">
                <Eye className="h-4 w-4" />
                <span>Preview</span>
              </button>
              <button className="flex-1 btn-primary flex items-center justify-center space-x-2">
                <Download className="h-4 w-4" />
                <span>Download</span>
              </button>
            </div>
          </div>
        ))}
      </div>

      {filteredReports.length === 0 && (
        <div className="text-center py-12">
          <FileText className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">Tidak ada laporan</h3>
          <p className="mt-1 text-sm text-gray-500">Tidak ditemukan laporan yang sesuai dengan filter.</p>
        </div>
      )}
    </div>
  );
};

export default Reports;