import { useQuery, type UseQueryOptions, useMutation, useQueryClient, type UseMutationOptions } from '@tanstack/react-query';
import { apiService, type MessageResponse } from '../services/api';
import type { StudentClass, StudentAttendanceResponse } from '../types';

export function useLecturerClasses(
  lecturerId?: string,
  params?: { semester?: string; start_day?: string; end_day?: string },
  options?: Omit<UseQueryOptions<StudentClass[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['lecturer-classes', lecturerId ?? null, params?.semester ?? null, params?.start_day ?? null, params?.end_day ?? null] as const;
  return useQuery<StudentClass[], Error>({
    queryKey: key,
    queryFn: () => {
      if (!lecturerId) return Promise.resolve([] as StudentClass[]);
      return apiService.getLecturerClasses(lecturerId, params);
    },
    enabled: Boolean(lecturerId),
    ...(options ?? {}),
  });
}

export function useStudentClasses(
  nim?: string,
  params?: { semester?: string; start_day?: string; end_day?: string },
  options?: Omit<UseQueryOptions<StudentClass[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['student-classes', nim ?? null, params?.semester ?? null, params?.start_day ?? null, params?.end_day ?? null] as const;
  return useQuery<StudentClass[], Error>({
    queryKey: key,
    queryFn: () => {
      if (!nim) return Promise.resolve([] as StudentClass[]);
      return apiService.getStudentClasses(nim, params);
    },
    enabled: Boolean(nim),
    ...(options ?? {}),
  });
}

export function useStudentAttendances(
  params?: { semester?: string; kode_mk?: string },
  options?: Omit<UseQueryOptions<StudentAttendanceResponse[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['student-attendances', params?.semester ?? null, params?.kode_mk ?? null] as const;
  return useQuery<StudentAttendanceResponse[], Error>({
    queryKey: key,
    queryFn: () => apiService.getStudentAttendances(params),
    ...(options ?? {}),
  });
}

export function useCheckInAttendance(
  options?: UseMutationOptions<MessageResponse, Error, import('../types').StudentCheckInRequest>
) {
  const qc = useQueryClient();
  return useMutation<MessageResponse, Error, import('../types').StudentCheckInRequest>({
    mutationFn: (payload) => apiService.checkInAttendance(payload),
    onSuccess: (...args) => {
      // Invalidate attendance queries
      qc.invalidateQueries({ queryKey: ['student-attendances'] });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}
