import React, { useState } from 'react';
import { Plus, Edit, Trash2, Search, Filter, BookOpen, Clock, ChevronDown, ChevronRight } from 'lucide-react';

interface Course {
  id: string;
  code: string;
  name: string;
  credits: number;
  semester: number;
  type: 'Wajib' | 'Pilihan' | 'MKWU';
  prerequisites: string[];
  description: string;
}

interface CurriculumProgram {
  id: string;
  name: string;
  code: string;
  year: string;
  totalCredits: number;
  totalCourses: number;
  status: 'Active' | 'Draft' | 'Inactive';
}

const Curriculum: React.FC = () => {
  const [selectedProgram, setSelectedProgram] = useState<string>('1');
  const [selectedSemester, setSelectedSemester] = useState<number>(0); // 0 = all semesters
  const [searchTerm, setSearchTerm] = useState('');
  const [expandedSemesters, setExpandedSemesters] = useState<Set<number>>(new Set([1, 2]));

  // Mock data
  const programs: CurriculumProgram[] = [
    {
      id: '1',
      name: 'Teknik Informatika',
      code: 'IF',
      year: '2023',
      totalCredits: 144,
      totalCourses: 48,
      status: 'Active'
    },
    {
      id: '2',
      name: 'Sistem Informasi',
      code: 'SI',
      year: '2023',
      totalCredits: 144,
      totalCourses: 46,
      status: 'Active'
    }
  ];

  const courses: Course[] = [
    {
      id: '1',
      code: 'IF101',
      name: 'Algoritma dan Pemrograman',
      credits: 3,
      semester: 1,
      type: 'Wajib',
      prerequisites: [],
      description: 'Mata kuliah dasar pemrograman dan algoritma'
    },
    {
      id: '2',
      code: 'IF102',
      name: 'Matematika Diskrit',
      credits: 3,
      semester: 1,
      type: 'Wajib',
      prerequisites: [],
      description: 'Konsep matematika untuk ilmu komputer'
    },
    {
      id: '3',
      code: 'IF103',
      name: 'Sistem Digital',
      credits: 3,
      semester: 1,
      type: 'Wajib',
      prerequisites: [],
      description: 'Dasar-dasar sistem digital dan logika'
    },
    {
      id: '4',
      code: 'MKWU01',
      name: 'Pendidikan Agama',
      credits: 2,
      semester: 1,
      type: 'MKWU',
      prerequisites: [],
      description: 'Mata kuliah wajib universitas'
    },
    {
      id: '5',
      code: 'IF201',
      name: 'Struktur Data',
      credits: 3,
      semester: 2,
      type: 'Wajib',
      prerequisites: ['IF101'],
      description: 'Konsep dan implementasi struktur data'
    },
    {
      id: '6',
      code: 'IF202',
      name: 'Pemrograman Berorientasi Objek',
      credits: 3,
      semester: 2,
      type: 'Wajib',
      prerequisites: ['IF101'],
      description: 'Paradigma pemrograman berorientasi objek'
    },
    {
      id: '7',
      code: 'IF203',
      name: 'Basis Data',
      credits: 3,
      semester: 2,
      type: 'Wajib',
      prerequisites: [],
      description: 'Konsep dan perancangan basis data'
    },
    {
      id: '8',
      code: 'MKWU02',
      name: 'Bahasa Indonesia',
      credits: 2,
      semester: 2,
      type: 'MKWU',
      prerequisites: [],
      description: 'Mata kuliah wajib universitas'
    }
  ];

  const toggleSemester = (semester: number) => {
    const newExpanded = new Set(expandedSemesters);
    if (newExpanded.has(semester)) {
      newExpanded.delete(semester);
    } else {
      newExpanded.add(semester);
    }
    setExpandedSemesters(newExpanded);
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'Wajib':
        return 'bg-blue-100 text-blue-800';
      case 'Pilihan':
        return 'bg-green-100 text-green-800';
      case 'MKWU':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredCourses = courses.filter(course => {
    const matchesSearch = course.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         course.code.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesSemester = selectedSemester === 0 || course.semester === selectedSemester;
    return matchesSearch && matchesSemester;
  });

  // Group courses by semester
  const coursesBySemester = filteredCourses.reduce((acc, course) => {
    if (!acc[course.semester]) {
      acc[course.semester] = [];
    }
    acc[course.semester].push(course);
    return acc;
  }, {} as Record<number, Course[]>);

  const selectedProgramData = programs.find(p => p.id === selectedProgram);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <BookOpen className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Kurikulum</h1>
            <p className="text-gray-600">Kelola kurikulum dan mata kuliah program studi</p>
          </div>
        </div>
        <button className="btn-primary flex items-center space-x-2">
          <Plus className="h-4 w-4" />
          <span>Tambah Mata Kuliah</span>
        </button>
      </div>

      {/* Program Selection */}
      <div className="card p-6">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0">
          <div className="flex items-center space-x-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Program Studi</label>
              <select
                value={selectedProgram}
                onChange={(e) => setSelectedProgram(e.target.value)}
                className="input-field w-64"
              >
                {programs.map((program) => (
                  <option key={program.id} value={program.id}>
                    {program.name} ({program.year})
                  </option>
                ))}
              </select>
            </div>
          </div>
          {selectedProgramData && (
            <div className="grid grid-cols-3 gap-6 text-center">
              <div>
                <div className="text-2xl font-bold text-blue-600">{selectedProgramData.totalCredits}</div>
                <div className="text-sm text-gray-500">Total SKS</div>
              </div>
              <div>
                <div className="text-2xl font-bold text-green-600">{selectedProgramData.totalCourses}</div>
                <div className="text-sm text-gray-500">Total Mata Kuliah</div>
              </div>
              <div>
                <div className={`inline-flex px-3 py-1 rounded-full text-sm font-medium ${
                  selectedProgramData.status === 'Active' ? 'bg-green-100 text-green-800' :
                  selectedProgramData.status === 'Draft' ? 'bg-yellow-100 text-yellow-800' :
                  'bg-red-100 text-red-800'
                }`}>
                  {selectedProgramData.status}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Search and Filters */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0 lg:space-x-4">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <input
            type="text"
            placeholder="Cari mata kuliah..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="input-field pl-10 w-full lg:w-80"
          />
        </div>
        <div className="flex space-x-4">
          <select
            value={selectedSemester}
            onChange={(e) => setSelectedSemester(Number(e.target.value))}
            className="input-field"
          >
            <option value={0}>Semua Semester</option>
            {[1, 2, 3, 4, 5, 6, 7, 8].map((sem) => (
              <option key={sem} value={sem}>Semester {sem}</option>
            ))}
          </select>
          <button className="btn-secondary flex items-center space-x-2">
            <Filter className="h-4 w-4" />
            <span>Filter</span>
          </button>
        </div>
      </div>

      {/* Curriculum Structure */}
      <div className="space-y-4">
        {Object.keys(coursesBySemester)
          .sort((a, b) => Number(a) - Number(b))
          .map((semester) => {
            const semesterNumber = Number(semester);
            const semesterCourses = coursesBySemester[semesterNumber];
            const totalCredits = semesterCourses.reduce((sum, course) => sum + course.credits, 0);
            const isExpanded = expandedSemesters.has(semesterNumber);

            return (
              <div key={semester} className="card">
                <div
                  className="p-4 cursor-pointer hover:bg-gray-50 transition-colors"
                  onClick={() => toggleSemester(semesterNumber)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      {isExpanded ? (
                        <ChevronDown className="h-5 w-5 text-gray-400" />
                      ) : (
                        <ChevronRight className="h-5 w-5 text-gray-400" />
                      )}
                      <h3 className="text-lg font-semibold text-gray-900">Semester {semester}</h3>
                    </div>
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <span>{semesterCourses.length} mata kuliah</span>
                      <span>{totalCredits} SKS</span>
                    </div>
                  </div>
                </div>

                {isExpanded && (
                  <div className="border-t border-gray-200">
                    <div className="overflow-x-auto">
                      <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-50">
                          <tr>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Kode
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Nama Mata Kuliah
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              SKS
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Tipe
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Prasyarat
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Aksi
                            </th>
                          </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                          {semesterCourses.map((course) => (
                            <tr key={course.id} className="hover:bg-gray-50">
                              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                                {course.code}
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div>
                                  <div className="text-sm font-medium text-gray-900">{course.name}</div>
                                  <div className="text-sm text-gray-500">{course.description}</div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center space-x-1">
                                  <Clock className="h-4 w-4 text-gray-400" />
                                  <span className="text-sm text-gray-900">{course.credits}</span>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getTypeColor(course.type)}`}>
                                  {course.type}
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                                {course.prerequisites.length > 0 ? (
                                  <div className="flex flex-wrap gap-1">
                                    {course.prerequisites.map((prereq, index) => (
                                      <span key={index} className="bg-gray-100 text-gray-700 px-2 py-1 rounded text-xs">
                                        {prereq}
                                      </span>
                                    ))}
                                  </div>
                                ) : (
                                  <span className="text-gray-400">-</span>
                                )}
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                <div className="flex space-x-2">
                                  <button className="text-blue-600 hover:text-blue-900">
                                    <Edit className="h-4 w-4" />
                                  </button>
                                  <button className="text-red-600 hover:text-red-900">
                                    <Trash2 className="h-4 w-4" />
                                  </button>
                                </div>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                )}
              </div>
            );
          })}
      </div>

      {Object.keys(coursesBySemester).length === 0 && (
        <div className="text-center py-12">
          <BookOpen className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">Tidak ada mata kuliah</h3>
          <p className="mt-1 text-sm text-gray-500">Tidak ditemukan mata kuliah yang sesuai dengan filter.</p>
        </div>
      )}
    </div>
  );
};

export default Curriculum;