import React from 'react';
import { User, UserCheck, GraduationCap, Briefcase, School, Users, ShieldCheck, ArrowRight } from 'lucide-react';

const GuestDashboard: React.FC = () => {
  const roleOptions = [
    {
      role: 'student_applicant',
      title: 'Student Applicant',
      description: 'Apply for admission to our academic programs',
      icon: <GraduationCap className="h-8 w-8 text-blue-600" />,
      color: 'blue',
      features: ['View program information', 'Submit applications', 'Track application status']
    },
    {
      role: 'student',
      title: 'Student',
      description: 'Access student portal for enrolled students',
      icon: <User className="h-8 w-8 text-green-600" />,
      color: 'green',
      features: ['Course registration', 'View grades', 'Academic transcripts']
    },
    {
      role: 'lecturer',
      title: 'Lecturer',
      description: 'Faculty members and teaching staff',
      icon: <UserCheck className="h-8 w-8 text-purple-600" />,
      color: 'purple',
      features: ['Class management', 'Grade input', 'Student performance tracking']
    },
    {
      role: 'staff',
      title: 'Staff',
      description: 'Administrative and support staff',
      icon: <Briefcase className="h-8 w-8 text-orange-600" />,
      color: 'orange',
      features: ['Administrative tools', 'Student support', 'Department coordination']
    },
    {
      role: 'alumni',
      title: 'Alumni',
      description: 'Graduated students and alumni network',
      icon: <School className="h-8 w-8 text-indigo-600" />,
      color: 'indigo',
      features: ['Alumni directory', 'Career services', 'Event notifications']
    },
    {
      role: 'admin',
      title: 'Administrator',
      description: 'System administrators and management',
      icon: <ShieldCheck className="h-8 w-8 text-red-600" />,
      color: 'red',
      features: ['System management', 'User administration', 'Reports and analytics']
    }
  ];

  const getColorClasses = (color: string) => {
    const colorMap = {
      blue: 'border-blue-200 hover:border-blue-300 hover:bg-blue-50',
      green: 'border-green-200 hover:border-green-300 hover:bg-green-50',
      purple: 'border-purple-200 hover:border-purple-300 hover:bg-purple-50',
      orange: 'border-orange-200 hover:border-orange-300 hover:bg-orange-50',
      indigo: 'border-indigo-200 hover:border-indigo-300 hover:bg-indigo-50',
      red: 'border-red-200 hover:border-red-300 hover:bg-red-50'
    };
    return colorMap[color as keyof typeof colorMap] || 'border-gray-200 hover:border-gray-300 hover:bg-gray-50';
  };

  return (
    <div className="space-y-8">
      {/* Welcome Section */}
      <div className="text-center">
        <div className="flex items-center justify-center mb-4">
          <Users className="h-12 w-12 text-blue-600" />
        </div>
        <h1 className="text-3xl font-bold text-gray-900 mb-4">Welcome to ADB Academic System</h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          Choose your role to access the appropriate portal and features. If you don't have an account yet, 
          you can request access for the role that best describes your relationship with our institution.
        </p>
      </div>

      {/* Institution Info */}
      <div className="card p-8 bg-gradient-to-r from-blue-50 to-indigo-50 border-blue-200">
        <div className="text-center">
          <h2 className="text-2xl font-semibold text-gray-900 mb-3">About Our Institution</h2>
          <p className="text-gray-700 mb-4">
            We are committed to providing excellent education and fostering innovation in technology, 
            business, and engineering. Our comprehensive academic system serves students, faculty, 
            staff, and alumni with modern digital tools and resources.
          </p>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-6">
            <div className="bg-white rounded-lg p-4 shadow-sm">
              <div className="text-2xl font-bold text-blue-600">1,200+</div>
              <div className="text-sm text-gray-600">Active Students</div>
            </div>
            <div className="bg-white rounded-lg p-4 shadow-sm">
              <div className="text-2xl font-bold text-green-600">85+</div>
              <div className="text-sm text-gray-600">Faculty Members</div>
            </div>
            <div className="bg-white rounded-lg p-4 shadow-sm">
              <div className="text-2xl font-bold text-purple-600">12</div>
              <div className="text-sm text-gray-600">Study Programs</div>
            </div>
          </div>
        </div>
      </div>

      {/* Role Selection */}
      <div>
        <h2 className="text-2xl font-semibold text-gray-900 mb-6 text-center">Request Access</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {roleOptions.map((option) => (
            <div
              key={option.role}
              className={`card p-6 border-2 cursor-pointer transition-all duration-200 ${getColorClasses(option.color)}`}
            >
              <div className="text-center mb-4">
                <div className="flex justify-center mb-3">
                  {option.icon}
                </div>
                <h3 className="text-lg font-semibold text-gray-900 mb-2">{option.title}</h3>
                <p className="text-sm text-gray-600 mb-4">{option.description}</p>
              </div>
              
              <div className="space-y-2 mb-4">
                <p className="text-xs font-medium text-gray-500 uppercase tracking-wide">Features:</p>
                {option.features.map((feature, index) => (
                  <div key={index} className="flex items-center text-sm text-gray-600">
                    <div className="w-1.5 h-1.5 bg-gray-400 rounded-full mr-2"></div>
                    {feature}
                  </div>
                ))}
              </div>

              <button className="w-full btn-primary flex items-center justify-center space-x-2">
                <span>Request Access</span>
                <ArrowRight className="h-4 w-4" />
              </button>
            </div>
          ))}
        </div>
      </div>

      {/* Contact Information */}
      <div className="card p-6 bg-gray-50">
        <div className="text-center">
          <h3 className="text-lg font-semibold text-gray-900 mb-3">Need Help?</h3>
          <p className="text-gray-600 mb-4">
            If you're unsure about which role to request or need assistance with the application process, 
            our support team is here to help.
          </p>
          <div className="flex flex-col sm:flex-row justify-center space-y-2 sm:space-y-0 sm:space-x-4">
            <button className="btn-secondary">
              Contact Support
            </button>
            <button className="btn-secondary">
              View FAQ
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GuestDashboard;