import { useQuery, useMutation, useQueryClient, type UseQueryOptions } from '@tanstack/react-query';
import { apiService } from '../services/api';
import type { AttendanceRecord, Mahasiswa, CreateAttendanceRequest, UpdateAttendanceRequest, UpdateMeetingRequest } from '../types';

export function useClassStudents(
  classId?: number | string,
  options?: Omit<UseQueryOptions<Mahasiswa[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['class-students', classId ?? null] as const;
  return useQuery<Mahasiswa[], Error>({
    queryKey: key,
    queryFn: () => {
      if (!classId) return Promise.resolve([] as Mahasiswa[]);
      return apiService.getClassStudents(classId).then((res) => Array.isArray(res) ? res : []);
    },
    enabled: Boolean(classId),
    ...(options ?? {}),
  });
}

export function useMeetingAttendances(
  meetingId?: number,
  options?: Omit<UseQueryOptions<AttendanceRecord[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['meeting-attendances', meetingId ?? null] as const;
  return useQuery<AttendanceRecord[], Error>({
    queryKey: key,
    queryFn: () => {
      if (!meetingId) return Promise.resolve([] as AttendanceRecord[]);
      return apiService.getMeetingAttendances(meetingId).then((res) => Array.isArray(res) ? res : []);
    },
    enabled: Boolean(meetingId),
    ...(options ?? {}),
  });
}

export function useCreateAttendance() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: (data: CreateAttendanceRequest) => apiService.createAttendance(data),
    onSuccess: (_, variables) => {
      // Invalidate the meeting attendances query for the specific meeting
      queryClient.invalidateQueries({ queryKey: ['meeting-attendances', variables.id_pertemuan] });
    },
  });
}

export function useUpdateAttendance() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: ({ id, data }: { id: number; data: UpdateAttendanceRequest }) => 
      apiService.updateAttendance(id, data),
    onSuccess: () => {
      // Invalidate all meeting attendances queries as we don't know which meeting was updated
      queryClient.invalidateQueries({ queryKey: ['meeting-attendances'] });
    },
  });
}

export function useUpdateMeeting() {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: ({ id, data }: { id: number; data: UpdateMeetingRequest }) => 
      apiService.updateMeeting(id, data),
    onSuccess: () => {
      // Invalidate lecturer classes queries to refresh meeting schedules
      queryClient.invalidateQueries({ queryKey: ['lecturer-classes'] });
    },
  });
}
