import React, { useState } from 'react';

const AdminSettings: React.FC = () => {
  const [maintenance, setMaintenance] = useState(false);
  const [reportEmail, setReportEmail] = useState('admin@university.edu');
  return (
    <div className="max-w-3xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Settings</h1>
      <div className="card space-y-4">
        <div className="flex items-center justify-between">
          <div>
            <div className="font-medium">Maintenance Mode</div>
            <div className="text-sm text-gray-500">Temporarily disable user access</div>
          </div>
          <input type="checkbox" className="h-4 w-4" checked={maintenance} onChange={e => setMaintenance(e.target.checked)} />
        </div>
        <div>
          <div className="font-medium mb-1">Report Email</div>
          <input className="input-field" value={reportEmail} onChange={e => setReportEmail(e.target.value)} />
        </div>
        <button className="btn-primary">Save Changes</button>
      </div>
    </div>
  );
};

export default AdminSettings;
