import React, { useMemo, useState } from 'react';
import { Check, X, Notebook, User, FileText, Search } from 'lucide-react';
import type { AdvisorNote, KRSRequest, StudentSummary } from '../../types';

const mockAdvisees: StudentSummary[] = Array.from({ length: 12 }).map((_, i) => ({
  id: `S${i + 1}`,
  name: `Student ${i + 1}`,
  studentId: `22010${(i + 1).toString().padStart(3, '0')}`,
  program: 'Informatics',
  semester: 5,
  gpa: Math.round((3 + Math.random() * 1) * 100) / 100,
  lastSemesterGPA: Math.round((3 + Math.random() * 1) * 100) / 100,
}));

const mockRequests: KRSRequest[] = mockAdvisees.slice(0, 5).map((s, i) => ({
  id: `KRS-${i + 1}`,
  student: s,
  semester: '2024/2025 Genap',
  requestedCredits: 21 - i,
  status: 'submitted',
  submittedAt: new Date().toISOString(),
}));

const AcademicAdvisor: React.FC = () => {
  const [query, setQuery] = useState('');
  const [requests, setRequests] = useState<KRSRequest[]>(mockRequests);
  const [notes, setNotes] = useState<AdvisorNote[]>([]);
  const [noteDraft, setNoteDraft] = useState('');
  const [selectedStudent, setSelectedStudent] = useState<StudentSummary | null>(mockAdvisees[0]);

  const advisees = useMemo(() => {
    return mockAdvisees.filter(s => s.name.toLowerCase().includes(query.toLowerCase()) || s.studentId.includes(query));
  }, [query]);

  const approve = (id: string) => {
    setRequests(prev => prev.map(r => (r.id === id ? { ...r, status: 'approved', decisionAt: new Date().toISOString() } : r)));
  };
  const reject = (id: string) => {
    setRequests(prev => prev.map(r => (r.id === id ? { ...r, status: 'rejected', decisionAt: new Date().toISOString() } : r)));
  };

  const addNote = () => {
    if (!selectedStudent || !noteDraft.trim()) return;
    const newNote: AdvisorNote = {
      id: `${Date.now()}`,
      studentId: selectedStudent.id,
      advisorId: 'L1',
      date: new Date().toISOString(),
      note: noteDraft.trim(),
    };
    setNotes(prev => [newNote, ...prev]);
    setNoteDraft('');
  };

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Academic Advisor</h1>
        <div className="text-sm text-gray-500">Approve KRS and track advisees</div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Advisee list */}
        <div className="card">
          <div className="flex items-center justify-between mb-3">
            <div className="font-semibold text-gray-900 inline-flex items-center"><User className="h-4 w-4 mr-2" /> Advisees</div>
          </div>
          <div className="mb-3 relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
            <input
              className="w-full pl-9 pr-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
              placeholder="Search name or ID"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
            />
          </div>
          <div className="max-h-80 overflow-y-auto divide-y">
            {advisees.map(a => (
              <button
                key={a.id}
                className={`w-full text-left py-2 px-2 hover:bg-gray-50 ${selectedStudent?.id === a.id ? 'bg-primary-50' : ''}`}
                onClick={() => setSelectedStudent(a)}
              >
                <div className="font-medium text-gray-900">{a.name}</div>
                <div className="text-xs text-gray-600">{a.studentId} • Semester {a.semester} • IPK {a.gpa}</div>
              </button>
            ))}
          </div>
        </div>

        {/* KRS approvals */}
        <div className="card">
          <div className="font-semibold text-gray-900 mb-3 inline-flex items-center"><FileText className="h-4 w-4 mr-2" /> KRS Submissions</div>
          <div className="space-y-3">
            {requests.map(r => (
              <div key={r.id} className="p-3 border rounded-lg">
                <div className="flex items-center justify-between">
                  <div>
                    <div className="font-medium text-gray-900">{r.student.name} • {r.student.studentId}</div>
                    <div className="text-xs text-gray-500">{r.semester} • Requested SKS: {r.requestedCredits}</div>
                  </div>
                  <div className="flex items-center space-x-2">
                    {r.status === 'submitted' ? (
                      <>
                        <button className="btn btn-primary inline-flex items-center" onClick={() => approve(r.id)}>
                          <Check className="h-4 w-4 mr-1" /> Approve
                        </button>
                        <button className="btn inline-flex items-center" onClick={() => reject(r.id)}>
                          <X className="h-4 w-4 mr-1" /> Reject
                        </button>
                      </>
                    ) : (
                      <div className={`text-sm font-medium ${r.status === 'approved' ? 'text-green-600' : 'text-red-600'}`}>{r.status.toUpperCase()}</div>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Progress and notes */}
        <div className="space-y-6">
          <div className="card">
            <div className="font-semibold text-gray-900 mb-2 inline-flex items-center"><Notebook className="h-4 w-4 mr-2" /> Progress Summary</div>
            {selectedStudent ? (
              <div className="text-sm text-gray-700 space-y-1">
                <div>Name: <span className="font-medium text-gray-900">{selectedStudent.name}</span></div>
                <div>ID: {selectedStudent.studentId}</div>
                <div>Program: {selectedStudent.program}</div>
                <div>Semester: {selectedStudent.semester}</div>
                <div>IPS (Last): <span className="font-medium text-gray-900">{selectedStudent.lastSemesterGPA}</span></div>
                <div>IPK: <span className="font-medium text-gray-900">{selectedStudent.gpa}</span></div>
              </div>
            ) : (
              <div className="text-sm text-gray-600">Select a student to view progress</div>
            )}
          </div>

          <div className="card">
            <div className="font-semibold text-gray-900 mb-2">Guidance Notes</div>
            <div className="space-y-2">
              <textarea
                className="w-full border rounded-lg px-3 py-2"
                rows={3}
                placeholder="Add a new note..."
                value={noteDraft}
                onChange={(e) => setNoteDraft(e.target.value)}
              />
              <div className="flex justify-end">
                <button className="btn btn-primary" onClick={addNote}>Add Note</button>
              </div>
            </div>
            <div className="mt-3 space-y-2 max-h-48 overflow-y-auto">
              {notes
                .filter(n => !selectedStudent || n.studentId === selectedStudent.id)
                .map(n => (
                  <div key={n.id} className="p-2 border rounded">
                    <div className="text-xs text-gray-500">{new Date(n.date).toLocaleString()}</div>
                    <div className="text-sm text-gray-800">{n.note}</div>
                  </div>
                ))}
              {notes.filter(n => !selectedStudent || n.studentId === selectedStudent.id).length === 0 && (
                <div className="text-sm text-gray-500">No notes yet.</div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AcademicAdvisor;
